<template>
    <div class="cajon">
        <v-container>

            <v-form ref="form" v-model="valid" @submit.prevent="validate" lazy-validation
                class="container-cajon my-2 mx-2 px-3 py-3">
                <div class="text-center mt-5" style=" margin-top: 0px; margin-bottom: -30px;">
                    <p class="title">
                        AGENDA TU CITA CON NOSOTROS!</p>
                </div>
                <div class=" text-center">
                    <img src="../../assets/logoicon.png" alt="logo">
                </div>

                <v-alert v-if="alert == true" dense text type="success">
                    Tus datos han sido enviados correctamente, pronto se prondran en contacto contigo.
                </v-alert>

                <v-text-field :rules="nameRules" v-model="nombre" dense filled rounded label="Tu nombre"></v-text-field>
                <v-text-field v-model="negocio" :rules="nameRules" dense filled rounded
                    label="Nombre de tu negocio"></v-text-field>

                <v-row>
                    <v-col cols="12" md="6">
                        <v-text-field v-model="telefono" type="tel" :rules="nameRules" dense filled rounded
                            label="Nmero de telfono"></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                        <v-text-field v-model="correo" :rules="nameRules" dense filled rounded
                            label="Correo electrnico"></v-text-field>
                    </v-col>
                    <v-col cols="12">
                        <v-select v-model="horario" :rules="nameRules" dense filled rounded :items="horarios"
                            item-value="full_horario" item-text="full_horario" label="Horarios" return-object></v-select>
                    </v-col>
                </v-row>

                <v-btn large dark color="green" rounded block type="submit">Agregar</v-btn>
            </v-form>
        </v-container>
    </div>
</template>
<script>
import api from "../../models/controller";
export default {


    data() {
        return {
            alert: false,
            valid: true,
            name: '',
            nombre: '',
            negocio: '',
            telefono: '',
            correo: '',
            nameRules: [
                v => !!v || 'Campo es requerido.'
            ],
            horarios: [{
                id: 1,
                nombre: 'Martes',
                horario: '08 am - 09 am',
                full_horario: 'Martes (08 am - 09 am)'
            }],
            horario: { full_horario: "Seleccionar", id: null },
        }
    },

    methods: {
        validate: function () {
            if (this.$refs.form.validate()) {
                var _data = {
                    cliente: this.nombre,
                    negocio: this.negocio,
                    telefono: this.telefono,
                    correo: this.correo,
                    horario: this.horario.full_horario,
                    fechacreacion: new Date(),
                    estado: 'Agendado',
                    ext: 0
                }

                api.addAgendarMeet(_data).then(() => {
                    this.alert = true;
                    this.reset();
                    window.setTimeout(() => {
                        this.alert = false;
                    }, 3000)
                })
            } else {
                console.log("Campos vacios")
            }

        },
        reset() {
            this.$refs.form.reset()
        },

    },
}
</script>



<style>
.cajon {
    background: ghostwhite;
    height: 100%;
}

.container-cajon {
    background: white;
    border-radius: 5px;

}

@media screen and (min-width: 550px) {

    .container {
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .container-cajon {
        width: 500px;
    }

}

.submit {
    float: right;
    margin-bottom: 14px !important;
    padding: 8px;
    width: 150px;
    font-size: 14pt;
    color: white;
    background: #04790d;
    border-radius: 40px;
    width: 100%;
}
</style>