<template>
  <div>
    <v-app-bar style="background: #0eb308 !important" dark app>

      <v-icon dark prominent class="d-flex d-sm-none text-right" @click="open"
        style="position: absolute; right: 35px; zoom: 1.5; z-index: 3" size="19">mdi-share-variant</v-icon>

      <v-icon dark prominent @click.stop="drawer = !drawer" class="d-flex d-sm-none text-right"
        style="position: absolute; right: 0px; zoom: 1.5; z-index: 3">mdi-dots-vertical</v-icon>
      <!---<v-app-bar-nav-icon dark prominent @click.stop="drawer = !drawer" class="d-flex d-sm-none text-right"
        style="position: absolute; right: 0px; zoom: 1.5; z-index: 3"></v-app-bar-nav-icon>-->

      <v-toolbar-title style="margin-left: -12px !important">
        <router-link style="color: #fff; margin-left: -20px; font-weight: bolder" to="/">
          <v-avatar size="110" class="mt-1">
            <img src="../../assets/logo.png" alt="logo" /> </v-avatar></router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <search v-on:searchProducts="onSearch" class=" ml-0 mt-10" style="margin-right: 85px; margin-top: 5px;"></search>
      <v-spacer></v-spacer>
      <ListItem style="width: 100%;" :search="search" :productos="productos" class="item-list"></ListItem>
      <router-link class="d-none d-sm-flex" style="color: #fff; margin-left: 5px; margin-right: 15px"
        to="/servicios">Servicios</router-link>
      <router-link class="d-none d-sm-flex" style="color: #fff; margin-left: 5px; margin-right: 25px"
        to="/nosotros">Nosotros</router-link>
      <router-link v-if="isLogin == false" fab elevation="12"
        class="intermitente btn-warning p-2 ratio-1x1 rounded-pill font-weight-medium mr-sm-3 mr-md-4 mr-xl-8 d-none d-sm-flex"
        style="
          color: #fff;
          margin-left: 5px;
          box-shadow: 1px 1px 6px 2px rgba(0, 0, 0, 0.4);
        " to="/registro">Registrarme</router-link>

      <v-btn rounded text dark @click="logearme = true" style="
height: 50px;
      margin-left: 5px;
      border
    " v-if="isLogin == false" class="ml-1 mr-0 d-none d-sm-flex">
        <v-icon size="28">mdi-login</v-icon>
        <p class="entrar">Entrar</p>
      </v-btn>
      <!--<v-avatar
        v-if="!isLogin"
        class="d-none d-sm-flex"
        size="38"
        color="indigo"
      >
        <span class="white--text">Lg</span>
      </v-avatar> -->
      <v-menu bottom min-width="200px" rounded offset-y v-if="isLogin">
        <template v-slot:activator="{ on }">
          <v-btn icon x-large v-on="on" class="d-none d-sm-flex mr-3">

            <v-avatar color="brown" size="36">
              <img class="img-style"  v-if="isLogin" :src="user.photo" alt="John" />

              <span v-else class="white--text">Lg</span>
            </v-avatar>
          </v-btn>
        </template>
        <v-card>
          <v-list-item-content class="justify-center">
            <div class="mx-auto text-center">
              <v-avatar color="brown">
                <img class="img-style" v-if="isLogin" :src="user.photo" alt="John" />
              </v-avatar>



              <h6>{{ user.nombres }}</h6>
              <p class="text-caption mt-1">
                {{ user.email }}
              </p>
              <v-divider class="my-3"></v-divider>
              <v-btn depressed rounded text :to="`/perfil/${user.id_user}/edit`">
                Editar Cuenta
              </v-btn>
              <v-divider class="my-3"></v-divider>
              <v-btn depressed rounded text @click="salir"> Desconectar </v-btn>
            </div>
          </v-list-item-content>
        </v-card>
      </v-menu>
    </v-app-bar>
    <!-- Add a navigation bar -->
    <v-navigation-drawer app v-model="drawer" fixed temporary width="315">
      <v-list style="background: #0eb308">
        <v-btn class="btn-close" @click="drawer = false" text color="white">
          <v-icon color="white" size="35">mdi-close</v-icon>
        </v-btn>
        <v-list-item class="text-center item-avatar" color="green" v-if="isLogin">
          <div class="edit mx-auto" v-if="isLogin">
            <div class="style-photo pt-6">
              <photo v-on:photo="onPhotos" :idPhoto="idPhotoActual" :photoProfile="photoUser" :idPhotoNewV="idPhotoNew">
              </photo>
            </div>
          </div>

        </v-list-item>

        <v-list-item class="text-center" color="green" v-else style="padding: 0px 0px 0px 4px">
          <v-list-item-avatar class="mt-0" size="60" color="gray" style="
              border: solid 2px gray;
              background: gainsboro;
              margin-top: -10px;
            ">
            <v-img src="https://cdn-icons-png.flaticon.com/512/149/149071.png"></v-img>
          </v-list-item-avatar>

          <v-list-item-content style="margin-left: -10px">
            <v-list-item-title class="text-h3 text-left" style="
                font-size: 12pt !important;
                color: white;
                font-weight: bolder;
              ">
              BIENVENIDO
            </v-list-item-title>
            <v-list-item-subtitle class="text-left" style="color: white">Ingresa a tu cuenta, vende y compra.
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-list-item class="mt-4" v-if="!isLogin">
          <v-btn class="rounded-lg" large dark outlined style="width: 130px; padding: 0px" @click="logearme = true">
            Ingresar
          </v-btn>
          <v-btn to="/registro" large color="rgb(253, 202, 0)" dark class="ml-5 rounded-lg btn-warning"
            style="width: 130px; color: white !important">
            <span> Crear cuenta</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="isLogin" class="mx-auto" style="
            padding: 0px;
            margin-top: -15px;
            margin-bottom: -20px;
            text-align: center !important;

            display: flex;
            justify-content: center;
            align-items: center;
          ">
          <div class="monedas" @click="verHistorial()">
            <v-img max-width="45" src="../../assets/moneda.png" alt="monedas" style="object-fit: cover"></v-img>
            <p class="money">{{ money.fichas }}</p>
          </div>
        </v-list-item>
        <codigo v-if="isLogin == true"></codigo>


        <v-list-item class="text-center item-avatar user-name" :to="`/perfil/${user.id_user}/edit`" link v-if="isLogin"
          style="margin-top: -20px; margin-bottom: -15px">
          <v-list-item-content class="user-name">
            <v-list-item-title class="text-h6 text-uppercase user-name name-user">
              {{ user.nombres + " " + user.apellidos }}
            </v-list-item-title>
          </v-list-item-content>

          <v-list-item-action>
            <v-icon size="30" color="black"> mdi-chevron-right</v-icon>
          </v-list-item-action>
        </v-list-item>
      </v-list>
      <!-- AUI AGREGAR-->
      <menulateral></menulateral>
    </v-navigation-drawer>
    <login v-model="logearme" />

    <v-dialog v-model="dialogHM" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-toolbar dark color="green" height="45">
        <v-toolbar-title>Historial Monedas</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn icon dark @click="dialogHM = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-card class="" style=" border-radius: 0px !important;">
        <historial v-if="cajon == 'historial'" />
      </v-card>
    </v-dialog>

    <vue-bottom-sheet ref="myBottomSheet" max-width="100%" :max-height="maxHeight" :click-to-close="clickToClose">
      <v-container>
<p>Compartir por:</p>
        <v-row no-gutters>
          <v-col cols="2" v-for="network in networks" :key="network.network">
            <div>
              <ShareNetwork :network="network.network" :url="sharing.url" :title="sharing.title"
                :description="sharing.description" :hashtags="sharing.hashtags"> <v-icon :color="network.color" size="40">
                  mdi-{{ network.icon }}</v-icon>
                <!--<span style="color:black; font-size: 12pt;">{{ network.name }}</span>-->
              </ShareNetwork>
            </div>

          </v-col>
          <v-col cols="2" >
            <div>

           <v-icon @click=" copyUrl()" size="40" href="https://vendeloyaglobal.com/" class="text-dark" target="_blank" rel="noopener noreferrer" ref="mylink">mdi-link</v-icon>
            </div>

          </v-col>

        </v-row>

      </v-container>
    </vue-bottom-sheet>
  </div>
</template>
<script>
import store from '../../store'
import api from "../../models/controller";
import photo from "@/views/PerfilEdit/CreatePhoto.vue";
//import { mdiExitRun } from "@mdi/js";
import login from "@/components/Login/Login.vue";
import menulateral from "@/components/AppBar/MenuMovilLateral.vue";
import search from '@/components/Filtros/Search.vue'
import historial from '@/components/Perfil/Items/Fichas/FichasHistorial.vue'
import codigo from "@/components/Perfil/Items/Fichas/Codigo.vue";
import { mdiArrowUpBoldCircle } from "@mdi/js";
import { mdiStoreCheck } from "@mdi/js";
import { mdiAccountCash } from "@mdi/js";
import { mdiLogin } from "@mdi/js";
import { mdiBallot } from "@mdi/js";
import { mdiFolder } from "@mdi/js";
import ListItem from '@/components/Filtros/ListSearch.vue'
import { mdiCardAccountDetails } from "@mdi/js";
export default {
  components: { login, menulateral, codigo, photo, search, ListItem, historial },

  data() {
    return {
      maxHeight: "90%",
      clickToClose: true,
      share: false,
      newEdit: false,
      oc: null,
      photoUser: "",
      photoNew: '',
      idPhotoActual: "s",
      idPhotoNew: "",
      navegation: [],
      overlay: false,
      logearme: null,
      user: {},
      dialogHM: false,
      search: '',
      loading: false,
      productos: [],
      isLogin: false,
      drawer: false,
      money: 0,
      tab: null,
      cajon: null,
      total: null,
      appdrawer: {
        mdiFolder,
        mdiBallot,
        mdiArrowUpBoldCircle,
        mdiStoreCheck,
        mdiAccountCash,
        mdiLogin,
        mdiCardAccountDetails,
      },
      items: ["Subir", "shopping", "videos", "images", "news"],
      icons: ["mdi-facebook", "mdi-twitter", "mdi-linkedin", "mdi-instagram"],
      sharing: {
        url: 'https://vendeloyaglobal.com/',
        title: 'VENDELO YA | Vende y Compra',
        description: 'Vende y compra productos que otras personas suben a nuestra pgina. ',
        hashtags: 'vendo,compro,anuncio',
      },
      networks: [
        { network: 'email', name: 'Email', icon: 'email', color: '#333333', height: '100 px !important' },
        { network: 'facebook', name: 'Facebook', icon: 'facebook', color: '#1877f2', height: '100 px' },
        { network: 'linkedin', name: 'LinkedIn', icon: 'linkedin', color: '#007bb5', height: '100 px' },
        { network: 'twitter', name: 'Twitter', icon: 'twitter', color: '#1da1f2', height: '100 px' },
        { network: 'whatsapp', name: 'Whatsapp', icon: 'whatsapp', color: '#25d366', height: '100 px' },
      ]
    };
  },

  methods: {
    copyUrl() {
      navigator.clipboard.writeText('https://vendeloyaglobal.com/');
    },

    open() {
      this.$refs.myBottomSheet.open();
    },
    verHistorial() {
      this.$router.push(`/historial-monedas`).catch(() => { });
      //this.dialogHM= true;
      // this.cajon =='historial'
    },
    irChat: function () {
      this.$router.push(`/bandeja`).catch(() => { });


    },
    paginar(pagina) {
      //`/perfil/${user.id_user}/ventas`
      this.$router
        .push(`/perfil/${this.user.id_user}/${pagina}`)
        .catch(() => { });
    },
    salir() {
      store.dispatch('logout');
      window.localStorage.removeItem("user");
      this.$router.go();
    },
    islogen() {
      if (window.localStorage.getItem("user")) {
        this.isLogin = true;
        var user = JSON.parse(window.localStorage.getItem("user"));
        this.user = user.user;
      } else {
        this.isLogin = false;
        console.log("NO LOGEADO EL USUARIO");
      }
    },

    actualizarFoto: function () {
      let foto = {
        id: this.user.id_user,
        photo: this.PhotoNew,
      };
      api.updateFotoPut(this.user.id_user, foto).then(() => {
        this.idPhotoNew = 's'
        // this.idPhoto = "";
        //this.step++;
        this.newEdit = false;
      });
    },
    getMoney: function (id) {
      api.getMoney(id).then((result) => {
        var money = result.data;
        this.money = money.money;
      });
    },

    obtenerIdPhto: function (photo) { //
      let [a, b, c, d, e, f] = photo.split(/. |! |[/]/, -1);
      a = "";
      b = "";
      c = "";
      d = "";
      e = "";
      this.idPhotoActual = f;
      this.oc = a + b + c + d + e
    },

    onPhotos: function (item) {
      if (item != "vacio") {
        this.newEdit = true;
        this.PhotoNew = item.img
      } else {
        this.PhotoNew = this.photoUser
        this.newEdit = false;
      }
    },

    onSearch: function (data) {
      this.search = data;

    }
  },
  beforeCreate() {
    this.overlay = true;
  },

  created() {

    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;

      this.user.photo = 'https://cdn-icons-png.flaticon.com/512/149/149071.png'
      this.overlay = true;
      this.isLogin = true;

      let id = data;

      this.getMoney(id.id_user);


      // this.user= user.user;
      api.getUser(id.id_user).then((result) => {
        this.user = result.data[0];
        this.photoUser = this.user.photo;
        this.obtenerIdPhto(this.user.photo);
        this.navegation = [
          {
            icon: mdiArrowUpBoldCircle,
            title: "Vender producto",
            href: "/addProduct",
          },
          {
            icon: mdiStoreCheck,
            title: "Comprar",
            href: "/",
          },
          {
            icon: mdiBallot,
            title: "Mis productos",
            href: `/profile/${this.user.id_user}/list_products`,
          },

          {
            icon: mdiFolder,
            title: "Mis ventas",
            href: `/perfil/${this.user.id_user}/ventas`,
          },
          {
            icon: mdiFolder,
            title: "Mis pagos",
            href: `/perfil/${this.user.id_user}/historial_pagos`,
          },
        ];
      });
      // console.log("ESTA LOGEADO EL USUARIO :",this.user);

    } else {
      this.isLogin = false;
    }
  },
  watch: {
    overlay(val) {
      val &&
        setTimeout(() => {
          this.overlay = false;
        }, 3000);
    },
  },
};
</script>

<style scoped>
#nav {
  background: #0eb308 !important;
}

.intermitente {
  padding: 18px 18px;
  box-shadow: 0px 0px 18px;
  animation: infinite resplandorAnimation 2s;
  text-decoration: none !important;
}

@keyframes resplandorAnimation {

  0%,
  100% {
    box-shadow: 0px 0px 20px;
  }

  50% {
    box-shadow: 0px 0px 0px;
  }
}

.item-avatar {
  display: flex;
  justify-content: center;
  align-items: center;
}

.money {
  margin-top: 20px !important;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bolder;
  font-size: 28pt;
  color: white;
  padding-bottom: 0px;
}

.entrar {
  margin-top: 14px;
  color: white;
  font-weight: bolder;
}

.btn-close {
  position: fixed;
  right: -19px;
  top: 6px;
  z-index: 3;
  color: white !important;
}

.monedas {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.item-list {
  position: absolute;
  top: 56px;
  left: 0px;
  z-index: 5;
}

.user-name {
  padding-top: -30px !important;
  background: #9ef39c;
}

.name-user {
  font-size: 13pt !important;
  color: rgb(0, 0, 0)
}

.img-style{
width: 100%;
height: 100%; 
object-fit: cover
}
</style>