<template>
    <div>

      <v-img class="file-upload" :src="photoNew"><input class="input-photo" type="file" ref="photo"
          accept="image/jpeg image/jpg image/png" value="" @click="$refs.photo.value = null"
          @change="uploadPhoto($event)" />
          
      <v-progress-circular  v-if="loadingPhoto==true"  :rotate="360" width="1" class="progres-circular"
      indeterminate
  
    > 
    </v-progress-circular>

          <v-btn v-if="idPhotoNew != ''" block @click="actualizarFoto" dense class="mt-2 mb-3 btn-guardar" style="font-size: 10px;" rounded dark color="orange">
      GUARDAR
    </v-btn>
      </v-img>
      <!--v-if="idPhotoNew != ''"-->
      <v-btn v-if="idPhotoNew != ''" class="mx-2 img-close btn-cerrar" small dark fab color="red" @click="deletePhoto(idPhotoNew)">
        <v-icon dark> mdi-close </v-icon>
      </v-btn>

  </div>
</template>
  
<script>
import store from '../../store'
import updatePhotoProfile from "../../controller/updateProfile";
import api from "../../models/controller";
export default {
  props: ["idUser", "idPhoto", "photoProfile"],
  data: () => ({
    photo: {},
    loadingPhoto:false,
    idPhotoNew: "",
    photoNew: "",
    loading: false,
    dataFoto: {},
    user: {}
  }),
  methods: {
    onInputClick: function (event) {
      event.target.value = "";
    },
    addImage: function () {
      this.photo = {
        img: "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg",
        id: "",
      };
    },

    uploadPhoto: function (e) {
      this.loadingPhoto=true
      try {
        const { files } = this.$refs.photo;
        const file = files[0];
        updatePhotoProfile(file).then((result) => {
          var dataimg = result;
          this.photoNew = dataimg.url;
          this.idPhotoNew = dataimg.id;

          var photo = {
            img: dataimg.url,
            id: dataimg.id,
          };

          this.enviarFotos(photo);
        });
        //this.cargarImagen(file, i);
        e.currentTarget.files = null;
      } catch (error) {
        console.log("Error ", error);
      }
    },

    deletePhoto: function (item) {
      let data = {
        id: item,
      };
      api.eliminarFotoPost(data).then(() => {
        this.photoNew = this.photoProfile;
        this.idPhotoNew = "";
        var photo = "vacio";
        this.enviarFotos(photo);
      });
    },

    enviarFotos: function (datos) {
      this.dataFoto = datos;
      this.$emit("photo", datos);
     this.loadingPhoto=false;
    },

    actualizarFoto: function () {
      let foto = {
        id: this.user.id_user,
        photo: this.dataFoto.img,
      };
      api.updateFotoPut(this.user.id_user, foto).then(() => {
        //this.idPhotoNew='s'
        // this.idPhoto = "";
        //this.step++;
        this.idPhotoNew = '';
        this.newEdit = false;
      });
    },

  },
  created: async function () {

    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
      api.getUser(data.id_user).then((result) => {
        var user = result.data[0];
        this.user = user;
        this.photoNew = user.photo;
      });
    } else {
      console.log("NO LOGEADO EL USUARIO");
    }
  },
};
</script>
  
<style lang="scss" scoped>
.img-input {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;

}

.input-photo {
  width: 70px;
  height: 90px;
  background: red;

  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
}

.file-upload {
  display: flex;
  border-radius: 50%;
  width: 140px;
  height: 140px;
  border: solid rgb(198, 252, 185) 3px;
  /*border: 1px solid #01e934;*/
  /*border-radius: 100px;*/
  overflow: hidden;
  position: relative;
}

.file-upload input {
  position: absolute;
  height: 400px;
  width: 400px;
  left: -200px;
  top: -200px;
  background: transparent;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
}

.file-upload img {
  height: 200px;
  width: 200px;
  margin: 10px;
  border-radius: 8px;
  color: rgb(241, 241, 6);
}

.progres-circular{

  height: 135px !important;
  width: 134px !important;
  color: rgb(241, 241, 6);
  margin: auto;
}

.img-close {
  position: absolute;
  top: 25px;
  right: 60px;
  z-index: 3;
  border-radius: 50%;
}

.btn-cerrar {
  border: solid 2px white !important
}

.btn-guardar {
  position: absolute;
  bottom: -15px;
  width: 100% !important;
  border: solid 2px white !important
}
</style>
  
  