<template>
  <div class="containers">
    <div class="container">
      <b-row>
        <b-col cols="12" sm="6" md="6" lg="6" xl="6">
          <div class="contenedor-derecho"></div>
        </b-col>
        <b-col cols="12" sm="6" md="6" lg="6" xl="6">
          <div class="contenedor-izquierdo">
            {{ platform }}
            <b-form class="form-login" @submit.prevent="login">
              <div class="centrarfrom">
                <p class="titulo">LOGEARMEas</p>
                <v-text-field
                  v-model="email"
                  :rules="[rules.email]"
                  filled
                  color="green"
                  label="Correo electrnico"
                  type="email"
                ></v-text-field>
                <v-text-field
                  v-model="password"
                  filled
                  color="green"
                  label="Contrasea"
                  style="min-height: 96px"
                  type="password"
                ></v-text-field>
                <input
                  class="form-submit bt-block"
                  type="submit"
                  value="Entrar"
                />
                <p class="contrasenia">
                  Olvidaste tu contrasea?
                  <span class="recuperar"> Recuperar</span>
                </p>
                <p class="btn-registrar"><a href="/registro">REGISTRARME</a></p>
              </div>
            </b-form>
          </div>
        </b-col>
      </b-row>
    </div>
  </div>
</template>
<script>
//import axios from "axios";
import api from "../models/controller";
//alert("La resolucin de tu pantalla es: " + screen.width + " x " + screen.height)
export default {
  data() {
    return {
      platform: null,
      email: undefined,
      password: undefined,
      userSaved: false,
      sending: false,
      rules: {
        email: (v) =>
          !!(v || "").match(/@/) || "Por favor ingresa un correo vlido",
        length: (len) => (v) =>
          (v || "").length >= len ||
          `Longitud de carcter no vlida, obligatorio ${len}`,
        password: (v) =>
          !!(v || "").match(
            /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*(_|[^\w])).+$/
          ) ||
          "La contrasea debe contener una letra mayscula, un carcter numrico y un carcter especial",
        required: (v) => !!v || "Este campo es obligatorio",
      },
      pantalla: screen.height,
    };
  },
  methods: {
    login: function () {
      var data = { email: this.email, password: this.password };
      this.sending = true;
      api.loginUser(data).then((datos) => {
        localStorage.email = this.email;
        localStorage.pass = this.password ;
        localStorage.setItem("user", JSON.stringify(datos.data));
        console.log("DATA  : ", datos);
        var da = localStorage.getItem("user");
        console.log("RESUTLADO OBTENIDO localStorage : ", JSON.parse(da));
        this.userSaved = true;
        this.sending = false;
        this.$router.push("/").catch(() => {});
      });
    },

    clearForm: function () {
      this.email = undefined;
      this.password = undefined;
    },
  },
  mounted() {
    if (localStorage.email) {
      this.email = localStorage.email;
    }
    if (localStorage.pass) {
      this.pass = localStorage.pass;
    }
  },

  created() {

    console.log("DATOS GUARDADROD LOCAL : ", localStorage.email, " - ",localStorage.pass)
    if (navigator.userAgentData.platform) {
      console.log("SI hAY PLATAFORMA");
      this.platform = navigator.userAgentData.platform;
    } else {
      console.log("NO HAY PLATAFORMA , ", navigator.userAgentData.platform);
    }
  },
};
</script>

<style lang="scss" scoped>
.container {
  margin-right: 0px;
}
.containers {background-image: url("../assets/fondos/login.jpg");background-position: center center;background-repeat: no-repeat;background-attachment: fixed;background-size: cover;background-color: #fff;}

.contenedor-izquierdo {padding-top: 25px;padding-left: 25px;padding-right: 25px;padding-bottom: 25px;min-height: 100vh;background-color: rgba(252, 252, 252, 0.6);border-radius: 8px;}

.contenedor-derecho {padding: 0px;}

.img-login {border-radius: 10px;border: 30px solid transparent;}

.form-login {display: flex;justify-content: center;align-items: center;background: none !important;padding: 1px;border-radius: 10px;text-align: left;height: 450px;}

.centrarfrom {width: 100%;height: 300px;}

.titulo {font-weight: bolder;font-size: 18pt;color: #0eb308;text-align: center;margin-top: 2rem;}
.btn-entrar {margin-top: 10px;width: 100%;background: #0eb308;font-size: 12pt;}

.contrasenia {margin-top: 10px;font-weight: bolder;}

.recuperar {color: rgb(6, 6, 177);font-weight: bolder;}

.btn-registrar {text-align: center;font-weight: bolder;color: rgb(6, 6, 177);font-size: 14pt;text-shadow: 1px 1px 2px #363636;}

a {color: rgb(6, 6, 177) !important;}

.form-submit {background: #15d826;border: none;width: 100%;border-radius: 8px;color: white;margin-top: 1rem;padding: 1rem 0;cursor: pointer;transition: background 0.2s;&:hover {  background: #0b9185;}}
</style>