<template>
  <div>
    <b-row>
      <b-col
        cols="12"
        sm="4"
        md="3"
        lg="3"
        xl="3"
        v-for="item in searching"
        :key="item.id_pro"
      >
        <v-card
          style="border-radius: 8px"
          elevation="5"
          max-width="340"
          :loading="loading"
          class="mx-auto"
        >
          <v-list-item
            style="margin: 0px; padding-left: 10px; padding-top: 5px"
          >
            <v-list-item-avatar size="55px" style="border: solid 1px gray">
              <img alt="Avatar" :src="item.photo" />
            </v-list-item-avatar>

            <v-list-item-content style="margin: 0px; padding-left: 0px">
              <v-list-item-title
                :v-text="item.Nombre_Completo"
              ></v-list-item-title>
              <v-list-item-subtitle
                ><v-icon style="margin-left: -3px" color="indigo" small>
                  mdi-map-marker </v-icon
                >{{ item.nombreMunicipio }}
                {{ item.nombreDepartamento }}</v-list-item-subtitle
              >
              <span style="font-size: 6pt">{{ item.fecha_alta }}</span>
            </v-list-item-content>
          </v-list-item>
          <v-img
            v-bind:src="item.thumbnail"
            height="225px"
            @click="verProduct(item)"
          >
            <v-app-bar style="widht: 100%" flat color="rgba(0, 0, 0, 0)">
              <v-spacer></v-spacer>

              <v-icon large :color="item.color" role="img" aria-hidden="false">
                {{ icons.mdiCrown }}
              </v-icon>
            </v-app-bar>
          </v-img>
          <v-rating
            v-model="item.promedio"
            color="yellow darken-3"
            background-color="grey darken-1"
            empty-icon="$ratingFull"
            half-increments
            hover
            size="25"
          ></v-rating>
          <v-card-title
            class="text-truncate"
            style="
              margin-right: 10px;
              font-size: 13pt;
              margin-left: -8px;
              margin-top: -8px;
            "
          >
            <a style="color: black" :href="`/detalleProducto/${item.id_pro}`">{{
              item.titulo
            }}</a>
          </v-card-title>

          <v-card-subtitle style="margin-left: -8px">
            <span class="title"> Q.{{ item.precio }}</span>
          </v-card-subtitle>

          <v-card-actions style="margin-left: -1px; margin-top: -5px">
            <v-icon large>mdi-eye</v-icon>

            <span class="subheading mr-2">{{item.vistas}}</span>
            <v-spacer></v-spacer>
            <v-btn
              @click="verDetalle(item.id_pro)"
              style="margin-top: -10px; font-size: 10pt"
              rounded
              color="success"
              dark
              outlined
            >
              Detalles
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn icon @click="item.ocult_desc = !item.ocult_desc">
              <v-icon>{{
                item.ocult_desc ? "mdi-chevron-up" : "mdi-chevron-down"
              }}</v-icon>
            </v-btn>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="item.ocult_desc">
              <v-divider></v-divider>

              <v-card-text>
                {{ item.descripcion }}
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
        <br />
      </b-col>
    </b-row>
  </div>
</template>

<script>
import axios from "axios";
import { mdiFilterMenu } from "@mdi/js";
export default {
  props: { search: null },
  data() {
    return {
      productos: [],
      rating: 4.5,
      loading: false,
      filter: [],
      select: { title: "Seleccionar", id: null },
      items: [
        { title: "Filtar por...", id: null },
        { title: "categoria", id: 1 },
        { title: "Ubicacin", id: 2 },
      ],
      icons: {
        mdiFilterMenu,
      },
    };
  },
  methods: {},
  computed: {
    searching() {
      if (!this.search) return this.productos;

      const search = this.search.toLowerCase();

      return this.productos.filter((item) => {
        const text = item.titulo.toLowerCase();

        return text.indexOf(search) > -1;
      });
    },
  },
  created() {
    axios
      .get("https://vendeloyaglobal.herokuapp.com/api/productos")
      .then((resultado) => {
        if (resultado.status == 200) {
          this.productos = resultado.data;
        }
      });
  },
};
</script>