<template>
  <div class="contenedor">
    <br>
    <div class="edit mx-auto">
      <div class="style-photo pt-6">
        <photo v-on:photo="onPhotos" :idPhoto="idPhotoActual" :photoProfile="photoUser"></photo>
      </div>
    </div>
    <div class="input-text-profile px-6 mt-6">
      <v-row no-gutters>
        <v-col style="padding-right: 5px;" cols="6"> <v-text-field prepend-inner-icon="mdi-account-outline"
            label="Nombres" v-model="user.nombres" type="text"></v-text-field></v-col>
        <v-col style="padding-left: 5px;" cols="6"><v-text-field label="Apellidos" v-model="user.apellidos"
            type="text"></v-text-field></v-col>
      </v-row>
      <v-text-field prepend-inner-icon="mdi-cellphone" label="Telfono" v-model="user.telefono" type="tel"></v-text-field>
      <v-text-field prepend-inner-icon="mdi-email-outline" label="Correo" v-model="user.email"
        type="email"></v-text-field>
      <v-select prepend-inner-icon="mdi-map-marker-outline" @change="obtenerMuni" v-model="selectDepa"
        :items="departamentos" item-value="id" item-text="nombre" label="Departamento"></v-select>

      <v-select prepend-inner-icon="mdi-pin-outline" v-model="selectMuni" :items="municipios" item-value="id"
        item-text="nombre" label="Municipio"></v-select>
      <v-text-field prepend-inner-icon="mdi-home-outline" label="Direccin" v-model="user.direccion"
        type="text"></v-text-field>
      <br>

      <v-btn large rounded block outlined color="green" @click="updateDataUser()">Guardar</v-btn>
      <br>
      <br>
      <v-dialog v-model="dialog">
        <v-card class="pb-6 px-3">
          <div class="pa-4 text-center">
            <v-avatar color="green" size="130" class="mx-auto" v-if="tipoEdit == 'registro'">
              <v-img class="" contain height="170" src="../../assets/logo.png"></v-img>
            </v-avatar>
            <v-icon color="green" size="100" v-if="tipoEdit == 'normal'">mdi-hands-pray</v-icon>
            <h3 class="text-h6 font-weight-light mb-2">
              {{
                tipoEdit == "normal"
                ? "TUS DATOS FUERON ACTUALIZADOS CORRECTAMENTE!"
                : " BIENVENIDO A VENDELO YA"
              }}
            </h3>
            <span class="text-caption grey--text">Si lo publicas, lo vendes!</span>
          </div>
          <v-btn  color="green" rounded block outlined depressed dark class="mr-4" @click="irInicio(tipoEdit)">
            Finalizar
          </v-btn>
        </v-card>
      </v-dialog>
    </div>

  </div>
</template>
<script>
import store from '../../store'
//import axios from "axios";
//import AppBar from "@/components/AppBar/AppbarGeneral.vue";
import photo from "../../views/PerfilEdit/CreatePhoto.vue";
import updatePhotoProfile from "../../controller/update";
import api from "../../models/controller";
export default {
  components: {
    /* AppBar, */ photo
  },
  data: () => ({
    dialog: false,
    showMenu: false,
    items: [
      { title: "Cambiar foto", action: "subir" },
      { title: "Ver foto", action: "ver" },
      { title: "Eliminar foto", action: "eliminar" },
    ],
    newEdit: false,
    tipoEdit: null,
    step: 1,
    edit: false,
    width: screen.width,
    height: screen.height,
    email: null,
    pixel: "",
    idPhoto: "",
    photoUser: "",
    idPhotoActual: "",
    imagenMiniatura: "",
    fab: false,
    valid: true,
    photoNew: '',
    selectMuni: { nombre: "Seleccionar", id: null },
    selectDepa: { nombre: "Seleccionar", id: null },
    departamentos: [],
    municipios: [],
    oc: null,
    user:{}
  }),

  methods: {
    updateDataUser() {
      var data = {
        id_muni: this.selectMuni.id, nombres: this.user.nombres, apellidos: this.user.apellidos, email: this.user.email, telefono: this.user.telefono, direccion: this.user.direccion, id: this.user.id_user
      }
      api.updateUserBy(data).then(() => {
        this.dialog = true;
        //  this.irInicio(tipoEdit)
      })
    },
    onPhotos: function (item) {
      if (item != "vacio") {
        this.newEdit = true;
        this.PhotoNew = item.img
      } else {
        this.PhotoNew = this.photoUser
        this.newEdit = false;
      }
    },
    actualizarFoto: function () {
      let foto = {
        id: this.user.id_user,
        photo: this.PhotoNew,
      };
      api.updateFotoPut(this.user.id_user, foto).then(() => {
        this.idPhoto = "";
        this.step++;
      });
    },
    actionItem: function (item) {
      if (item == "eliminar") {
        console.log("-")
      } else if (item == "ver") {
        console.log("-");
      }
    },
    regresar: function (tipoEdit) {
      if (tipoEdit == "normal") {
        this.$router.go(-1);
      } else if (tipoEdit == "registro") {
        this.$router.push("/").catch(() => { });
      }
    },
    irInicio: function (tipoEdit) {
      if (tipoEdit == "normal") {
        this.$router.go(-1);
      } else if (tipoEdit == "registro") {
        this.$router.push("/").catch(() => {});
      }
    },
    validate: function () {
      this.$refs.form.validate();
    },
    activarEdit: function () {
      this.fab = !this.fab;
      this.edit = !this.edit;
    },

    updateDireccion: function () {
      var dire = "";
      if (this.user.direccion == "") {
        dire = "ninguna";
      } else {
        dire = this.user.direccion;
      }
      let params = {
        id: this.user.id_user,
        direccion: dire,
      };

      api.updateDirection(this.user.id_user, params).then(() => {
        this.step++;
      });
    },
    actNA: function () {
      let params = {
        id: this.user.id_user,
        nombres: this.user.nombres,
        apellidos: this.user.apellidos,
      };
      api.updateACNA(this.user.id_user, params).then(() => {
        this.step++;
      });
    },

    obtener_imagen: function (e) {
      let file = e.target.files[0];
      this.cargarImagen(file);
      let foto = new FormData();
      foto.append("image", file);

      api.updateFotoPost(foto).then((datafoto) => {
        let dataimg = datafoto.data;
        let urlimagen = dataimg.data;
        this.idPhoto = urlimagen.id_photo;
        this.photoUser = urlimagen.url;
      });
    },
    cargarImagen: function (file) {
      let reader = new FileReader();
      reader.onload = (y) => {
        this.imagenMiniatura = y.target.result;
        this.photoUser = this.imagenMiniatura;
      };
      reader.readAsDataURL(file);
    },
    eliminarFoto: function (id) {
      let data = {
        id: id,
      };

      api.eliminarFotoPost(data).then(() => {
        this.photoUser = this.user.photo;
        this.idPhoto = "";
      });
    },
    actualizarToken: function () {
      api.obtenerToken().then((result) => {
        this.user = result.data[0];
        this.photoUser = this.user.photo;
        this.step++;
      });
    },

    uploadPhoto: function (e) {
      try {
        console.log(e.target.files);
        const { files } = e.target.files;

        const file = files[0];
        updatePhotoProfile(file).then((result) => {
          var dataimg = result;
          this.idPhoto = dataimg.id;
          this.photoUser = dataimg.url;
          this.newEdit = true;
        });
        e.currentTarget.files = null;
      } catch (error) {
        console.log("Error ", error);
      }
    },

    deletePhoto: function (item) {
      let data = {
        id: item,
      };

      api.eliminarFotoPost(data).then(() => {
        this.photoUser = this.user.photo;
        this.idPhoto = "";
        this.newEdit = false;
      });
    },

    obtenerIdPhto: function (photo) { //
      let [a, b, c, d, e, f] = photo.split(/. |! |[/]/, -1);
      a = "";
      b = "";
      c = "";
      d = "";
      e = "";
      this.idPhotoActual = f;
      this.oc = a + b + c + d + e
    },
    obtenerMuni: async function (e) {
      this.municipios = [];
      let id = e.id;
      await api.getMunicipiosDepa(id).then((result) => {
        let muni = result.data;
        this.municipios = [];
        muni.forEach((element) => {
          this.municipios.push({
            id: element.idMunicipio,
            nombre: element.nombreMunicipio,
          });
        });
      });
    },
    getDepartamento: async function () {
      await api.getDepartamento().then((result) => {
        var depa = result.data;
        depa.forEach((element) => {
          this.departamentos.push({
            nombre: element.nombreDepartamento,
            id: element.idDepartamento,
          });
        });
      });
    },
  },

  computed: {
    currentTitle() {
      switch (this.step) {
        case 1:
          return "Foto de Perfil";
        case 2:
          return "Datos Personales";
        case 3:
          return "Direccin";
        default:
          return "Perfil Editado";
      }
    },
  },
  created: function () {
    if (store.getters.isLoggedIn) {

      var data = store.getters.getUser;
      console.log("DATOS DEL SUAURI :", data)
      this.user =data;
      this.isLogin = true;
    this.tipoEdit = 'registro';
    this.width = screen.width;
    if (this.width > 600) {
      this.width = 50;
      this.pixel = "%";
    } else {
      this.width = this.width - 30;
      this.pixel = "px";
    }

      this.getDepartamento();
      api.getUser(data.id_user).then((result) => {
        this.user = result.data[0];
        this.photoUser = this.user.photo;
        this.obtenerIdPhto(this.photoUser);
        api.getidMuni(this.user.id_muni).then((result) => {
          var data = result.data;
          var depaMuni = data.result;
          this.selectMuni = { nombre: depaMuni.nombreMunicipio, id: depaMuni.idMunicipio };
          this.selectDepa = { nombre: depaMuni.nombreDepartamento, id: depaMuni.idDepartamento };
          var idDepa = { id: depaMuni.idDepartamento }
          this.obtenerMuni(idDepa);
        })
      });
    
  } else {
    this.isLogin = false;
  }
  },
};
</script>
<style scoped>
.card-edit {}

.button-file {
  border-radius: 8px;
}

.input-photo {
  width: 70px;
  background: red;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
}

.file-upload {
  border-radius: 50%;
  width: 250px;
  height: 250px;
  /*border: 1px solid #01e934;*/
  /*border-radius: 100px;*/
  overflow: hidden;
  position: relative;
}

.file-upload input {
  position: absolute;
  width: 100%;
  height: 50px;
  left: -200px;
  top: -200px;
  background: transparent;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
}

.file-upload img {
  margin: 10px;
  border-radius: 8px;
  color: rgb(241, 241, 6);
}

.img-close {
  position: absolute;
  top: 25px;
  right: 30px;
  z-index: 3;
}

.edit {
  width: 100%;
  background: rgb(255, 255, 255);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contenedor {
  width: 100%;
  background: rgb(255, 255, 255);

  border-radius: 0px !important;
}

.btn-cancelar {
  position: absolute;
  top: 10px;
  right: 10px;
}

.gesture-tap {
  opacity: 0.4;
  top: 30%;
}


/*DISEO INPUT*/
.input-text-profile {}
</style>