<template>
  <div>
    <div v-if="items.length==0">
      <v-skeleton-loader v-bind="attrs" v-for="item in [0,1,2,3]"  :key="item"
          type="list-item-two-line"></v-skeleton-loader>
    </div>


    <v-list two-line>
      <v-list-item-group>

        <template v-for="(item, index) in items">
          <v-list-item :key="item.id">
            <v-list-item-content>
              <v-list-item-title class="font-weight-medium">{{ item.cantidad }} moneda(s)</v-list-item-title>

              <v-list-item-subtitle class="text--primary">{{ item.detalle }}</v-list-item-subtitle>

              <v-list-item-subtitle> {{ item.created_at }}</v-list-item-subtitle>
            </v-list-item-content>

            <v-list-item-action>
              <v-list-item-action-text><v-icon>mdi-bell</v-icon></v-list-item-action-text>
            </v-list-item-action>

          </v-list-item>

          <v-divider v-if="index < items.length - 1" :key="index"></v-divider>
        </template>
      </v-list-item-group>
    </v-list>
  </div>
</template>

<script>
import api from "../../../../models/controller";
import store from '@/store'
export default {
  data() {
    return {
      attrs: {
        class: 'mb-6',
        boilerplate: true,
        elevation: 0,
      },
      money: 0,
      items: [
      ],
    };
  },

  methods: {
    getMoney: function (id) {
      api.getFichasHistorial(id).then((result) => {
        var fichas = result.data;
        this.items = fichas.data;

      });
    },
  },
  created() {

    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
      this.isLogin = true;
      this.getMoney(data.id_user);
    }
    /* if (window.localStorage.getItem("user")) {
       this.isLogin = true;
       var user = JSON.parse(window.localStorage.getItem("user"));
       let id = user.user;
       this.getMoney(id.id_user);
     }*/
  },
};
</script>