<template>
  <div>
    <perfil style="margin-top: -7px !important" :id="id" :folder="folder" />
  </div>
</template>
<script>
import perfil from "@/components/Perfil/Perfil.vue";
import store from '../store'
export default {
  name: "Perfil",
  components: { perfil },
  data() {
    return {
      id: null,
      folder: null,
    };
  },

  created() {

    if (store.getters.isLoggedIn) {
      this.id = this.$route.params.id;
      this.folder = this.$route.params.folder;

      this.$watch(
        () => this.$route.params,
        (toParams) => {
          // react to route changes...
          this.id = toParams.id;
          this.folder = toParams.folder;
        }
      );
    } else {
      this.$router.push("/").catch(() => {});
    }
  },
};
</script>

