<template>
  <div class="mt-3" style="overflow: hidden">
    <div class="d-none d-sm-flex">
      <div v-if="menu.length < 1" style="min-height: 4px">
        <v-progress-linear v-model="value" :active="show" :indeterminate="query" :query="true"></v-progress-linear>
      </div>
      <v-sheet v-else class="mx-auto text-center" :max-width="this.$vuetify.breakpoint.width">
        <v-slide-group show-arrows v-model="model">
          <v-slide-item v-for="item in menu" :key="item.id_categoria" v-slot="{ active, toggle }" @change="subcat(item)">
            <v-btn class="mx-2" :input-value="active" active-class="green white--text" depressed rounded @click="toggle">
              <v-icon>mdi-{{ item.icon }}</v-icon>
              {{ item.nombre_categoria }}
            </v-btn>
          </v-slide-item>
        </v-slide-group>
      </v-sheet>
    </div>

    <div class="row" style="margin-top: -30px">
      <div class="col-3 d-none d-sm-flex">
        <div class="ml-md-5 mr-md-5 mt-md-12 mt-8 mb-0 ml-1 mr-1">
          <v-text-field dense ref="search" v-model="search" class="border-3" rounded solo label="Buscar en vendelo ya"
            append-icon="search" v-on="datamover"></v-text-field>

          <v-btn text class="text-truncate text-decoration-underline" @click="activarUbi"><v-icon color="blue">{{
            icons.mdiMapMarker }}</v-icon>
            <span class="d-inline-block text-truncate mt-1" style="max-width: 250px">
              {{ address }}
            </span>
          </v-btn>

          <p class=" text-h5 mt-8">Publicidad</p>

          <PublicidadEscritorio></PublicidadEscritorio>


        </div>
        <v-divider class="mt-15" vertical></v-divider>
      </div>
      <div class="col-xs-12 col-md-9 col-sm-12 col-lg-9 col-xl-9">


        <div class="ml-md-15 mr-md-15 mt-md-11 mt-7 mb-0 ml-5 mr-5"></div>
        <v-row no-gutters class="d-flex d-sm-none pl-4 pr-4 mx-auto">
          <v-col cols="4">
            <v-btn plain style="margin-left: -12px; padding-left: 0px;" text
              class="text-truncate d-flex d-sm-none text-sm-body-2" @click="activarUbi"><v-icon color="blue">{{
                icons.mdiMapMarker }}</v-icon>
              <a class="d-inline-block text-truncate mt-1 font-weight-light" style="max-width: 240px; font-size: 8.5pt">
                {{ address.slice(0, -10) }}...
              </a>
            </v-btn>
            <meubication :cord="center" :lat="center.lat" :lng="center.lng" v-model="activarMapa"
              v-on:meUbi="onUbicacion" />

            <br>


          </v-col>
          <v-col cols="8" class="text-end">
            <v-btn rounded outlined dark class="green--text font-weight-bolder" @click="open">
              <v-icon class="mr-2" small>{{ icons.mdiTuneVariant }}</v-icon>Filtros
            </v-btn>
          </v-col>
        </v-row>

        <div class="d-block mt-3"></div>

        <!--NOS MUESTRA LA LISTA DE PRODUCTOS EN LA VISTA PRINCIPAL-->
        <div class="mt-0">
          <div class="pl-md-2 pr-md-2" :hidden="busquedaPersonalizada">
            <div class="contendor-cuerpo rounded-lg mt-3 mb-0">

              <div class="px-2 mt-3 pr-5">
                <v-text-field class="field-text" single-line height="25px" @change="ir" @click="ir" color="green" filled
                  rounded dense label="Qu quieres publicar hoy?" append-outer-icon="mdi-shopping"></v-text-field>
              </div>
              <p class="pl-1">Publicidad</p>
              <publicidad class="mb-0 -none d-md-flex d-lg-none"></publicidad>


              <!--                  -->
              <listProRecientView></listProRecientView>
              <!--MOSTRANDO LOS PRODUCTOS MS DESTACADOS (DE PLAN MS ALTO AL MS BAJO)-->
              <listaProductosDestacados />

              <v-divider></v-divider>
              <!--MOSTRANDO LOS PRODUCTOS MS RECIENTES-->
              <listaProductsRecients />

            </div>
          </div>
          <div class="pl-md-2 pr-md-2" :hidden="!busquedaPersonalizada">
            <h5 class="ml-5 mt-3">Resultado de Busqueda</h5>
            <resultadosBusqueda v-if="busquedaPersonalizada == true" :value="search" />
          </div>
        </div>

        <br />
      </div>
    </div>

    <login v-model="isLogin" />

    <!--BOTON VENDER  -->
    <!--ACTIVA BOOTOM SHEETS -->
    <vue-bottom-sheet ref="myBottomSheet" max-width="100%" :max-height="maxHeight" :click-to-close="clickToClose">
      <v-container>
        <h3>Categorias</h3>
        <v-list-item-group color="primary">
          <v-list-item v-for="item in menu" :key="item.id_categoria" @change="subcat(item)">
            <v-list-item-icon>
              <v-icon :color="getcolor()">mdi-{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ item.nombre_categoria }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-container>
    </vue-bottom-sheet>
    <!-- <btnVender></btnVender>-->
  </div>
</template>

<script>
//import axios from "axios";
import api from "../models/controller";
import { mdiMapMarker } from "@mdi/js";
import PublicidadEscritorio from '@/components/PublicidadPagada/PublicidadEscritorio.vue'
import listaProductosDestacados from "@/components/ObtenerProductos/ObtenerProductos.vue";
import listaProductsRecients from "@/components/ObtenerProductos/getProductsRecients.vue";
import listProRecientView from "@/components/ProductRecientView/RecientsView.vue";

import meubication from "@/components/Mapa/MeUbication";
//import chatbot from "@/components/ChatBot/ChatBot.vue";
import publicidad from '@/components/PublicidadPagada/VistaPrevia.vue'
//import btncat from "@/components/Bottoms/FiltroCategoria.vue";
import store from "@/store"
import VueBottomSheet from "@webzlodimir/vue-bottom-sheet";
import { mdiTagSearchOutline } from "@mdi/js";
import { mdiFilterMenu } from "@mdi/js";
import { mdiTuneVariant } from "@mdi/js";
import resultadosBusqueda from "@/components/Filtros/ResultadosFiltro.vue";
import login from "@/components/Login/Login.vue";



//import btnVender from "@/components/AgregarProducto/BtnVender";
import { gmapApi } from "vue2-google-maps";
export default {
  components: {
    login,
    listaProductosDestacados,
    listProRecientView,
    VueBottomSheet,
    resultadosBusqueda,
    meubication,
    listaProductsRecients,
    publicidad,
    PublicidadEscritorio

    // chatbot
  },
  props: ["id"],
  data() {
    return {
      lat: null,
      lng: null,
      geocoder: null,
      activarMapa: false,
      address: "Mi ubicacin...",
      center: {},
      datamover: {
        click: () => this.$router.push("/buscar").catch(() => { }),
      },
      maxHeight: "90%",
      clickToClose: true,
      activeSheet: false,
      actBottomSheet: false,
      select: { title: "Seleccionar", id: null },
      items: [
        { title: "categoria", id: 1 },
        { title: "Ubicacin", id: 2 },
      ],
      isLogin: false,
      loading: false,
      toogle: null,
      product: {},
      productos: [],
      SubCategoriaproductos: false,
      id_SubCategoria: null,
      prod_categorias: [],
      showScheduleForm: false,
      value: 0,
      icons: {
        mdiTagSearchOutline,
        mdiFilterMenu,
        mdiTuneVariant,
        mdiMapMarker,
      },
      hidden: false,
      query: false,
      sinResultados: false,
      interval: 0,
      menu: [],
      previa: [],
      show: false,
      dialogVisible: false,
      subcategoria: [],
      search: null,
      listado: [],
      expandNews: false,
      expandSingle: false,
      model: null,
      cardscroll: null,
      active: false,
      toggle: false,
      nombre_subCategoria: "",
      ubicacion: "Mi ubicacin...",
      selected: [],
      filter: [],
      valores: null,
      id_catego: null,
      busquedaPersonalizada: false,
    };
  },

  methods: {

    ir() {
      if (store.getters.isLoggedIn) {
        this.$router.push("/addProduct").catch(() => { });
      } else {
        this.isLogin = true;
        console.log("NO ESTA LOGEADO");
      }

    },

    getcolor: function () {
      var makingColorCode = '0123456789ABCDEF';
      var finalCode = '#';
      for (var counter = 0; counter < 6; counter++) {
        finalCode = finalCode + makingColorCode[Math.floor(Math.random() * 16)];
      }
      return finalCode;
    },
    activarUbi: function () {
      this.$gmapApiPromiseLazy().then(() => {
        navigator.geolocation.getCurrentPosition(async (position) => {
          this.center = {
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          };
          this.activarMapa = !this.activarMapa;
        });
      });
    },
    gritar: function () {
      this.$emit("se_cae", "hijo menor");
    },
    open() {
      this.$refs.myBottomSheet.open();
    },
    close() {
      this.$refs.myBottomSheet.close();
    },
    onResultados(datos) {
      this.id_catego = datos;
    },
    onUbicacion(dire) {
      this.address = dire;
    },
    filterResults(value) {
      console.log("", value);
      this.$router.push("/buscar").catch(() => { });
      /* console.log("VALUR :", value);
      if (value.length>0) {
        this.busquedaPersonalizada = true;
        this.search= value;
      } else {
        this.busquedaPersonalizada =false
        this.filter = [];
        this.search= '';
      } */
    },
    irVender() {

      if (store.getters.isLoggedIn) {
        this.$router.push("/addProduct").catch(() => { });
      } else {
        this.isLogin = true;
        console.log("NO ESTA LOGEADO");
      }
    },

    subcat(e) {
      if (e != undefined) {
        let router = e;
        this.$router
          .push({
            path: `categoria/${router.nombre_categoria}/${router.id_categoria}`,
            params: {
              lat: "data",
            },
          })
          .catch(() => { });
      }
    },
  },

  computed: {
    google: gmapApi,
    dataprueba() {
      return this.search;
    },
    allSelected() {
      return this.selected.length === this.productos.length;
    },
    categories() {
      const search = this.search.toLowerCase();

      if (!search) return this.productos;

      return this.productos.filter((item) => {
        const text = item.titulo.toLowerCase();
        return text.indexOf(search) > -1;
      });
    },
    selections() {
      const selections = [];
      for (const selection of this.selected) {
        selections.push(selection);
      }
      return selections;
    },
  },

  mounted() {
    this.$gmapApiPromiseLazy().then(() => {
      if (this.google) {
        this.geocoder = new this.google.maps.Geocoder();
      }
    });
    this.SubCategoriaproductos = false;
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },

  created() {
    this.SubCategoriaproductos = false;
    api.getAllCategorias().then((result) => {
      var menu = result.data;
      var res = menu;
      var other = {};
      //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU

      for (let i = 0; i < res.length; i++) {
        if (res[i].nombre_categoria != 'Otros') {
          this.menu.push(res[i])
        } else {
          other = res[i];
        }
      }
      this.menu.push(other)
      this.loading = false;


      //console.log("MENU ", this.menu);
    });
    this.$gmapApiPromiseLazy().then(() => {
      navigator.geolocation.getCurrentPosition((position) => {
        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
        const latlng = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        };
        try {
          this.geocoder.geocode({ location: latlng }, (results, status) => {
            if (status == "OK") {
              this.address = results[0].formatted_address;
              //console.log("Direccin al entrar", results[0]);
            }
          }); //returns nothing!!
        } catch (error) {
          console.log(error.message);
        }
      });
    });
  },

  watch: {
    selected() {
      this.search = "";
    },
    dialogVisible(visible) {
      if (visible) {
        // Here you would put something to happen when dialog opens up
        console.log("Dialog was opened!");
      } else {
        console.log("Dialog was closed!");
      }
    },
  },
  inject: {
    theme: {
      default: { isDark: false },
    },
  },
  beforeCreate: function () { },
};
</script>

<style lang="scss" scoped >
$list-width: 320px;

.full-control {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap-reverse;
}

.list {
  width: $list-width;
}

.full-control>.md-list {
  width: $list-width;
  max-width: 100%;
  height: 400px;
  display: inline-block;
  overflow: auto;
  border: 1px solid rgba(#000, 0.12);
  vertical-align: top;
}

.control {
  min-width: 250px;
  display: flex;
  flex-direction: column;
  padding: 16px;
}

.contendor-cuerpo {
  width: 100%;
  margin-right: 0px;
}

.contendor-cuerpo {
  // background: rgb(244, 244, 247);
  border-radius: 3px;
  padding: 1px;
  margin-right: 0px;
}

.btn-plus {
  position: fixed;
  height: 50px !important;
  bottom: 15px;
  right: 15px;
  color: #ffffff !important;
  background: rgb(10, 165, 10) !important;
  border: 3px solid rgb(187, 252, 172) !important;
  box-shadow: 5px 5px;
}
</style>