<template>
    <div>
        <h4 class="ml-2 font-weight-bold">Publicaciones</h4>


        <v-list style="margin-right: 0px !important;">
            <v-list-item-group>
                <template v-for="(item, index) in publications">
                    <v-list-item style="margin-left: -15px !important;margin-right: -15px !important;" :key="item.titulo" @click="verDetalle(item.id_pro)">

                        <v-list-item-content style="margin-right: 0px !important;">
                            <v-list-item-title class="font-weight-bold ml-1">{{ item.titulo }}</v-list-item-title>
                            <p style="margin-top: -1px;
                            padding-top: 0px;
                                line-height: 12px !important;
                              " class="font-weight-regular caption">
                                <v-icon size="15" class="mr-0">mdi-map-marker</v-icon>
                                {{ item.fecha_alta }}
                            </p>
                            <v-list-item-subtitle class="ml-1 mt-1 mb-1">{{ item.descripcion
                            }}</v-list-item-subtitle>
                            <v-img style="object-fit: cover;" height="350" :src="item.thumbnail
                                "></v-img>
                            <v-row no-gutters class="pl-2 pr-2 pt-2">
                                <v-col cols="6">
                                    <p class="font-weight-black title precio-style">
                                        Q.{{ item.precio }}
                                    </p>
                                </v-col>
                                <v-col cols="6" class="text-end">
                                    <!--<p class="vistas font-weight-light">
                                <v-icon class="mr-2">mdi-eye</v-icon><b>{{ item.vistas }}</b> vista(s)
                              </p>-->
                                </v-col>
                            </v-row>
                        </v-list-item-content>

                    </v-list-item>

                    <v-divider  v-if="index < publications.length - 1" :key="index"></v-divider>
                </template>
            </v-list-item-group>
        </v-list>
    </div>
</template>
<script>
import api from '../../models/controller'
export default {
    props: { id: null },
    data() {
        return {
            publications: [],
        };
    },
    // Vuetify components provide
    // a theme variable that is
    // used to determine dark
    inject: {
        theme: {
            default: { isDark: false },
        },
    },

    methods:{
        verDetalle(id) {
      if (localStorage.getItem("user")) {
        var credential = JSON.parse(localStorage.getItem("user"));
        let user = credential;
        let vista = {
          id_user: user.id_user,
          id_pro: id,
          like: 0,
        };
        api.createView(vista).then(() => {
        });
      }
      // this.$router.push("/detalleProducto", {id_pro: id});
      this.$router.push({
        path: `/detalleProducto/${id}`,
        id_pro: { id },
      });

      //window.location.href= `/detalleProducto/${id}`
    },
    },

    created() {
        try {
            api.getUserAllProduct(this.id).then((result) => {
                this.publications = result.data;
            });
        } catch (error) {
            console.log(error);
        }

    },
};
</script>
<style scoped>
.c {
    box-shadow: 4;
}

.precio-style {
    color: rgb(43, 222, 43);
}
</style>
