    editProfile(item) {
      this.dialog = item;
    },
    convertir() {
      this.cajon = this.folder;
    },
    navegateItem(title, item) {
      this.cajon = item;
      this.title = title;
      this.dialog = true;
    },

    obtenerIdPhto: function (photo) {
      let [a, b, c, d, e, f] = photo.split(/. |! |[/]/, -1);
      a = "";
      (b = ""), (c = ""), (d = ""), (e = ""), (this.idPhoto = f);

      this.oc= a+b+c+d+e
    },
  },
  computed: {},
  created() {
    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
    this.user.photo= 'https://cdn-icons-png.flaticon.com/512/149/149071.png'
    //window.scrollTo(0, 0);
      try {
        api.getUser(data .id_user).then((result) => {
          this.user = result.data[0];
          this.obtenerIdPhto(this.user.photo);
          //console.log("Datos del Usuario array: ",  this.user);
        });
        this.$watch(
          () => this.folder,
          (toParams) => {
            // react to route changes...
            this.cajon = toParams;
          }
        );
        this.cajon = this.folder;
        //getUser(id)

        api.getUser(data.id_user).then((result) => {
          this.user = result.data[0];
          this.obtenerIdPhto(this.user.photo);
          //console.log("Datos del Usuario array: ",  this.user);
        });
      } catch (error) {
        console.log(error);
      }
    }
  },
};
</script>

