<template>
  <div>
    <AppBar />

    <carousel class="" />
   <!-- <v-btn v-if="deferredPrompt" ref="addBtn" class="add-button" @click="clickCallback">
      <v-icon>mdi-cellphone-arrow-down-variant</v-icon>
    </v-btn> -->
    <sesion v-if="!islogin" class="d-flex d-sm-none" />
    <menumedio />

    <v-snackbar v-if="active" v-model="active"  color="success" style="padding-top: -10px; padding-bottom: -15px;"
      :timeout="timeout">
      <v-row style="padding-top: -10px;" no-gutters>
        <v-col cols="6">
          <img style="width: 100px; height: 100px;" src="../assets/logo.png" alt="">
        </v-col>
        <v-col cols="6">
          Instala con acceso directo
          <v-btn dense @click="install()" outlined rounded class="mt-3">Instalar</v-btn>
        </v-col>
      </v-row>


      <template v-slot:action="{ attrs }">
        <v-btn color="red" style="font-weight: bolder;" text v-bind="attrs" @click="active = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>



<script>
// @ is an alias to /src
//import navbar from '@/components/Navbar.vue';
//import axios from "axios";
import store from '../store'

//import notificar from '../models/controllerOnesignal'
//import api from "../models/controller";
import sesion from "@/components/Login_Register/Home.vue";
import carousel from "@/components/Carousel.vue";
import menumedio from "@/components/MenuMedio.vue";
import AppBar from "@/components/AppBar/AppbarGeneral.vue";
//import AuthService from '@/services/AuthService.js';

export default {
  name: "Home",

  components: {
    //navbar,
    carousel,
    menumedio,
    AppBar,
    sesion
  },
  data() {
    return {
      islogin: null,
      active: false,
      timeout: 7000,
      deferredPrompt: null

    };
  },

  beforeCreate() {

  },
  mounted() {

    //this.captureEvent();


   /* if ("serviceWorker" in navigator) {
      navigator.serviceWorker.register("../service-worker.js").then(() => {

      })
        .catch(err => {
          console.log(err);
        })
    }*/


    // window.scrollTo(0, 0);

  },

  methods: {
    install: function () {
      this.deferredPrompt.prompt();
    },

    captureEvent() {
      window.addEventListener('beforeinstallprompt', (e) => {
        // ! Prevent Chrome 67 and earlier from automatically showing the prompt
        e.preventDefault()
        // Stash the event so it can be triggered later.
        this.deferredPrompt = e
        this.active = true;
      });

      window.addEventListener('appinstalled', (event) => {
        console.log('', 'appinstalled', event);
        // Clear the deferredPrompt so it can be garbage collected
        this.deferredPrompt = null;
        this.active = false;
      });
    },
    clickCallback() {
      // Show the prompt
      this.deferredPrompt.prompt()
      // Wait for the user to respond to the prompt
      this.deferredPrompt.userChoice.then((choiceResult) => {
        if (choiceResult.outcome === 'accepted') {
          // Call another function?
        }
        this.deferredPrompt = null
      })
    },
  },

  created() {



    window.addEventListener("beforeinstallprompt", (e) => {
      e.preventDefault();
      // Stash the event so it can be triggered later.
      this.deferredPrompt = e;
      this.active = true;
    });

    window.addEventListener('appinstalled', (event) => {
        console.log('', 'appinstalled', event);
        // Clear the deferredPrompt so it can be garbage collected
        this.deferredPrompt = null;
        this.active = false;
      });



    

    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
      this.islogin = true;
      var user = {
        user: data
      }
      localStorage.setItem("user", JSON.stringify(user));

    } else {
      this.islogin = false;
    }

  },
};
</script>

