<template>
    <div class="cajon">

        <v-container>
            <v-form v-model="valid" ref="form" class="container-cajon my-2 mx-2 px-3 py-3"
                @submit.prevent="savePagoPublicidad">
                <v-alert v-if="camVacio == true" class="mt-5" rounded-lg rounded dense outlined type="error">
                    Parece que hay <strong>campos vacios</strong>. Favor completa para poder continuar.
                </v-alert>
                <div class="text-center mt-5" style=" margin-top: 0px; margin-bottom: -30px;">
                    <p class="title">
                        ANUNCIATE CON NOSOTROS!</p>
                </div>

                <div class="text-center" style=" margin-top: 0px;">
                    <div class=" text-center">
                        <img src="../../assets/logoicon.png" alt="logo">
                    </div>

                    <!--<div style="margin-top: -40px;">
                        <v-card elevation="2" class="py-3 px-3 card-cuenta" rounded-lg
                            style="border-radius: 10px;  border-bottom:solid 10px green !important">
                            <div class="">DNDE DEPOSITAR?</div>
                            <div class="texto subtitle-1"><b style="color: green;">BANCO:</b> PROMERICA</div>
                            <div class="texto subtitle-1"><b style="color: green;">TITULAR: </b>PLIISS TECHNOLOGY BUSINESSES
                            </div>
                            <div class="texto subtitle-1"><b style="color: green;">No. </b>12335010221476</div>
                            <div class="texto subtitle-1"><b style="color: green;">TIPO: </b>MONETARIA</div>
                        </v-card>
                    </div>-->
                </div>


                <div class="mb-8 text-center mt-5" style="font-size: 18pt; font-weight: bolder;  ">
                    Completar formulario
                </div>

                <p class="mt-5" style="font-size: 14pt;">Datos publicitarios</p>
                <div class="mb-5 body-2">Recuerda que estos datos son los que sern visibles para el usuario en tu espacio
                    publicitario y as poder ponerse en contacto con tu marca.</div>

                <v-text-field :rules="[rules.required]" v-model="empresa" dense filled rounded
                    label="Nombre de Empresa"></v-text-field>
                <v-textarea v-model="descripcion" dense counter="250" :rules="[rules.length(250), rules.required]" filled
                    rounded rows="2" row-height="20" label="Descripcin breve"></v-textarea>
                <p>Redes Sociales</p>
                <div class="mb-5 body-2">Ingresa el link que lleve ha tus redes sociales. Llena los que consideres </div>

                <div class="text-center">
                    <div class="mt-1">
                        <v-text-field dense v-model="linkFacebook" filled rounded label="Link Facebook"
                            prepend-inner-icon="mdi-facebook" hint="https://www.facebook.com/tu_user"
                            persistent-hint></v-text-field>
                        <v-text-field dense v-model="linkInstagram" filled rounded label="Link Instagram"
                            prepend-inner-icon="mdi-instagram" hint=" https://www.instagram.com/tu_user"
                            persistent-hint></v-text-field>

                        <v-row>
                            <v-col cols="5">
                                <v-select rounded v-model="itemCod" :items="codigos" item-value="id" filled
                                    item-text="descripcion" label="Cdigo" persistent-hint return-object
                                    single-line></v-select>
                            </v-col>
                            <v-col cols="7">
                                <v-text-field dense v-model="linkWhatsapp" type='tel' filled rounded label="No. Whatsapp"
                                    prepend-inner-icon="mdi-whatsapp"></v-text-field>
                            </v-col>
                        </v-row>

                    </div>
                </div>

                <p class="mt-1">Pgina Web</p>
                <v-text-field hint=" https://mi_pagina.com" persistent-hint v-model="linkPagina" dense
                    prepend-inner-icon="mdi-web" filled rounded label="Link pgina"></v-text-field>

                <p class="mt-0">Imagen de publicidad</p>

                <div class="img-principal">
                    <div class="text-center container-img-publicidad">
                        <photoPublicidad v-on:photoPublicidad="onPhotoPublicidad"></photoPublicidad>
                    </div>
                </div>

                <div class="mt-5 picker-color">
                    <p>Elige el color para tu botn principal</p>
                    <div class="tex-center picker-cajon mb-5">
                        <v-btn rounded dark class="btn-verdetalles" :color="color">MS DETALLES</v-btn>

                    </div>
                    <br>

                    <div class="tex-center picker-cajon mt-3">
                        <v-color-picker v-model="color" dot-size="25" swatches-max-height="200"></v-color-picker>
                    </div>
                </div>


                <p class="mt-5">Redireccionamiento</p>
                <div class="mb-5 body-2">Selecciona con que red social quieres que vean ms detalles de tu publicacin.
                </div>
                <v-radio-group v-model="row" row>
                    <v-radio label="Facebook" value="facebook"></v-radio>
                    <v-radio label="Instagram" value="instagram"></v-radio>
                    <v-radio label="Whatsapp" value="whatsapp"></v-radio>
                    <v-radio label="Pgina Web" value="pagina"></v-radio>
                </v-radio-group>
                <br>
                <v-divider></v-divider>

                <p style="font-size: 14pt;" class="mt-5">Datos del pago</p>
                <div class="mb-5 body-2">Para completar la compra debers enviar los siguientes datos para procesar y
                    publicar tu anuncio.</div>

                <!--<v-text-field  v-model="banco" dense filled rounded
                    label="Banco donde deposito"></v-text-field>-->

                <v-text-field name="message" :rules="[rules.required]" v-model="noBoleta" dense type='number' filled rounded
                    label="No. Referencia o Boleta"></v-text-field>
                <v-text-field :rules="[rules.required]" v-model="cantDeposito" dense type='number' filled rounded
                    label="Cantidad que deposito"></v-text-field>
                <v-select :rules="[rules.required]" rounded v-model="itemPlan" :items="planes" item-value="id" filled
                    item-text="descripcion" label="Seleccionar Plan de Publicidad" persistent-hint return-object
                    single-line></v-select>
                <div class="text-center">
                    <v-btn color="blue" style="
          color: #fff;
          margin-left: 5px;
          box-shadow: 1px 1px 6px 2px rgba(255, 251, 3, 0.7);" class="parpadea btn-warning" dark
                        v-if="itemPlan.id != null" block><a style="color:white !important" target="_blank"
                            :href="itemPlan.link_pago"> <v-icon>mdi-credit-card-outline</v-icon> Presiona aqu para ir al
                            link de PAGO</a></v-btn>
                </div>
                <br>
                <v-text-field name="to_name" :rules="[rules.required]" v-model="representante" dense type='text' filled
                    rounded label="Nombre del representante"></v-text-field>
                <v-row>
                    <v-col cols="6">
                        <v-text-field :rules="[rules.required]" v-model="nit" dense hint="Ingrese sin guion" persistent-hint
                            type='number' filled rounded label="NIT"></v-text-field>
                    </v-col>
                    <v-col cols="6">
                        <v-text-field :rules="[rules.required]" v-model="telefono" dense type='tel' filled rounded
                            label="Telfono"></v-text-field>
                    </v-col>
                </v-row>
                <v-text-field name="from_email" :rules="[rules.required]" v-model="correo" dense class="mt-2" type='text'
                    filled rounded label="Correo electrnico"></v-text-field>
                <p class="mt-0">Imagen boleta de pago</p>

                <div class="img-principal">
                    <div class="text-center container-img-publicidad">
                        <photoBoleta v-on:photoPago="onPhotos"></photoBoleta>
                    </div>
                </div>

                <br>
                <input class="submit" type="submit" name="send" />
            </v-form>
        </v-container>

        <v-dialog v-model="dialog" persistent max-width="290">
            <div>
                <v-card style="border-radius: 10px !important;" min-height="100" color="white" elevation="0" tile dark
                    class="text-center py-10">
                    <v-progress-circular class="mt-3" size="120" width="5" indeterminate
                        color="amber"></v-progress-circular>
                    <p class="mt-5" style="color:black">Procesando...</p>
                </v-card>
            </div>
        </v-dialog>

        <v-dialog v-model="dialogSuccess" persistent max-width="290">
            <div>
                <v-card style="border-radius: 10px !important;" min-height="100" color="white" elevation="0" tile dark
                    class="text-center py-10">
                    <v-icon size="150" style="color:green">mdi-receipt-text-check</v-icon>
                    <p style="color:rgb(207, 4, 4); font-size: 16pt;">Gestion No. {{ gestion }}</p>
                    <p style="color:black">Tu informacin fue enviada correctamente. Pronto se estarn comunicando contigo
                    </p>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn style="font-size: 16pt;" color="green darken-1" text @click="enviarNueva()">
                            Ok
                        </v-btn>

                        <v-spacer></v-spacer>
                    </v-card-actions>
                </v-card>
            </div>

        </v-dialog>
    </div>
</template>

<script lang="ls">
import photoPublicidad from '../Photo/subirPhotoPublicidadMarca.vue'
import photoBoleta from '../Photo/sendBoletaPhotoCash.vue'
import api from '../../models/controller'
import veryemail from '../../models/email'

export default {
    components: { photoBoleta, photoPublicidad },
    data: () => ({
            type: 'hex',
            rgba: { r: 255, g: 0, b: 255, a: 0.5 },
            column: null,
            row: null,

           // color: '#1976D2FF',
            dialogm1: '',
            dialogCod: false,
            activeListCode: false,
            codigos: ['502'],
            itemCod: '502',
            camVacio: false,
            valid: true,
            dialog: false,
            dialogSuccess: false,
            empresa: null,
            descripcion: null,
            gestion: null,
            noBoleta: null,
            cantDeposito: null,
            banco: '',
            representante: '',
            nit: '',
            telefono: '',
            correo: '',
            urlB: '',
            itemPlan: {},
            items: ['Foo', 'Bar', 'Fizz', 'Buzz'],
            photoPago: '',
            photoPubli: '',
            amenities: [],
            activeChip: null,
            isFacebook: false,
            isInstagram: false,
            isWhatsapp: false,
            neighborhoods: [1],
            linkFacebook: null,
            linkInstagram: null,
            linkWhatsapp: null,
            linkPagina: null,
            planes: [],
            rules: {
                length: len => v => (v || '').length <= len || `Longitud de carcter no vlida, obligatorio ${len}`,
                required: value => !!value || 'Campo requerido',
            }
    }),

    computed: {
        color: {
            get() {
                return this.type
            },
            set(v) {
                this.type = v
            },
        },

    },

    methods: {
hexToRGB: function(hex, alpha) {
    var r = parseInt(hex.slice(1, 3), 16),
        g = parseInt(hex.slice(3, 5), 16),
        b = parseInt(hex.slice(5, 7), 16);
        return "rgba(" + r + ", " + g + ", " + b + ", " + alpha + ")";
},


        getColor() {
            console.log("Color: ", this.color);
         
            console.log("Color RG: ",  this.hexToRGB(this.color,0.63));
          

        },

        tomarCod: function () {
            this.dialogCod = true;
            return '502'
        },

        enviarNueva: function () {
            this.dialogSuccess = false;
            this.$router.go(0);
        },
        salir: function () {
            this.dialog = false;
            window.close()
        },

        getPlanesMarcas: function () {
            api.getPlanesPublicidadMarcas().then((res) => {

                var data = res.data;
                this.planes = data.data
            })
        },
        onPhotoPublicidad(item) {
            this.photoPubli = item;
        },
        onPhotos(item) {
            this.urlB = item;
        },

        savePagoPublicidad: function (e) {
            if (this.$refs.form.validate()) {
                this.dialog = true;
                window.setTimeout(() => {
var linkWhat = null;
                    if(this.linkWhatsapp != null){
                        linkWhat=`https://wa.me/${this.itemCod}${this.linkWhatsapp}`
                    } else {
                        linkWhat= null
                    }

                    var pass = "";
                    var str = "1234567890987654321";
                    for (let i = 1; i <= 8; i++) {
                        var char = Math.floor(Math.random() * str.length + 1);
                        pass += str.charAt(char);
                    }
                    this.gestion = pass;
                    var fecha = new Date();
                    var data = {
                        pago: {
                            idPlan: this.itemPlan.id,
                            gestion: this.gestion,
                            noBoleta: this.noBoleta,
                            cantDeposito: this.cantDeposito,
                            banco: this.banco,
                            representante: this.representante,
                            nit: this.nit.replace('-', ''),
                            telefono: this.telefono,
                            correo: this.correo,
                            url: this.urlB.img,
                            comentario: '',
                            fecha: fecha.toISOString(),
                            estado: 'Nuevo',
                            estadoFalla: '0'
                        },
                        venta: {
                            idPlan: this.itemPlan.id,
                            gestion: this.gestion,
                            empresa: this.empresa,
                            descripcion: this.descripcion,
                            facebook: this.linkFacebook,
                            instagram: this.linkInstagram,
                            whatsapp:  linkWhat,
                            pagina: this.linkPagina,
                            linkPrincipal: this.row,
                            colorBoton: this.hexToRGB(this.color,0.63),
                            url: this.photoPubli.img,
                            estado: 'Nuevo',
                            fecha: fecha.toISOString(),
                            fecha_fin: this.sumarDias(fecha),
                            fecha_ultmod:fecha,
                            estado_publi: 'Detenido'
                        },
                        /*cuenta : {
                            nombre:this.empresa, 
                            nit:this.nit.replace('-', ''), 
                            direccion:'null',
                            password:this.nit.replace('-', ''), 
                            telefono:this.telefono, 
                            correo: this.correo,
                            fecha_create: new Date()
                        }*/
                    }

                   api.saveCompraPublicidadMarca(data).then((res) => {
                        veryemail.sendEmailCompraPublicidadMarca(e.target)
                        console.log(res);
                        this.clearVariables();
                        this.dialog = false;
                    })
                }, 3000)
            } else {
                this.camVacio = true;
                //subir TOp
                window.scrollTo(0, -10);
                window.setTimeout(() => {
                    this.camVacio = false;
                }, 3000)
                console.log("CAMPOS VACIOS")
            }

            /*
*/

        },

        clearVariables: function () {

            this.itemPlan = {};
            this.gestion = null;
            this.noBoleta = null;
            this.cantDeposito = null;
            this.banco = '';
            this.representante = '';
            this.nit = '';
            this.telefono = '';
            this.correo = '';
            this.urlB = '';
            this.empresa = null;
            this.descripcion = null;
            this.linkFacebook = null;
            this.linkInstagram = null;
            this.linkWhatsapp = null;
            this.linkPagina = null;
            this.photoPubli = '';

            this.dialogSuccess = true;
        },

        sumarDias: function (fecha) {
            var dias = 0;
            if (this.itemPlan.id == 1) {
                dias = 30;

            } else if (this.itemPlan.id == 2) {
                dias = 90
            } else if (this.itemPlan.id == 3) {
                dias = 365
            }
            fecha.setDate(fecha.getDate() + dias);
            return fecha.toISOString();
        },


    },

    created() {
        this.getPlanesMarcas();
        this.color='#3ed219';  
        
        //            this.$router.go(0);

    }
}
</script>

<style>
.cajon {
    background: ghostwhite;
    height: 100%;
}

.container-cajon {
    background: white;
    border-radius: 5px;

}



.container-img-publicidad {
    width: 100%;

    align-items: center;
    display: flex;
    justify-content: center;
    border: 2px gainsboro solid;
    border-radius: 10px;

}

.img-publicidad {}

img {}


@media screen and (min-width: 550px) {

    .container {
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .container-cajon {
        width: 500px;
    }

}

.picker-color {
    width: 100%;
}

.picker-cajon {
    align-items: center;
    display: flex;
    justify-content: center;
}

.submit {
    float: right;
    margin-bottom: 14px !important;
    padding: 8px;
    width: 150px;
    font-size: 14pt;
    color: white;
    background: #04790d;
    border-radius: 40px;
    width: 100%;
}


.parpadea {

    animation-name: parpadeo;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;

    -webkit-animation-name: parpadeo;
    -webkit-animation-duration: 1s;
    -webkit-animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
}

@-moz-keyframes parpadeo {
    0% {
        opacity: 1.0;
    }

    50% {
        opacity: 0.0;
    }

    100% {
        opacity: 1.0;
    }
}

@-webkit-keyframes parpadeo {
    0% {
        opacity: 1.0;
    }

    50% {
        opacity: 0.0;
    }

    100% {
        opacity: 1.0;
    }
}

@keyframes parpadeo {
    0% {
        opacity: 1.0;
    }

    50% {
        opacity: 0.0;
    }

    100% {
        opacity: 1.0;
    }
}
</style>