<template>
    <div style="background: white;">
        <v-dialog v-model="show" fullscreen hide-overlay transition="dialog-bottom-transition">
            <v-card class="mx-auto">
                <v-toolbar dark color="green">
                    <v-btn icon dark @click="show = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>Producto No.{{ idpro }} - Plan {{ titlePlan }}</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-toolbar-items> </v-toolbar-items>
                </v-toolbar>
                <v-container class="mx-auto">
                    <div class="cajon-contenedor">
                        <h5>Completar compra</h5>
                        <!--<span class="mb-3" style="font-size: 10pt">CUENTA A DEPOSITAR #......... DEL BANCO PROMERICA
                    </span> -->
                        <br>
                        <p>Realiza tu pago en el siguiente enlace :</p>
                            <v-btn block color="blue" style="
          color: #fff;
          
          box-shadow: 1px 1px 6px 2px rgba(255, 251, 3, 0.7);" class="parpadea btn-warning mb-4" dark><a
                                    style="color:white !important" target="_blank"
                                    href="https://mallvirtualvisanet.com.gt/formulario-de-pago/1556/pliis-technology-businesses">
                                    <v-icon>mdi-credit-card-outline</v-icon>Link de PAGO</a></v-btn>
                        <h5 class="mb-3">Llena los siguientes campos</h5>

                        <v-form ref="form" class="mt-2" v-model="form" @submit.prevent="createVenta">
                          <v-text-field  v-model="nBoleta" dense filled rounded label="No. de boleta"
                                        required></v-text-field>
                        
                            <div class="mx-auto-photo">
                                <photo class="interno" v-on:photoPago="onPhotos"></photo>
                            </div>
                            <p>Carga tu boleta o comprobante de pago*</p>
                            <hr>
                            <div style="background: gainsboro; padding: 10px; border-radius: 5px;">
                                <v-row no-gutters>
                                    <v-col cols="12"><b>DETALLE DE LA COMPRA</b></v-col>
                                    <v-col cols="8">
                                        Plan {{ titlePlan }} - Depsito
                                    </v-col> <v-col cols="4" class="text-right">
                                        Q.{{ getCashBank(price) }}
                                    </v-col>
                                    <v-col cols="8">
                                        Monedas
                                    </v-col> <v-col cols="4" class="text-right">
                                        Q.{{ getMoneyCash(monedas) }}
                                    </v-col>
                                    <v-col cols="8">
                                        <b> TOTAL</b>
                                    </v-col> <v-col cols="4" class="text-right">
                                        <b> Q.{{ getPrice() }}</b>
                                    </v-col>
                                </v-row>
                            </div>

                            <v-text-field style="display: none;" name="to_name" type="text" v-model="user.nombres"
                                outlined></v-text-field>
                                <v-text-field name="message" style="display: none;" dense v-model="nGestion" outlined disabled
                                      ></v-text-field>
                            <v-text-field style="display: none;"  name="from_email" type="text" v-model="user.email"
                                outlined></v-text-field>

                            <input value="Confirmar Envio" :disabled="!form" class="submit" type="submit" name="send" />
                        </v-form>
                        <br>
                    </div>
                </v-container>
            </v-card>
        </v-dialog>
        <v-dialog v-if="idpro" v-model="success" persistent>
            <v-card class="mx-auto" style="text-align: center;">
                <v-icon class="mt-6" size="120" color="success">mdi-check-circle</v-icon>
                <v-card-text>Tu compra ha sido registrada correctamente.</v-card-text>
                <p class="text-h5">Fecha lmite </p>
                <p><b>{{ sumarDias(new Date()) }}</b></p>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="salir()">
                        Salir
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="progressBoleta" hide-overlay persistent width="300">
            <v-card color="success" dark>
                <v-card-text>
                    Espera un momento...
                    <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import store from '../../store'
import veryemail from "../../models/email"
//import enviarBoleta from "@/components/EnviarBoleta.vue";
import api from '../../models/controller' //../../views/PerfilEdit/CreatePhoto.vue
import photo from "../../views/Photo/SubirPhoto.vue";
export default {
    components: {/* enviarBoleta*/  photo },
    props: {
        value: Boolean,
        idpro: null,
        titlePlan: null,
        idPlan: null,
        price: null,
        monedas: null,
 
    },
    data() {
        return {
            isges: false,
            progressBoleta: false,
            success: false,
            money: 0,
            planes: [],
            nGestion: null,
            nBoleta: null,
            form: false,
            photo: 'vacio',
            nTelefono: null,
            user:{},
            nameRules: [
                (v) => !!v || "Campo requerido",
                (v) => (v && v.length <= 20) || "Puedes escribir hasta 20 caracteres",
            ],
            rules: {
                length: len => v => (v || '') >= 25 || `Longitud de carcter no vlida, obligatorio ${len}`,
                required: value => !!value || 'Campo requerido',
            },
        }
    },
    computed: {
        show: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit("input", value);
            },
        },
    },
    methods: {
        salir() {
            this.show = false;
            this.success = false;

        },

        getCashBank(price) {
            return Math.round((price - this.getMoneyCash(this.monedas)) * 100) / 100
        },
        getMoneyCash(monedas) {
            if (monedas < 2000) return 50
            else if (monedas < 3000) return 100
            else if (monedas < 5000) return 150
            return 0
        },

        getMoneyDebitar(monedas) {
            if (monedas < 2000) return 1000
            else if (monedas < 3000) return 2000
            else if (monedas < 5000) return 3000
            return 0
        },
        getPrice() {
            return Math.round((this.getCashBank(this.price) + this.getMoneyCash(this.monedas)) * 100) / 100
        },
        onPhotos(item) {
            this.photo = item;
        },
        getMoney: function (id) {
            api.getMoney(id).then((result) => {
                var money = result.data;
                this.money = money.money;
            });
        },
        getPlanes: function () {
            api.getAllPlanes().then((datos) => {
                var planes = datos.data;
                planes.forEach(element => {
                    if (element.id_plan != 5 && element.id_plan != 4) {
                        this.planes.push(element);
                    }
                });
            });
        },
        sumarDias: function (fecha) {
            var dias = 0;
            if (this.idPlan == 1) {
                dias = 30;

            } else if (this.idPlan == 2) {
                dias = 15
            } else if (this.idPlan == 3) {
                dias = 7
            }
            fecha.setDate(fecha.getDate() + dias);
            return fecha.toISOString();
        },
        createVenta(e) {
            if (this.$refs.form.validate()) {
            this.progressBoleta = true;
            var dias = 0;
            if (this.idPlan == 1) {
                dias = 30;

            } else if (this.idPlan == 2) {
                dias = 15
            } else if (this.idPlan == 3) {
                dias = 7
            }

            if (this.photo != 'vacio') {

                var fecha = new Date();

                let params = {
                    idCliente: this.user.id_user,
                    idUser: 2147483647,
                    tipo_comprobante: 'Ingresos',
                    num_gestion: this.nGestion,
                    num_comprobante: this.nBoleta,
                    total: this.getPrice(),
                    idPlan: this.idPlan,
                    idPro: this.idpro,
                    cantidad: 1,
                    precio: this.getCashBank(this.price),
                    descuento: this.getMoneyCash(this.monedas),
                    foto: this.photo.img,
                    idfoto: this.photo.id,
                    dias: dias,
                    fecha: fecha.toISOString()
                }
                api.createVentaPubli(params).then(() => {

                    veryemail.sendEmailCompraPublicidadProductos(e.target)
                    this.progressBoleta = false;
                    let debitar = {
                        total: this.monedas - this.getMoneyDebitar(this.monedas),
                        id_user: this.user.id_user,
                        fichas: this.getMoneyDebitar(this.monedas),
                        fichas_detalle: "- Por compra de plan publicitario: Plan " + this.titlePlan,
                    }
                    api.debitarFichasUser(debitar).then(() => {
                        this.success = true;
                        this.nGestion = null;
                        this.nBoleta = null;
                        this.photo = 'vacio';
                        this.getGestion();
                    })
                })
            }
        }
        },
        
        getGestion: function(){
            var pass = "";
            var str = "1234567890987654321";
            for (let i = 1; i <= 8; i++) {
                var char = Math.floor(Math.random() * str.length + 1);
                pass += str.charAt(char);
            }
            this.nGestion = pass;
        }
    },
    created() {
        if (store.getters.isLoggedIn) {
            var data = store.getters.getUser;
            var pass = "";
            var str = "1234567890987654321";
            for (let i = 1; i <= 8; i++) {
                var char = Math.floor(Math.random() * str.length + 1);
                pass += str.charAt(char);
            }
            this.nGestion = pass;
            this.isLogin = true;
            this.user = data;
            this.getMoney(this.user.id_user);

        }
    }
}
</script>

<style scoped>
.mx-auto-photo {
    width: 100% !important;
    padding: 10px;
    text-align: center;
}

.interno {
    width: 60%;
    margin: 0 auto;
    /*esto fue lo que se agreg*/
}

.submit {
    color: white;
    width: 100%;
    margin-top: 15px;
    background: green;
    padding: 10px;
    border-radius: 20px;
    font-size: 14pt;
}



@media screen and (min-width: 550px) {
    .cajon-contenedor{
padding-left: 350px;
padding-right: 350px;
}
}
</style>