<template>
  <div style="height: 100%;">
    <div class="container-fichas" style="height: 100%;">
      <div class="fichas-activas">
        <div class="contenido-fichas">
          <v-list-item> <v-img class="ml-8" max-width="95" src="../../../../assets/moneda.png" alt="John"
              style="object-fit: cover"></v-img>
            <p class="cantidad">{{ money.fichas }}</p>
          </v-list-item>
          <h2>

          </h2>
          <hr />
          <h3 class="text-fichas">MONEDAS DISPONIBLES</h3>
        </div>
        <v-menu class="ml-6" bottom right transition="slide-y-transition">
          <template v-slot:activator="{ on, attrs }">
            <v-btn icon v-bind="attrs" v-on="on">
              <v-icon size="30">mdi-dots-vertical</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-item link v-for="(item, i) in items" :key="i" @click="navegateItem(item.title, item.tipo)">
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>

      <div></div>

      <div class="contenido-canje">
        <br />
        <h2 style="color: yellow; font-weight: bolder">CMO GANAR MONEDAS?</h2>

        <p class="texto-indicaciones-reclamo">
          Anuncia tu producto o vende desde nuestra plataforma.
        </p>

        <hr />
        <p class="texto-indicaciones-reclamo">
          Para poder canjear recuerda que debes tener un mnimo de 2,000 monedas.
        </p>
        <br />
        <v-btn @click="canjear()" block rounded large color="white" style="color: green; font-weight: bolder">CANJEAR MONEDAS</v-btn>
      </div>

    </div>
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-toolbar dark color="green">
        <v-toolbar-title>{{ title }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-card class="grey lighten-5">
        <historial v-if="cajon == 'historial'" />
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import store from '../../../../store'
import api from '../../../../models/controller'
import historial from './FichasHistorial.vue'
export default {
  components: { historial },
  data() {
    return {
      cajon: '',
      title: '',
      dialog: false,
      money: 0,
      items: [/*{ title: "Canjeadas", action: '', tipo: 'canjeos' },*/ { title: "Historial", action: '', tipo: 'historial' }],
    };
  },

  methods: {

    canjear: function(){
      this.$router.push("/mis-productos").catch(() => {});
    },
    navegateItem(title, tipo) {
      this.cajon = tipo;
      this.title = title;
      this.dialog = true;
    },
    getMoney: function (id) {
      api.getMoney(id).then((result) => {
        var money = result.data;
        this.money = money.money;
      });
    },
  },
  created() {
    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
      this.isLogin = true;
      this.getMoney(data.id_user);
    }
  }

};
</script>

<style  scoped>
.container-fichas {
  background: rgb(4, 177, 4);

}

.fichas-activas {
  background: white;
  height: 275px;
  width: 100%;
  box-shadow: inset 0 -13px 0 0 rgb(129, 236, 136);
  /* box-shadow: inset 0 -13px 0 0  rgb(129, 236, 136);*/
  border-end-start-radius: 50% 10%;
  border-end-end-radius: 40% 9%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.contenido-fichas {
  text-align: center;
}

.cantidad {
  font-size: clamp(3rem, 12vw, 6rem);
  font-weight: bolder;
  margin-top: 23px;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.text-fichas {
  font-size: clamp(1.5rem, 3vw, 3rem);
  font-weight: bolder;
}

.ficha_adorno1 {
  position: absolute;
  top: 20px;
  height: 50px;
  z-index: 5px;
}

.contenido-canje {
  padding: 40px;
  width: 100%;
  height: 100%;
  text-align: center;
}

.texto-indicaciones-reclamo {
  font-weight: bolder;
  font-size: 16pt;
  color: white;
}
</style>