<template>
  <div class="response-div">


    <v-container>
      <v-row>
        <v-col cols="12" md="6">
          <v-select v-model="estado" :items="estados" label="Buscar por Estado" dense solo append-inner-icon="mdi-map"
            @change="filterEstado"></v-select>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field v-model="gestion" @click:append="getGestion()" dense solo label="No. Gestin"
            placeholder="Ingrese" append-icon="mdi-magnify"></v-text-field>
        </v-col>
      </v-row>
      <v-row no-gutters>
        <v-col cols="12" v-if="pagos.length == 0" class="mt-2 text-center">
          <p>No se encontraron resultados</p>
          <v-skeleton-loader v-for="item in [0, 1]" :key="item" class="mx-auto mt-2" max-width="400"
            type="card"></v-skeleton-loader>

        </v-col>
        <v-col v-else cols="12" v-for="(item, i) in pagos" :key="i" :class="i == 0 ? 'mt-0' : 'mt-6'">
          <v-card class="text-center py-0 px-0 " elevation="5" style="border-top: 15px solid green;">
            <div class="mx-3 py-2">
              <p class="title font-weight-black">Plan {{ item.nombre_plan }}</p>
              <p style="margin-bottom: 0px; margin-top: -15px;">Titular</p>
              <p style="margin-bottom: 0px;" class="caption font-weight-bold">{{ item.titular }}</p>
              <p class="title font-weight-bold">{{ item.num_gestion }}</p>

              <v-divider></v-divider>
              <div>
                <v-row no-gutters>
                  <v-col style="text-align: left; margin-bottom: -17px;" cols="4">
                    <p class="subtitle color-text font-weight-black">Servicio</p>
                  </v-col>
                  <v-col style="text-align: right; margin-bottom: -20px;" cols="8">
                    <p class="caption font-weight-black">Publicidad</p>
                  </v-col>
                  <v-col style="text-align: left; margin-bottom: -10px;" cols="5">
                    <p class="subtitle color-text font-weight-black">Descripcin</p>
                  </v-col>
                  <v-col cols="7" style="margin-bottom: -10px;">
                    <p class="title font-weight-black caption">Vigencia de {{ item.dias }} dias de publicidad.</p>
                  </v-col>
                  <v-divider></v-divider>
                </v-row>
              </div>


              <div>
                <v-row no-gutters>
                  <v-col style="text-align: left; margin-bottom: -20px;" cols="4">
                    <p class="subtitle color-text font-weight-black">Monto</p>
                  </v-col>
                  <v-col style="text-align: right; margin-bottom: -30px;" cols="8">
                    <p class="caption font-weight-black">Q.{{ item.precio }}</p>
                  </v-col>
                  <v-col style="text-align: left; margin-bottom: -20px;" cols="4">
                    <p class="subtitle color-text font-weight-black">Descuento</p>
                  </v-col>
                  <v-col style="text-align: right; margin-bottom: -30px;" cols="8">
                    <p class="caption font-weight-black">Q.{{ item.descuento }}</p>
                  </v-col>

                  <v-col style="text-align: left; margin-bottom: -10px;" cols="5">
                    <p class="subtitle color-text font-weight-black">Monto Total</p>
                  </v-col>
                  <v-col cols="7" style="text-align: right ;margin-bottom: -10px;">
                    <p class="caption font-weight-black">Q.{{ item.precio + item.descuento }}</p>
                  </v-col>

                  <v-divider></v-divider>
                </v-row>
              </div>
              <div>
                <v-row no-gutters>
                  <v-col style="text-align: left; margin-bottom: -20px;" cols="4">
                    <p class="subtitle color-text font-weight-black">Fecha</p>
                  </v-col>
                  <v-col style="text-align: right; margin-bottom: -30px;" cols="8">
                    <p class="caption font-weight-black">{{ item.fecha }}</p>
                  </v-col>
                  <v-col style="text-align: left; margin-bottom: -30px;" cols="4">
                    <p class="subtitle color-text font-weight-black">Estado</p>
                  </v-col>
                  <v-col style="text-align: right; margin-bottom: -30px;" cols="8">
                    <p class="caption font-weight-black">{{ item.estado }}</p>
                  </v-col>
                </v-row>
              </div>
            </div>
            <v-card-actions class="mt-4  px-0 py-0">

              <v-btn @click="verDetalle(item)" block full text class="rounded-0" color="blue"> <v-spacer></v-spacer>
                Ver Detalles <v-spacer></v-spacer> <v-icon end>mdi-chevron-right</v-icon>
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
//import axios from "axios";
import store from "../../store"
import api from "../../models/controller";
import moment from "moment";
export default {
  data() {
    return {
      headerProps: {
        sortByText: "Buscar por:"
      },
      show3: false,
      loading: true,
      gestion: '',
      page: 1,
      pageCount: 0,
      itemsPerPage: 2,
      id_user: null,
      pagos: [],
      estado: null,
      estados: ["Todos", "Pendiente", "Procesando", "Finalizado"],
      headers: [
        {
          text: "No. Gestin",
          align: "start",
          sortable: false,
          value: "gestion",
        },
        { text: "Plan", value: "nombre_plan" },
        { text: "Precio Q.", value: "precio" },
        { text: "Fecha", value: "fecha" },
        { text: "Estado", value: "estado" },
      ],
    };
  },
  methods: {
    getColor(estado) {
      if (estado == "Validando") return "red";
      else if (estado == "Procesando") return "orange";
      else return "green";
    },

    verDetalle(item){
      this.$router.push(`/detalle-compra-publicidad/${item.idVenta}`).catch(() => {});
    },

    filterEstado(e) {
      this.loading = true;
      this.pagos = [];
      let estado = e;
      let busqueda = null;
      if (e != "Todos") {
        busqueda = `/${estado}/${this.id_user}`;
      } else {
        busqueda = `/${this.id_user}`;
      }
      window.setTimeout(() => {
        api.getPagosFilter(busqueda).then((result) => {
          let resultados = result.data;
          resultados.forEach((element) => {
            this.pagos.push({
              descuento: element.descuento,
              estado: element.estado,
              fecha: moment(element.fecha, "YYYY-MM-DD").format("DD/MM/YYYY"),
              idVenta: element.idVenta,
              id_user: element.id_user,
              descripcion: element.descripcion,
              dias: element.dias,
              no_comprobante: element.no_comprobante,
              nombre_plan: element.nombre_plan,
              num_gestion: element.num_gestion,
              precio: element.precio,
              titular: element.titular,
            });
          });
          this.loading = false;
        }, 3000)
      })
    },

    getGestion() {
      if (this.gestion != '') {
        this.pagos = [];
        api.getPagos_gestion(this.id_user, this.gestion).then((result) => {

          if (result.status == 200) {
          console.log(result)
          let resultados = result.data;
          resultados.forEach((element) => {
            this.pagos.push({
              descuento: element.descuento,
              estado: element.estado,
              fecha: moment(element.fecha, "YYYY-MM-DD").format("DD/MM/YYYY"),
              idVenta: element.idVenta,
              id_user: element.id_user,
              descripcion: element.descripcion,
              dias: element.dias,
              no_comprobante: element.no_comprobante,
              nombre_plan: element.nombre_plan,
              num_gestion: element.num_gestion,
              precio: element.precio,
              titular: element.titular,
            });
            this.loading = false;
          });
        }

        })
      }
    }
  },
  created() {

    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;

      const idUser = data;
      this.id_user = data.id_user;
      try {
        window.setTimeout(() => {
          this.pagos = [];
          api.getPagos(idUser.id_user).then((result) => {
            let resultados = result.data;
            resultados.forEach((element) => {
              this.pagos.push({
                descuento: element.descuento,
                estado: element.estado,
                fecha: moment(element.fecha, "YYYY-MM-DD").format("DD/MM/YYYY"),
                idVenta: element.idVenta,
                id_user: element.id_user,
                descripcion: element.descripcion,
                dias: element.dias,
                no_comprobante: element.no_comprobante,
                nombre_plan: element.nombre_plan,
                num_gestion: element.num_gestion,
                precio: element.precio,
                titular: element.titular,
              });
              this.loading = false;
            });
          });
        }, 3000)
      } catch (error) {
        console.log(error);
      }

    } else {
      console.log("Hay que salir de ac");
    }
  },
};
</script>

<style scoped>


.color-text {
  color: rgb(0, 68, 0);
  font-weight: bolder;
}

@media only screen and (min-width: 600px) {
  .response-div {
    padding-left: 30%;
    padding-right: 30%;
  }
}
</style>