<template>
    <v-carousel cycle height="58" :show-arrows="false" delimiter-icon="mdi-minus" hide-delimiter-background hide-delimiters>
        <v-carousel-item v-for="(slide, i) in publicidad" :key="i">
            <v-sheet color="rgba(82, 240, 82,0.73)" height="100%" dark>
                <div class="pl-1 contenido">
                    <div class="cajon-img">
                        <div class="img-style">
                            <img style=" border-radius: 10px; width: 100%; height: 100%; object-fit: cover; object-position: center; background-size: cover;
                 "  :src="slide.url" alt="">
                        </div>
                    </div>

                    <p class="title" style="font-size: 16pt; margin-bottom: 0px; margin-left: 55px;">{{ slide.empresa }}</p>
                    <p class="subtitle" style=" margin-left: 55px; margin-top: -5px;">{{ getLimite(slide.descripcion) }} </p>
                   <!-- <v-btn icon class="bt-icon"> <v-icon color="black" size="30"> mdi-web</v-icon></v-btn>-->
                </div>
            </v-sheet>
        </v-carousel-item>
    </v-carousel>
</template>
<script>
import api from "@/models/controller";
import timePublicidad from "../../events/timePublicidadMarca"
export default {
    data() {
        return {

            publicidad: [],

        }
    },

    methods: {
        getLimite(item) {
            var text = '';

            if (item.length >= 40) {
                let total = item.length - 40
                text = item.slice(0, -total) + '...'
            } else {
                text = item
            }

            return text

        },

        allPublicidad() {
            api.allPublicidadPagada().then((res) => {

                var lista = res.data;
              
                timePublicidad(lista.result).then((resp)=>{
                    this.publicidad =resp;
                })
                
            })
        }
    },

    created() {
        this.allPublicidad();
    }
}
</script>

<style>
.contenido {position: relative;color: black}

.bt-icon {position: absolute;top: 5px;right: 3px;}

.cajon-img {position: absolute;left: 2px;top: 4px;width: 50px !important;height: 50px !important;border-radius: 10px;}

.img-style {width: 50px !important;height: 50px !important;}

.img-slide {width: 100%;height: 100%;object-fit: cover;}

.title{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}

.subtitle{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}
</style>