<template>
    <div class="px-1 mb-1" v-if="products.length > 0">
        <h4 class=" mt-4" style="margin-bottom: -10px;">Vistos Recientemente</h4>
        <v-sheet elevation="0" max-height="300">
            <!--AGREGA SIG. ANTE.    show-arrows-->
            <v-slide-group v-model="model" center-active >
                <v-slide-item style="margin-left:2px !important; margin-right: 2px !important;" v-for="item in products"
                    :key="item.id_pro">
                    <v-card class="ma-4" height="100" width="100">
                        <v-img @click="verDetalle(item.id_pro)" style="border-radius: 2px" class="mb-1" v-bind:src="item.thumbnail" height="100px">
                            <v-app-bar style="widht: 100%" flat color="rgba(0, 0, 0, 0)">
                                <v-spacer></v-spacer>

                            </v-app-bar>
                            <div style="max-width: 370px; font-size: 12pt" class="mb-2 margin-title-view text-truncate">
                                {{ item.titulo }}
                            </div>
                        </v-img>


                    </v-card>
                </v-slide-item>
            </v-slide-group>
        </v-sheet>
    </div>
</template>

<script>
import api from "@/models/controller";
import store from "../../store"
export default {
    components: {},
    props: {},

    data() {
        return {
            products: [],
            model: null,
        }
    },

    methods: {
        getProductsRecientsViews(iduser) {
            api.getProductRecientsViews(iduser).then((result) => {
                var data = result.data;
                this.products = data.result;
                this.products = this.products .reverse();

            })
        },
        verDetalle(id) {
            this.$router.push({
        path: `/detalleProducto/${id}`,
        id_pro: { id },
      });
        }
    },

    created() {

        if (store.getters.isLoggedIn) {
            var data = store.getters.getUser;
            this.getProductsRecientsViews(data.id_user);
        }
    }
}
</script>

<style>
.margin-title-view{
    padding-left: 5px;
  margin-top: -5px;
  margin-bottom: 8px;
    width: 100%;
    font-size: 12px !important;
    position: absolute;
    bottom: -9.5px;
    background: white;
    color:black
}
</style>