<template>
    <v-container class="text-center">
        <v-icon color="red" size="150">mdi-account-multiple-remove</v-icon>
        <p class="font-weight-bold subtitle-1">ELIMINAR TU CUENTA DE VENDELO YA</p>
        <p>Puedes eliminar permanentemente tu cuenta de Vendelo Ya y todos tus productos con los planes incluidos. </p>
        <v-btn outlined large class="mt-5" block @click="dialogDelete=true" rounded style="color:red"><v-icon>mdi-delete</v-icon>
            Eliminar cuenta</v-btn>

        <v-dialog v-model="dialog" hide-overlay persistent width="300">
            <v-card color="red" dark>
                <v-card-text class="pt-2">
                    Estamos eliminando la cuenta...
                    <v-progress-linear indeterminate color="white" class="mb-0 mt-1"></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>


        <v-dialog
      v-model=" dialogDelete"
      max-width="320" style="text-align: center !important;"
    >
      <v-card class="text-center pt-4">
        <p class="text-h6 text-center mt-5">
         ELIMINAR CUENTA
        </p>

        <v-card-text>
       Ests seguro de eliminar tu cuenta? </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            text
            @click=" dialogDelete = false"
          >
            Cancelar
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            @click="deleteUser()"
          >
            Eliminar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </v-container>
</template>
<script>
import api from '../../../models/controller'
import store from '../../../store'

export default {
    props: { id: null },
    data() {
        return {
            dialog: false,
            dialogDelete: false,
        }
    },
    methods: {
        deleteUser: function() {
            this.dialog = true;
            window.setTimeout(() => {
                try {
                    api.deleteUser(this.id).then(() => {
                        this.dialog = false;
                        store.dispatch('logout');
                        window.localStorage.removeItem("user");
                        this.$router.replace("/");
                    })
                } catch (error) {
                    console.log(error)
                }
            }, 4000)

        }
    },
    watch: {
        dialog(val) {
            if (!val) return

            setTimeout(() => (this.dialog = false), 4000)
        },
    },
    created() {
        console.log("idCliente :", this.id)
    },
}
</script>

<style scoped>
#cajon1 {
    float: left;
    background: #CC6600;
}

#cajon2 {
    float: left;
    background: #000FFF;

}

.content {

    width: 100%;
}

.right {
    height: 70px;
    width: 69%;
}

.left {
    height: 70px;
    width: 25%;
}
</style>