<template>
  <v-dialog
    v-model="show"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <v-card>
      <v-toolbar flat dark color="#0eb308" class="card-toolbar"
        ><v-toolbar-title
          style="font-size:1.6rem; text-center; font-weight:bolder"
          >Datos del Vendedors</v-toolbar-title
        >
        <v-spacer></v-spacer>
        <v-icon @click="show = false">mdi-close</v-icon></v-toolbar
      >
      <v-img :src="vendedor.photo" height="500px" dark>
        <v-row class="fill-height text-center">
          <v-spacer></v-spacer>
        </v-row>
      </v-img>
      <v-card-title class="pl-12 black--text">
        <div class="text-h5 font-weight-bolder text-center">
          {{ vendedor.Nombre_Completo }}
        </div>
      </v-card-title>
      <span class="pl-12">Vendedor</span>
      <v-list two-line>
        <v-list-item>
          <v-list-item-icon>
            <v-icon color="indigo"> mdi-phone </v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>(502) {{ vendedor.telefono }}</v-list-item-title>
            <v-list-item-subtitle>Celular</v-list-item-subtitle>
          </v-list-item-content>

          <v-list-item-icon @click="irChat(vendedor.id_user)">
            <v-icon large color="success">mdi-wahtsapp</v-icon>
          </v-list-item-icon>
        </v-list-item>

        <v-divider inset></v-divider>

        <v-list-item>
          <v-list-item-icon>
            <v-icon color="indigo"> mdi-email </v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ vendedor.email }}</v-list-item-title>
            <v-list-item-subtitle>Correo Personal</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-divider inset></v-divider>

        <v-list-item>
          <v-list-item-icon>
            <v-icon color="indigo"> mdi-map-marker </v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Direccin</v-list-item-title>
            <v-list-item-subtitle>Municipio, Departamento</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-card>
  </v-dialog>
</template>

<script>
import axios from "axios";
import { mdiChat } from "@mdi/js";
export default {
  props: {
    value: Boolean,
    id: null,
  },
  data() {
    return {
      vendedor: {},
      title: "",
      icons: {
        mdiChat,
      },
    };
  },

  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
  methods: {
    irChat: function (id) {
      this.$router.push(`/chat/${id}`).catch(() => {});
    },
  },

  created() {
    if (localStorage.getItem("user")) {
      axios
        .get(
          `https://vendeloyaglobal.herokuapp.com/api/contacto_vendedor/${this.id}`
        )
        .then((result) => {
          console.log("Datos del Vendedor: ", result);
          let vendedor = result.data[0];
          this.vendedor = vendedor;
          console.log("PRODUCTOS : ", this.vendedor);
        });
    } else {
      console.log("NO AUTORIZADO");
    }
  },
};
</script>