<template>
  <v-dialog
    v-model="show"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <v-app app>
      <v-app-bar app color="success" dark>
        <v-btn icon @click="ircerrar(null)">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        <v-avatar class="mr-2" color="warning" size="40">
            <img
                    v-if="user.photo"
                    :alt="user.nombres"
                    :src="user.photo"
                  >
        </v-avatar>
        <v-toolbar-title class="text-h6">{{user.nombres}} {{user.apellidos}}</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-btn icon>
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </v-app-bar>
      <v-main>
        <div>
          <bodyChat :id_user="id_user" />
        </div>
      </v-main>
      <footerChat v-on:result="onId" :id_user="id_user" />
    </v-app>
  </v-dialog>
</template>

<script>
//import headerChat from "@/components/Chat/HeaderCom.vue";

import api from "../../models/controller";
import bodyChat from "@/components/ChatBot/BodyChat.vue";
import footerChat from "@/components/ChatBot/FooterChat.vue";

export default {
  components: { /*headerChat,*/ bodyChat, footerChat },
  props: { value: Boolean, id_user: null, photo: null, nombre: null },

  data() {
    return {
      dialog: false,
      user: {},
    };
  },
  methods: {
    ircerrar: function (e) {
      this.$emit("DialogChat", e);
    },

    onId(id) {
      api.getUser(id).then((result) => {
        this.user = result.data[0];
        console.log("dato del usuario BOT : ", this.user);
      });
    },
  },
  created() {
    this.$watch(
      () => this.id_user,
      (toParams) => {
        this.id_user = toParams;
      }
    );
  },
  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
};
</script>