<template>
    <div>
        <v-app-bar style="background: #0eb308 !important" dark app>

            <v-btn icon large @click="regresar()"><v-icon color="white" size="40">mdi-arrow-left-circle</v-icon></v-btn>
        </v-app-bar>
        <div class="foto-portada">

            <v-carousel hide-delimiter-background hide-delimiters :show-arrows="false" height="400"
                delimiter-icon="mdi-minus">
                <v-carousel-item class="presentacion" style="background-position: center;
        background-size: cover;
        background-repeat:no-repeat ;
        max-width: 100%;" src="../../assets/fondos/servicios.jpg" reverse-transition="fade-transition"
                    transition="fade-transition">
                    <v-app-bar flat color="rgba(0, 0, 0, 0)">


                    </v-app-bar>


                    <span class="servicios">Nuestros Servicios</span>

                </v-carousel-item>
            </v-carousel>
        </div>

        <div>

            <v-row>
                <v-col cols="12" md="3">
                    <div class="publicidad">

                        <div style="margin: 0 auto;">

                            <v-row>
                                <v-col cols="6" md="6">
                                    <p class="ml-2">Publicidad pagada</p>
                                </v-col>
                                <v-col cols="6" md="6"><v-btn @click="comprarEspacio()" block dark color="orange"
                                        class=" mb-2 btn-compra animated flash" rounded>Anunciarme</v-btn></v-col>
                            </v-row>


                        </div>

                        <publicidad></publicidad>
                    </div>
                </v-col>
                <v-col cols="12" md="9">



                    <div class="px-3">

                        <v-expansion-panels elevation="5" v-model="item" @change="activeForm($event)">
                            <v-expansion-panel>
                                <v-expansion-panel-header>
                                    Quires ser Invesionista?
                                </v-expansion-panel-header>
                                <v-expansion-panel-content>
                                    <div class="css-socios">
                                        <socios v-if="tipo == 'Inversion'" :tipo="tipo"></socios>
                                    </div>

                                </v-expansion-panel-content>
                            </v-expansion-panel>
                            <v-expansion-panel>
                                <v-expansion-panel-header>
                                    Deseas una Alianza Estrategica
                                </v-expansion-panel-header>
                                <v-expansion-panel-content>
                                    <div class="css-socios">
                                        <socios v-if="tipo == 'Socio'" :tipo="tipo"></socios>
                                    </div>

                                </v-expansion-panel-content>
                            </v-expansion-panel>
                        </v-expansion-panels>
                        <br>
                        <br>
                    </div>
                </v-col>
            </v-row>
        </div>





    </div>
</template>

<script>
import socios from '@/components/Nosotros/FormSocios.vue'
import publicidad from '@/components/PublicidadPagada/VistaPrevia.vue'
export default {
    components: { socios, publicidad },
    data() {
        return {
            item: null,
            tipo: null
        }
    },
    methods: {
        comprarEspacio: function () {
            //this.$router.go("/pago-publicidad-marcas");
            window.open("/pago-publicidad-marcas", 'Publicidad')
        },
        activeForm: function (e) {
            if (e == 0) {
                this.tipo = 'Inversion'
            } else if (e == 1) {
                this.tipo = 'Socio'
            } else {
                this.tipo = null;
            }
        },
        regresar: function () {
            window.history.back();
        }
    }

}
</script>
<style>
.foto-portada {}

.title {
    font-size: 18pt !important;
}

@media screen and (min-width: 550px) {

    .publicidad {
        width: 100%;

        margin: 0 auto;
        text-align: center !important;

    }

    .css-socios {
        background: ghostwhite;
        padding-top: 10px;
        padding-bottom: 20px;
        padding-left: 210px;
        padding-right: 210px;
    }
}


.btn-compra {

    box-shadow: 0px 2px 2px 1px rgba(140, 247, 98, 0.6);
}


.animated {

    -webkit-animation-duration: 3s;
    animation-duration: 3s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
}

@-webkit-keyframes flash {

    0%,
    50%,
    100% {
        opacity: 1;
    }

    25%,
    75% {
        opacity: 0;
    }
}

@keyframes flash {

    0%,
    50%,
    100% {
        opacity: 1;
    }

    25%,
    75% {
        opacity: 0;
    }
}

.flash {
    -webkit-animation-name: flash;
    animation-name: flash;
}


.presentacion{
position: relative;
}

.presentacion img{
max-width: 100%;
height: auto;
opacity: .88;
}

.presentacion .servicios{
    text-align: center;
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
font-size: 5vh;
color:white;
}
</style>