<template>
    <div>
        <v-list subheader>
            <v-list-item>
                <h4 style="margin-top:-25px; margin-left:-9px !important" class="ml-0 font-weight-bold">Seguidores</h4>
                <v-list-item-content>
                </v-list-item-content>
                <v-list-item-icon style="margin-top:-3px;" class="font-weight-bold">
                    {{ followers.length }}
                </v-list-item-icon>
            </v-list-item>

            <v-list-item v-for="follower in followers" :key="follower.id_user" @click="goSeller(follower.id_user)">
                <v-list-item-avatar size="60" style="object-fit: cover;">
                    <v-img style="object-fit: cover;" :alt="`${follower.nombre} avatar`" :src="follower.photo"> <template
                            v-slot:placeholder>
                            <v-row class="fill-height ma-0" align="center" justify="center">
                                <v-progress-circular indeterminate color="green lighten-3"></v-progress-circular>
                            </v-row>
                        </template></v-img>
                </v-list-item-avatar>

                <v-list-item-content>
                    <v-list-item-title class="font-weight-bold">{{ follower.nombre }}</v-list-item-title>
                </v-list-item-content>

                <v-list-item-icon>
                    <v-icon color="green">mdi-check</v-icon>
                </v-list-item-icon>
            </v-list-item>
        </v-list>
    </div>
</template>
<script>
import api from '../../models/controller'
export default {
    props: { id: null },
    data() {
        return {
            followers: [],
        };
    },
    // Vuetify components provide
    // a theme variable that is
    // used to determine dark
    inject: {
        theme: {
            default: { isDark: false },
        },
    },

    methods: {
        goSeller(id_user) {
            this.$router.push(`/seller/${id_user}`).catch(() => { });
        },
    }
    ,
    created() {
        try {
            api.getFollowerByUser(this.id).then((result) => {
                var data = result.data;
                this.followers = data.result;
                console.log("Seguidores: ", this.followers)
            });
        } catch (error) {
            console.log(error);
        }

    },
};
</script>
