<template>
  <v-list subheader>
    <v-subheader>RECIENTES</v-subheader>
    <v-list-item v-for="chat in chatBot" :key="chat.nombre">
      <v-list-item-avatar>
        <v-img :alt="`${chat.nombre} avatar`" :src="chat.photo"></v-img>
      </v-list-item-avatar>

      <v-list-item-content>
        <v-list-item-title :v-text="chat.nombre"></v-list-item-title>
      </v-list-item-content>

      <v-list-item-icon @click="islogen(chat.from_id, 'soporte')">
        <v-badge
          :content="chat.total"
          :value="chat.total"
          color="green"
          overlap
        >
          <v-icon :color="(chat.estado = 1 ? 'deep-purple accent-4' : 'grey')">
            mdi-chat
          </v-icon>
        </v-badge>
      </v-list-item-icon>
      <chatBot
        v-on:DialogChat="OnCerrar"
        v-if="vendedor != null"
        :id_user="vendedor"
        v-model="activarDialogBot"
      />
    </v-list-item>

    <v-list-item v-for="chat in recent" :key="chat.nombre">
      <v-list-item-avatar>
        <v-img :alt="`${chat.nombre} avatar`" :src="chat.photo"></v-img>
      </v-list-item-avatar>

      <v-list-item-content>
        <v-list-item-title :v-text="chat.nombre"></v-list-item-title>
      </v-list-item-content>

      <v-list-item-icon @click="islogen(chat.from_id, 'user')">
        <v-badge
          :content="chat.total"
          :value="chat.total"
          color="green"
          overlap
        >
          <v-icon :color="(chat.estado = 1 ? 'deep-purple accent-4' : 'grey')">
            mdi-chat
          </v-icon>
        </v-badge>
      </v-list-item-icon>
      <chat
        v-on:DialogChat="OnCerrar"
        v-if="vendedor != null"
        :id_user="vendedor"
        v-model="activarDialog"
      />
    </v-list-item>
  </v-list>
</template>

<script>
import api from "../../models/controller";
import chat from "@/components/Chat/ChatComplete.vue";
import chatBot from "@/components/ChatBot/ChatComplete.vue";
export default {
  components: { chat, chatBot },
  data: () => ({
    recent: [],
    chatBot: [],
    previous: [],
    activarDialog: false,
    activarDialogBot: false,
    vendedor: null,
  }),
  methods: {
    islogen(id, rol) {
      if (localStorage.getItem("user")) {
        if (rol == "user") {
          this.activarDialog = true;
          this.vendedor = id;
        } else {
          this.activarDialogBot = true;
          this.vendedor = id;
          console.log("ENTRO ACTIVAR BOT")
        }
      }
    },
    OnCerrar(valor) {
      this.vendedor = valor;
    },
  },
  created() {
    if (localStorage.getItem("user")) {
      var credential = JSON.parse(localStorage.getItem("user"));
      const idUser = credential.user;
      api.getChats(idUser.id_user).then((results) => {
        this.recent = results.data;
      });

      console.log("DATOS DEL USUARIO : ", idUser.id_user)
      api.getChatsBot(idUser.id_user).then((results) => {
        console.log("RESUTLADOS DE cHATS: ", results.data);
        this.chatBot = results.data;
      });
    }
  },
};
</script>