<template>
    <div class="text-center">
        <v-dialog v-model="show" transition="dialog-bottom-transition" max-width="500">
            <v-card class="mx-auto">
                <v-toolbar dark color="green">

                    <v-toolbar-title>Producto No.{{ product }}</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn icon dark @click="show = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-items> </v-toolbar-items>
                </v-toolbar>
                <v-container>
                    <v-row>
                        <v-col cols="12">
                            <p class="font-weight-bold text--secondary">
                                Saldo en monedas
                            </p>

                            <v-list-item style="padding:0px; margin-bottom: 0px !important;">
                                <p class="font-weight-black text-h4" style="margin-top:-15px !important">
                                    {{ money.fichas }}
                                </p> <v-avatar class="ma-3" size="33" tile style="margin-top:-23px !important">
                                    <v-img src="../../assets/moneda.png"></v-img>
                                </v-avatar>
                                <v-list-item-content style="margin-top:-26px !important; margin-bottom: 0px !important;">
                                    <v-list-item-subtitle>1,000 monedas = <b> 50</b> GTQ</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-list-item>

                            <v-divider></v-divider>
                        </v-col>
                        <v-col cols="12">
                            <v-card outlined tile class="rounded-lg">
                                <v-app-bar flat color="rgba(0, 0, 0, 0)">

                                    <p class="font-weight-black h5 mt-2" style="margin-top:30px !important">
                                        Deposito libre
                                    </p>


                                    <v-spacer></v-spacer>

                                </v-app-bar>
                                <span class="pl-4">Realizar un depsito mnimo.</span>
                                <v-list>

                                    <v-list-item>
                                        <v-avatar color="success" size="40" class="mr-3">
                                            <span class="white--text  font-weight-bold">1</span>
                                        </v-avatar>
                                        <v-list-item-content>
                                            <v-list-item-title class="font-weight-bold mt-1">25 GTQ</v-list-item-title>

                                            <v-list-item-subtitle>Mnimo de Q25, Q50, Q100 etc...</v-list-item-subtitle>

                                            <v-list-item-subtitle>Puedes elegir cuntos dias</v-list-item-subtitle>
                                            <v-list-item-subtitle>necesitas publicitar tu producto.</v-list-item-subtitle>
                                        </v-list-item-content>
                                        <div style="text-align: center;">
                                            <v-btn color="success" outlined @click="buyPlan(plan)"
                                                style="margin-top:-5px !important; width: 40px !important;"
                                                class="mt-2 mb-4 mr-2"><v-icon size="25">mdi-bank</v-icon></v-btn>
                                        </div>
                                    </v-list-item>
                                </v-list>
                            </v-card>
                        </v-col>
                        <v-col cols="12">
                            <br>
                            <v-card outlined tile class="rounded-lg">
                                <v-app-bar flat color="rgba(0, 0, 0, 0)">

                                    <p class="font-weight-black h5 mt-2" style="margin-top:30px !important">
                                        Planes
                                    </p>

                                    <v-spacer></v-spacer>
                                    <p class="font-weight-black h6 mt-2" style="margin-top:30px !important">
                                        Mtodos de compra
                                    </p>
                                </v-app-bar>
                                <v-list>

                                    <v-list-item v-for="(plan, i) in planes" :key="i" class="mb-1">
                                        <v-avatar :color="plan.color" size="40" class="mr-3">
                                            <span class="white--text  font-weight-bold">{{ plan.id_plan }}</span>
                                        </v-avatar>
                                        <v-list-item-content>
                                            <v-list-item-title class="font-weight-bold mt-1">{{ plan.precio
                                            }} GTQ</v-list-item-title>

                                            <v-list-item-subtitle>{{ plan.nombre_plan }}</v-list-item-subtitle>
                                        </v-list-item-content>
                                        <div style="text-align: center;">

                                            <v-btn max-width="50" max-height="50" color="orange"
                                                :disabled="getDisable(plan.monedas)" outlined @click="buyPlanMoney(plan)"
                                                style="margin-top:-5px !important" class="mt-2 mb-4 ml-2"><v-icon
                                                    color="success" size="23">mdi-bank</v-icon> + <v-img max-height="25"
                                                    max-width="25" sizes="25" src="../../assets/moneda.png"></v-img></v-btn>
                                        </div>
                                    </v-list-item>


                                </v-list>
                            </v-card>
                        </v-col>
                        <v-col cols="12">

                            <v-card @click="verInfo" class="rounded rounded-lg mt-2 mb-2" color="green" dark>
                                <div class="d-flex flex-no-wrap justify-space-between">
                                    <div>
                                        <v-card-title class="text-h6">Gana ms monedas </v-card-title>

                                        <v-card-subtitle class="body-2">al subir, referir o compartir tus
                                            productos</v-card-subtitle>
                                    </div>

                                    <v-avatar class="ma-3" size="80" tile>
                                        <v-img src="../../assets/moneda.png"></v-img>
                                    </v-avatar>
                                </div>
                            </v-card>
                        </v-col>
                    </v-row>

                </v-container>
                <!--<enviarBoleta class=" mb-1" v-if="item" :product="item" v-model="showScheduleForm" />-->
                <buyfinish :idpro="product" :price="price" :titlePlan="titlePlan" :idPlan="idPlan" v-model="active" />
                <buyMoney v-if="idPlan" :monedas="money.fichas" :idpro="product" :price="price" :titlePlan="titlePlan"
                    :idPlan="idPlan" v-model="activeMoney" />

                <info v-if="isInfo == true" v-model="showScheduleForm"></info>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import store from '../../store'
import buyfinish from './BuyFinish.vue'
import buyMoney from './BuyFinishMoney.vue'
import info from './Instrucciones.vue'
import api from '../../models/controller'
export default {
    components: {/* enviarBoleta*/  buyfinish, buyMoney, info },
    props: {
        value: Boolean,
        product: null,
    },
    data() {
        return {
            plan: {
                nombre_plan: 'Deposito',
                id_plan: 4,
                precio: 25
            },
            active: false,
            activeMoney: false,
            money: 0,
            planes: [],
            titlePlan: null,
            idPlan: null,
            price: null,
            isInfo: false,
            showScheduleForm: false,
        }
    },
    computed: {
        show: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit("input", value);
            },
        },
    },
    methods: {
        buyPlan(plan) {
            this.active = true;
            this.titlePlan = plan.nombre_plan;
            this.idPlan = plan.id_plan;
            this.price = plan.precio
        },

        buyPlanMoney(plan) {
            this.activeMoney = true;
            this.titlePlan = plan.nombre_plan;
            this.idPlan = plan.id_plan;
            this.price = plan.precio
        },
        getDisable(fichas) {
            if (this.money.fichas < 1000) return true
            if (fichas > this.money.fichas) return false
            else if (fichas > this.money.fichas) return false
            else if (fichas >= this.money.fichas) return false
            else return true
        },
        getMoney: function (id) {
            api.getMoney(id).then((result) => {
                var money = result.data;
                this.money = money.money;
            });
        },

        getPlanes: function () {
            api.getAllPlanes().then((datos) => {
                var planes = datos.data;
                planes.forEach(element => {
                    if (element.id_plan != 5 && element.id_plan != 4) {
                        this.planes.push(element);
                    }
                });
            });
        },

        verInfo: function () {
            this.isInfo = true;
            this.showScheduleForm = true;
        }
    },
    created() {
        if (store.getters.isLoggedIn) {
            var data = store.getters.getUser;

            this.isLogin = true;
            this.getMoney(data.id_user);
            this.getPlanes();

        }
    }
}
</script>
