<template>
  <div>
    <h4 class="ml-2">Destacados</h4>
    <b-row v-if="productos.length < 1">
      <b-col v-for="item in [0, 1]" :key="item" cols="6" sm="4" md="3" lg="3" xl="3">
        <v-sheet :color="`grey ${theme.isDark ? 'darken-2' : 'lighten-4'}`" class="pa-1"
          style="border-radius: 3px"><v-skeleton-loader class="mx-auto" max-width="300" type="card"></v-skeleton-loader>
        </v-sheet>
      </b-col>
    </b-row>
    <v-row v-else no-gutters>
      <v-col cols="6" sm="4" md="3" lg="3" xl="3" v-for="item in productos" :key="item.id_pro">
        <v-card style="border-radius: 2px; box-shadow: none !important" max-width="350" :loading="loading"
          class="mx-auto pa-1">
          <v-img @click="verDetalle(item.id_pro)" style="border-radius: 2px" class="mb-1" v-bind:src="item.thumbnail"
            height="225px">
            <v-app-bar style="width: 100%" flat color="rgba(0, 0, 0, 0)">
              <div v-if="idUser == item.id_user" class="flag">DESTACA YA</div>
              <div v-else class="flag ">COMPRALO YA</div>
              <v-spacer></v-spacer>
              <div class="backIcon "> <v-icon class="intermitente parpadea" large :color="item.color" role="img" aria-hidden="false">
                  {{ icons.mdiCrownCircle }}
                </v-icon></div>

            </v-app-bar>
          </v-img>
          <span class="mt-2 margin" style="font-size: 12pt; font-weight: bolder" v-text="'Q.' + item.precio">
          </span>
          <br />
          <div style="max-width: 370px; font-size: 12pt" class="mb-0 margin-title text-truncate">
            {{ item.titulo }}
          </div>
          <v-btn class="mb-2" @click="anunciar(item.id_pro)" small dark :color="item.id_plan != 5 ? item.color : 'rgb(255, 0, 0)'" block
            v-if="idUser == item.id_user"> <v-icon class="mb-0" size="20" color="white">mdi-bullhorn</v-icon> {{ item.id_plan != 5 ? 'MEJORAR PLAN' : 'DESTACA YA' }} 
            </v-btn>
        </v-card>
      </v-col>
    </v-row>
    <a href="/productsOutStanding" v-if="productos.length > 200">
      <p class="text-right mt-3 link mr-2 font-weight-bold">Ver ms...</p>
    </a>
    <detalleProducto :product="product" v-model="showScheduleForm" />
    <login v-model="isLogin" />
    <buyplan v-on:input="onResultados" v-if="buyPro" :product="buyPro" v-model="showScheduleForm1"></buyplan>

  </div>
</template>

<script>
//import axios from "axios";
import api from "../../models/controller";
import timePublicidad  from "../../events/timepublicidad"
import store from "../../store"
import { mdiCrownCircle } from "@mdi/js";
import { mdiAccount } from "@mdi/js";
import { mdiCrown } from "@mdi/js";
import detalleProducto from "@/components/DetalleProducto.vue";
import login from "@/components/Login/Login.vue";

import buyplan from "@/components/Buyplan/Home.vue";
export default {
  components: { detalleProducto, login, buyplan },
  props: { id: null },

  data() {
    return {

      edit: false,
      id_pro: null,
      id_cat: null,
      rating: 4.5,
      dialog: false,
      loading: false,
      productos: [],
      idUser: null,
      product: {},
      buyPro: null,
      showScheduleForm1: false,
      showScheduleForm: false,
      isLogin: false,
      icons: {
        mdiCrown,
        mdiCrownCircle,
        mdiAccount,
      },
    };
  },
  inject: {
    theme: {
      default: { isDark: false },
    },
  },
  methods: {
    islogen(id) {
      if (localStorage.getItem("user")) {
        this.isLogin = false;
        this.$router.push({
          path: `/detalleProducto/${id}`,
          id_pro: { id },
        });
      } else {
        this.isLogin = true;
      }
    },
    verDetalle(id) {

      if (store.getters.isLoggedIn) {
        var data = store.getters.getUser;
        let vista = {
          id_user: data.id_user,
          id_pro: id,
          like: 0,
        };

        let recientView = {
          idUser: data.id_user,
          idPro: id
        }


        api.insertSendRecientView(recientView)
        api.createView(vista).then(() => {
        });

      }
      /*  if (localStorage.getItem("user")) {
          var credential = JSON.parse(localStorage.getItem("user"));
          let user = credential;
          let vista = {
            id_user: user.id_user,
            id_pro: id,
            like: 0,
          };
  
          let recientView={
            idUser: user.id_user, 
            idPro: id
          }
  
          console.log("DATOS ENVIO :", recientView)
  
          api.insertSendRecientView(recientView);
          api.createView(vista).then(() => {
          });
        }*/
      // this.$router.push("/detalleProducto", {id_pro: id});
      this.$router.push({
        path: `/detalleProducto/${id}`,
        id_pro: { id },
      });

      //window.location.href= `/detalleProducto/${id}`
    },
    verProduct(e) {
      //console.log("Datos del producto :", e);
      this.product = e;
      this.showScheduleForm = true;
    },

    agregarCaracter(precio) {
      let cadena = String(precio);
      let caracter = ",";
      let pasos = 3;
      let cadenaConCaracteres = "";
      const longitudCadena = cadena.length;

      if (longitudCadena == 6) {
        for (let i = 0; i < longitudCadena; i += pasos) {
          if (i + pasos < longitudCadena) {
            cadenaConCaracteres += cadena.substring(i, i + pasos) + caracter;
          } else {
            cadenaConCaracteres += cadena.substring(i, longitudCadena);
          }
        }
        return "Q." + cadenaConCaracteres;
      } else {
        return "Q." + cadena;
      }
    },
    

    anunciar: function (item) {
      this.buyPro = item;
      this.showScheduleForm1 = true;
    },
    onResultados: function (item) {
      this.showScheduleForm1 = item;
      this.edit = false;
      this.buyPro = null;
      this.id_pro = null;
      this.id_cat = null;
    },

    sumarDias: function(fecha, dias) {
      fecha.setDate(fecha.getDate() + dias);
      return fecha;
    }
  },

  created() {
    if (navigator && navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
        this.productos = [];

        api.getAllProPlanRecientes(this.lat, this.lng, 10).then((result) => {
          var datos = result.data;
          var resul = datos.data;
          timePublicidad(resul).then((res)=>{
            this.productos= res;
          });
        });
      });
    }

    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
      this.idUser = data.id_user;
    }
  },
};
</script>

<style scoped>
.margin {
  padding-left: 5px;
  margin-top: 5px;
}

.margin-title {
  padding-left: 5px;
  margin-top: -5px;
  margin-bottom: 8px;
}

.flag {
  font-family: monospace;
  font-size: 13px;
  position: absolute;
  top: 5px;
  left: 0px;
  color: white;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  padding-left: 5px;
  padding-right: 5px;
  background: orange;
}

.backIcon {
  right: 5px;
  position: absolute;
  top: 5px;
  background: white;
  border-radius: 55%;
}

i.v-icon.v-icon {
  color: rgb(10, 223, 3);
}


.parpadea {
  
  animation-name: parpadeo;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;

  -webkit-animation-name:parpadeo;
  -webkit-animation-duration: 3s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
}

@-moz-keyframes parpadeo{  
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
  100% { opacity: 1.0; }
}

@-webkit-keyframes parpadeo {  
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
   100% { opacity: 1.0; }
}

@keyframes parpadeo {  
  0% { opacity: 1.0; }
   50% { opacity: 0.0; }
  100% { opacity: 1.0; }
}
</style>
