<template>
    <v-container class="text-center">
        <div v-if="!isActive">
            <v-icon class="mt-15" color="red" size="120">mdi-alert-circle</v-icon>
            <p class="font-weight-black text-h6 mt-2">Al parecer el enlace ha dejado de funcionar o ha sido utilizado con
                anterioridad.</p>
            <span class="mb-3">Intenta restablecer tu contrasea nuevamente.</span>
            <v-btn to="/" class="mt-2" large outlined block rounded color="success">Ir al inicio</v-btn>
        </div>
        <div v-else>


            <div style="width: 100%;" class="text-center"> <v-icon class="mt-4" color="rgb(15, 170, 15)"
                    size="120">mdi-lock-reset</v-icon></div>
            <p class="font-weight-black text-h5 mt-2">Crea tu nueva contrasea</p>
            <span>La nueva contrasea debe ser diferente a las contraseas anteriormente usadas.</span>
            <v-form ref="form" class="mt-5 pt-1" @submit.prevent="confirmatePassword" style="text-align: left !important;">
                <span class="font-weight-bold text-left">Contrasea</span>
                <v-text-field dense class="rounded-lg mt-2" v-model="password" :rules="[rules.password, rules.length(10)]"
                    outlined color="green" counter="20" label="Contrasea"
                    style="min-height: 96px; margin-bottom: -18px !important;"
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'" :type="show1 ? 'text' : 'password'" name="input-10-1"
                    hint="Contrasea Valida" @click:append="show1 = !show1"></v-text-field>
                <span class="font-weight-bold">Confirmar contrasea</span>
                <v-text-field dense class="rounded-lg mt-2" v-model="password_repeat" :rules="[rules.passRepeat]" outlined
                    color="green" label="Repite Contrasea" style="min-height: 96px;margin-bottom: -25px !important;"
                    :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'" :type="show2 ? 'text' : 'password'" name="input-10-1"
                    hint="Las contraseas coinciden" @click:append="show2 = !show2"></v-text-field>
                <input class="submit" type="submit" value="Restablece contrasea" name="send" />
            </v-form>
        </div>
        <v-dialog v-model="loading" hide-overlay persistent width="300">
            <v-card color="success" dark class="pt-1">
                <v-card-text class="mt-2">
                    Por favor espere
                    <v-progress-linear indeterminate color="white" class="mb-0 mt-2"></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>
    </v-container>
</template>
  
<script>
import api from '../../models/controller'
export default {
    data() {
        return {
            loading: false,
            isActive: null,
            code: '',
            email: '',
            passwordRepeat: undefined,
            password_repeat: undefined,
            password: undefined,
            show1: false,
            show2: false,
            rules: {
                length: (len) => (v) =>
                    (v || "").length >= len ||
                    `Longitud de carcter no vlida, obligatorio ${len}`,
                password: (v) =>
                    !!(v || "").match(
                        /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*(_|[^\w])).+$/
                    ) ||
                    "La contrasea debe contener una letra mayscula, un carcter numrico y un carcter especial, Ejemplo: Abc123@456",
                passRepeat: (v) =>
                    v == this.password || "Las contraseas no son iguales.",
                required: (v) => !!v || "Este campo es obligatorio",
            },
        }
    },
    mounted() {

        api.getVeryEmailByUserPassword(this.$route.params.email, this.$route.params.id).then((result) => {
            var data = result.data;
            if (data.result != 'X') {
                this.isActive = true;
            } else {
                this.isActive = false;
            }
        })
    },
    methods: {
        confirmatePassword: function () {
            this.loading=true;
            window.setTimeout(() => {
                let data = {
                    email: this.$route.params.email,
                    password: this.password,
                    code: this.$route.params.id,
                    password_repeat: this.password_repeat
                }

                api.updatePassword(data).then(() => {
                    this.$router.replace("/succes-password");
                    this.loading=false;
                })
            }, 2000)
        },

        back() {
            this.$route.params.id;

            window.history.back();
        }
    }
}
</script>

<style scoped>
.submit {
    width: 100%;
    background: rgb(15, 170, 15);
    padding: 12px;
    color: white;
    font-size: 14pt;

    border-radius: 10px;
}
</style>