<template>
  <div>
    <v-btn
      @click="tomarAnswer(item)"
      color="success"
      outlined
      class="mt-2 mb-1 btn-size mr-2 d-block"
      rounded
      dark
      v-for="item in answers"
      :key="item.id"
    >
      <span class="span-size">{{ item.answers }}</span></v-btn
    >
  </div>
</template>

<script>
import io from "socket.io-client";
import api from "../../models/controller";
import moment from "moment";
export default {
  props: { id: null, fromId: null, id_pro: null },
  data() {
    return {
      socket: io("https://vendeloyaglobal.herokuapp.com"),
      answers: [],
      idUser: null,
    };
  },
  methods: {
    sendMessage() {
      console.log("Enviando mensahe");
   
    },
    tomarAnswer(item) {
      console.log("DATOS DE LA RESPUESTA : ", item);
      if (item.action == "pregunta") {
           console.log("Entrando a : ", item);
         this.socket.emit("nuevoChat", {
        to_id: this.fromId,
        from_id: this.user.id_user,
        id_pro: this.id_pro ,
        id_questions: this.id,
        id_answer: item.id,
        mensaje: item.answers,
        estado: 0,
        author: this.user.nombres + " " + this.user.apellidos,
        rol: "user",
        fecha: moment(),
      });
        api.getChatsBotQuestion(item.valor).then((resuls) => {
          console.log("Traer Pregunta , ", resuls.data);
          this.tomarQuestions(resuls.data[0])
        });
      } else if (item.action == "redirigir") {
        console.log("REDIRIGIR ");
      }
    },
     tomarQuestions(item) {
      console.log("DATOS DE LA NEW pREGUNTA : ", item);
         this.socket.emit("nuevoChat", {
        to_id: this.user.id_user ,
        from_id: this.fromId,
        id_pro: this.id_pro ,
        id_questions: item.id,
        id_answer: null,
        mensaje: item.question,
        estado: 0,
        author: 'Soporte',
        rol: "soporte",
        fecha: moment(),
      });
    },
  },
  
  created() {
    console.log("ID PREGINTA : ", this.id, this.fromId);
    if (localStorage.getItem("user")) {
      var credential = JSON.parse(localStorage.getItem("user"));
      const idUser = credential.user;
      try {
        api.getUser(idUser.id_user).then((result) => {
          this.user = result.data[0];
        });

        //OBTENER LOS CHAT
        api.getChatBotAnswer(this.id).then((result) => {
          console.log("RESUTLADOS RESPUESTA : ", result.data);
          this.answers = result.data;
        });
      } catch (error) {
        console.log(error);
      }
    }
  },
};
</script>

<style>
.btn-size {
  font-size: 6pt;
}

.span-size {
  font-size: 8.5pt;
}
</style>