<template>
  <v-row justify="center">
    <v-dialog
      v-model="show"
      persistent
      max-width="290"
    >
      <v-card class="text-center pt-5 mx-auto">
        <div style="width: 100%;   display: flex;
        justify-content: center;
        align-items: center;">
          <v-img max-width="150" src="../../assets/moneda.png"></v-img>
        </div>

        <h2 class=" text-center" style="text-aling: center !important">
        Felicidades acabas de ganar 20 <b>monedas </b>por subir tu producto!
        </h2>
        <v-card-text>T artculo ha sido cargado y publicado con xito.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            text
            @click="repetir()"
          >
            Cargar Nuevo
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="salir"
          >
            Salir
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
  export default {
      
   props: {
    value: Boolean,
  },
    data () {
      return {
        dialog: false,
      }
    },

    methods:{
        salir(){
         this.$router.push("/").catch(()=>{});
        },
        repetir(){
           this.$router.go();
        }
    },
     computed: {
    show: {
      get() {
    
        return this.value;
      },
      set(value) {
        this.$emit("input", value);

        
      },
          
    },
  },
  }
</script>