<template>
    <div class="container-carousel " v-if="publicidad.length > 0">
        <div class="cajon-carousel mx-1">
            <v-carousel :show-arrows="false" class="roundCarousel " cycle height="350" delimiter-icon="mdi-minus"
                hide-delimiter-background hide-delimiters>
                <v-carousel-item v-for="item in publicidad" :key="item.id">

                    <v-card elevation="4" rounded style="border-radius: 10px !important">

                        <v-img class="white--text align-end" gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.2)"
                            height="200px" aspect-ratio="2" :src="item.url">

                            <v-btn class="btn-verdetalles" :color="item.colorBoton" @click=" navegar(item)">MS
                                DETALLES</v-btn>

                        </v-img>

                    </v-card>

                </v-carousel-item>
            </v-carousel>
        </div>
        <a href="/espacio-publicitario" class="btn-vermas" text>Ver ms...</a>
    </div>
</template>
<script>
import api from "@/models/controller";
import timePublicidad from "../../events/timePublicidadMarca"
export default {
    data() {
        return {

            publicidad: [],
            colors: [
                'rgb(61, 243, 61)',
                'rgb(72, 208, 241)',
            ],
            slides: [
                ' <b>Gana tus primeras 100 monedas al registrarte!<b>',
                'Second',
            ],
        }
    },

    methods: {

        navegar: function (item) {
            if (item != 4) {


                if (item.linkPrincipal == 'facebook') {
                    window.location.href = `${item.facebook}`
                } else if (item.linkPrincipal == 'instagram') {
                    window.location.href = `${item.instagram}`
                } else if (item.linkPrincipal == 'whatsapp') {
                    window.location.href = `${item.whatsapp}`
                } else if (item.linkPrincipal == 'pagina') {
                    window.location.href = `${item.pagina}`
                }
            } else {
                window.location.href = `https://www.facebook.com/globaltechnologygt`
            }

        },
        allPublicidad() {
            api.allPublicidadPagada().then((res) => {

                var lista = res.data;

                timePublicidad(lista.result).then((resp) => {
                    this.publicidad = resp;
                })
            })
        },

        eventObtenerPubli: function () {
            window.setInterval(() => {
                api.allPublicidadPagada().then((res) => {
                    var lista = res.data;
                    var listaLength = []
                    listaLength = lista.result;
                    if (listaLength.length > this.publicidad.length) {

                        this.allPublicidad();
                    }
                })
            }, 10000)
        }
    },

    created() {
        this.allPublicidad();
        this.eventObtenerPubli()
    }
}
</script>

<style>
.container-carousel {
    position: relative;
    text-align: right;
    height: 250px;
}

.cajon-carousel {
    position: relative;
    height: 175px;
    margin-bottom: 30px !important;
}

.roundCarousel {
    border-radius: 10px !important;
    height: 200px !important;
}

.color {
    color: rgb(72, 208, 241);
}

.url {
    border-radius: 10px !important;
    width: 100% !important;
    height: 100%;
}

.empresa {
    left: 5px;
    font-size: 15pt;
    position: absolute;
    bottom: -18px;
    color: white;
    font-weight: bolder;
    text-shadow: 2px 0px 15px black;
}

.title-empresa {
    margin-bottom: -10px !important;
    font-size: 16pt;
    color: black;
}

.btn-verdetalles {
    z-index: 10;
    position: absolute;
    bottom: -110px;
    top: 0;
    left: 0;
    right: 0;
    bottom: -80;
    margin: auto;
    width: 200px;
    border-radius: 15px;
    border: solid 1px white !important;
    color: white !important;
}

.v-card--reveal {
    align-items: center;
    bottom: 0;
    justify-content: center;
    opacity: .8;
    position: absolute;
    width: 100%;
}

.icon-close {
    position: absolute;
    top: 0px;
    right: 0px;
    zoom: 1.5;
    color: white !important;
}
</style>