<script>
//import axios from "axios";
//import firebase from "firebase/app";
//import "firebase/storage";
//import updatePhotoProduct from "../../controller/update";
import store from "../../store"
import veryemail from "@/models/email"
import api from "../../models/controller";
import noti from "../../models/controllerOnesignal";
import { gmapApi } from "vue2-google-maps";
import fotos from "@/components/AgregarProducto/CargaFotos";
//import enviarBoleta from "@/components/EnviarBoleta.vue";
//import { required, digits, email, max, regex } from "vee-validate/dist/rules";
//import {extend,ValidationObserver,ValidationProvider,setInteractionMode,} from "vee-validate";
import finish from "@/components/Dialogs/CargaFinalizada.vue";


export default {
  components: { finish, fotos },
  data: () => ({
    valid: true,
    radioGroup: 1,
    catEmpleo: [],
    tab: null,
    headers: [{
      id: 1,
      title: 'Productos',
      icon: 'shopping'
    },
    {
      id: 2,
      title: 'Empleos',
      icon: 'chair-rolling'
    }
    ],
    intentos: 1,
    snackbar: false,
    snackbarError: false,
    isOther: null,
    nameEspecification: '',
    isText: 2,
    isSubCat: false,
    selectCat: { nombre_categoria: "Seleccionar", id_categoria: null },
    selectSubCat: { nombre: "Seleccionar", id_cat: null },
    idfoto: "",
    loading: false,
    downloadUrl: "",
    file: "",
    geocoder: null,
    cargaFinalizada: false,
    cargandoEnvio: false,
    plan: {},
    codigoUser: null,
    select: { nombre: "Seleccionar", id: null },
    selectM: { nombre: "Seleccionar", id: null },
    showScheduleForm: false,
    expandNews: false,
    expandSingle: false,
    departamentos: [],
    municipios: [],
    planes: [],
    user:{},
    form: {
      id_SubCategoria: null,
      id_plan: 5,
      titulo: null,
      descripcion: null,
      precio: null,
      ocult_pro: false,
      ocult_desc: false,
      fecha_alta: null,
      id_muni: null,
      thumbnail: null,
      existencia: null,
      categoria: null,
      namefoto: null,
      direccion: null,
    },
    categorias: [],
    subCategorias: [],
    step: 1,
    sending: false,
    userSaved: false,
    selection: "4",
    sizes: ["1", "2", "3", "4"],
    arrayimgUpload: [],
    arrayImg: [],
    imagenMiniatura:
      "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg",
    rules: {
      select: [(v) =>  v.length>0 || "This field is required"],
      length: len => v => (v || '').length <= len || `Longitud de carcter no vlida, obligatorio ${len}`,
      required: value => !!value || 'Campo requerido',
      counter: (value) => value.length <= 40 || "Max 40 characters",
      email: (value) => {
        const pattern =
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "Invalid e-mail.";
      },
    },

    emailJs: `    <form id="contact-form">
        <input type="hidden" name="contact_number">
        <label>Name</label>
        <input type="text" name="user_name">
        <label>Email</label>
        <input type="email" name="user_email">
        <label>Message</label>
        <textarea name="message"></textarea>
        <input type="submit" value="Send">
    </form>`
  }),

  methods: {

    obSC: function (e) {
      console.log(this.form.id_SubCategoria, " --*--  ", e);
    },

    obtenerTab(e) {
      console.log(this.tab, " --  ", e);
      if (e == 0) {
        this.getCategorias();
      } else {
        this.getCatEmpleo();
      }
    },
    regresar: function () {
      this.$router.go(-1);
    },
    getUbi: function (idMuni, idPro) {
      api.getUbicacion(idMuni).then((data) => {
        let direccion = data.data;
        try {
          this.geocoder.geocode(
            { address: direccion.data.Direccion },
            (results, status) => {
              if (status == "OK") {
                var latitude = results[0].geometry.location.lat();
                var longitude = results[0].geometry.location.lng();
                console.log(latitude, longitude);
                let data = {
                  id_pro: idPro,
                  dire: direccion.data.Direccion,
                  lat: latitude,
                  lng: longitude,
                };
                api.createUbicacion(data).then(() => {

                });
              }
            }
          );
        } catch (error) {
          console.log('');
        }
        //console.log('Direccion : ',direccion.data.Direccion)
      });
    },

    obtSubCat: function (e) {
      this.isSubCat = false;
      /* if (e.nombre_categoria == 'Otros') {
         this.form.categoria = e.id_categoria;
         this.isOther = false;
         this.isText = 1;
       } else {*/

      this.subCategorias = [];
      this.form.categoria = e.id_categoria;
      api.getSubCategoriaByProduct(e.id_categoria).then((result) => {
        var menu = result.data;
        var res = menu;

        if (menu.length == 0) {
          console.log(result);
          this.isOther = true;
          this.isText = 0
        }

        var other = null;
        //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU

        for (let i = 0; i < res.length; i++) {
          if (res[i].nombre != 'Otros' || res[i].nombre != '+ Agregar') {
            this.subCategorias.push(res[i])
          } else {
            other = res[i];
          }
        }

        if (menu.length > 0) {
          if (other != null) {
            this.subCategorias.push(other);
          }

          this.isOther = true;
          this.isText = 0
        }
      });
      //}
    },

    setSubCat: function (e) {
      this.form.id_SubCategoria = e.id

      if (e.nombre == 'Otros' || e.nombre == '+ Agregar') {
        this.isSubCat = true;
      } else {
        console.log()
        this.isSubCat = false;
      }
    },
    saveProduct: function (e) {

      if (this.$refs.form.validate()) {
      this.sending = true;
      this.cargandoEnvio = true;
      let idMuni = this.form.id_muni;
      let params = {
        id_SubCategoria: this.form.id_SubCategoria,
        id_plan: this.form.id_plan,
        titulo: this.form.titulo,
        descripcion: this.form.descripcion,
        precio: this.form.precio,
        ocult_pro: this.form.ocult_pro,
        ocult_desc: this.form.ocult_desc,
        fecha_alta: new Date(),
        existencia: this.form.existencia,
        id_categoria: this.form.categoria,
        id_municipio: idMuni.id,
        thumbnail: this.arrayImg[0].img,
        id_user: this.form.id_user,
        direccion: this.form.direccion,
      };
      try {
        if (this.arrayImg.length > 0) {
          api.createProduct(params).then((data) => {
            veryemail.sendEmailCargaProductos(e.target)
            let product = data.data;
            let vista = {
              id_user: 1000000000010000000100000000001,
              id_pro: product.insertId,
              like: 5,
            };
            this.getUbi(idMuni.id, product.insertId);
            api.createView(vista);
            this.arrayImg = this.arrayImg.slice(0, this.selection);
            let n = 1;
            let array = this.arrayImg.length + 1;
            for (var i = 0; i < array; i++) {
              //PROCESO PARA CARGAR LA GALERIA DE IMAGENES, MEDIANTE UN CICLO
              if (n > this.arrayImg.length) {
                this.cargandoEnvio = false;

                this.clearForm();
                this.cargaFinalizada = true;
                // this.
              } else {
                let photo = {
                  id_pro: product.insertId,
                  clave: this.arrayImg[i].id,
                  photo: this.arrayImg[i].img,
                };
                api.createPostGaleria(photo).then(() => { });
              }
              n++;
            }
            //CARGAR FICHAS 
            this.cargarFichas(this.codigoUser);

            this.userSaved = true;
            this.sending = false;
          });
        } else {
          console.log("DEBES AGREGAR FOTO");
        }
      } catch (error) {
        console.log(error);
      }
    }
    },
    clearForm: function () {
      this.form.id_SubCategoria = null;
      this.form.id_plan = 5;
      this.form.titulo = null;
      this.form.descripcion = null;
      this.form.precio = null;
      this.form.ocult_pro = false;
      this.form.ocult_desc = false;
      this.form.fecha_alta = null;
      this.form.id_muni = null;
      this.form.thumbnail = null;
      this.form.existencia = null;
      this.form.categoria = null;
      this.form.namefoto = null;
      this.form.direccion = null;
      this.select = { nombre: "Seleccionar", id: null };
      this.selectM = { nombre: "Seleccionar", id: null };
      this.arrayImg = [];
    },


    validation: function () {
      if (this.rules.required != "Required.") {
        this.step++;
      } else {
        console.log("Campos Vacios");
      }
    },
    obtenerMuni: function (e) {
      this.municipios = [];
      let id = e.id;

      api.getMunicipiosDepa(id).then((result) => {
        let muni = result.data;
        this.municipios = [];
        muni.forEach((element) => {
          this.municipios.push({
            id: element.idMunicipio,
            nombre: element.nombreMunicipio,
          });
        });
      });
    },
    verdata() {
      console.log("VALORES DE fOMR : ");
    },
    obtenerSub: function (e) {
      this.form.categoria = e;
      api.getSubCategoriaByProduct(e).then((result) => {
        var menu = result.data;
        //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU
        this.subCategorias = menu;
        this.step++;
      });
    },
    seleccionarCat: function () {
      this.form.id_SubCategoria;
      this.step++;
    },

    onPhotos: function (datos) {
      this.arrayImg = datos;
    },
    cargarFichas: function (codigo) {
      api.obtenerReferido(codigo).then((result) => {
        var user = result.data;
        var detalle = user.user;
        var data = {
          id_user: detalle.id_user,
          fichas: 20,
          fichas_detalle: "+ Por subir nuevo producto a VENDELO YA.",
        };
        api.CargarFichasUserRefiere(data).then(() => {
          noti.notifiCliente('addProduct', detalle.id_user, 'FELICIDADES ACABAS DE GANAR *20* MONEDAS', 'Tu producto se ha subido correctamente, disfruta de tus monedas.')
        });
      });
    },

    areEqual(name1, name2) {
      return name1 === name2;
    },

    agregarEspecificacion: function () {
      //  window.localStorage.removeItem('newCat');
      var resultado = "";
      if (window.localStorage.getItem('newCat')) {
        resultado = window.localStorage.getItem('newCat');
        if (resultado === this.nameEspecification) {
          //IMPRIMI ALERTA ERROR
          this.snackbarError = true;
          window.setTimeout(() => {
            this.snackbarError = false;
          }, 3000)
        } else {
          this.getGuardaCatEspecificada()
          window.localStorage.setItem('newCat', this.nameEspecification)
        }
      } else {
        this.getGuardaCatEspecificada()
        window.localStorage.setItem('newCat', this.nameEspecification)
      }
    },

    getGuardaCatEspecificada() {

      var data = {
        name: this.nameEspecification,
        cat: this.form.categoria
      }

      api.insertSubCategory(data).then((result) => {
        var data = {};
        data = result.data;
        var res = data.data
        this.snackbar = true;
        this.form.id_SubCategoria = res.id_SubCategorias;
        window.setTimeout(() => {
          this.snackbar = false;
        }, 3000)
      })

    },

    getCategorias: function () {
      this.form.categoria = null;
      this.form.id_SubCategoria = null;
      api.getAllCategorias().then((result) => {

        var menu = result.data;
        var res = menu;
        var other = {};
        //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU

        for (let i = 0; i < res.length; i++) {
          if (res[i].nombre_categoria != 'Otros') {
            this.categorias.push(res[i])

          } else {
            other = res[i];
          }
        }
        this.categorias.push(other)
      });
    },

    getCatEmpleo: function () {
      api.getCatEmpleo().then((result) => {
        var dat = result.data;
        this.catEmpleo = dat.result;
        this.form.categoria = this.catEmpleo[0].id_categoria
        console.log(this.form.categoria)
      })
    }
  },

  computed: {
    currentTitle() {
      switch (this.step) {
        case 1:
          return "Qu publicars hoy?";
        case 2:
          return "Sub Categora";
        case 3:
          return "Qu vendes?";
        case 4:
          return "Cargar imagenes";
        case 5:
          return "Dnde se encuentra?";
        default:
          return "Adicional";
      }
    },
    google: gmapApi,
  },

  created: async function () {
    window.scrollTo(0, 0);
    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;

      let id = data;
      this.user= id
      this.codigoUser = data.codigo;
      this.form.id_user = id.id_user;
      console.log( this.user)
      try {
        this.getCategorias();
        this.getCatEmpleo();

        api.getDepartamento().then((result) => {
          var depa = result.data;
          depa.forEach((element) => {
            this.departamentos.push({
              nombre: element.nombreDepartamento,
              id: element.idDepartamento,
            });
          });
        });

        api.getAllPlanes().then((datos) => {
          var planes = datos.data;
          planes.forEach((element) => {
            if (element.id_plan != 5 && element.id_plan != 4) {
              this.planes.push(element);
            }
          });

          this.planes = this.planes.reverse();
        });
      } catch (error) {
        console.log("error", error);
      }

    }
  },
  mounted() {
    this.$gmapApiPromiseLazy().then(() => {
      if (this.google) {
        this.geocoder = new this.google.maps.Geocoder();
      }
    });
  },
};
</script>



