<script>
import api from "../../models/controller";
export default {
  props: ["idProduct","plan"],
  data: () => ({
    galery: [],
    length: 0,
    portada: {},
    width: screen.width
  }),
  methods: {
    viewPhoto() {},

    verFoto(photo){
      this.$emit('obtenerFoto', photo);
    },

    getPhotosPrincipal:function(id){
      api.getGaleriaProduct(id).then((result) => {
      this.galery = result.data;
      this.portada = this.galery[0];
      this.length = this.galery.length;
    });
    }
  },
  created() {

    this. getPhotosPrincipal(this.idProduct)
    
    this.$watch(
      () => this.idProduct,
      (toParams) => {
        this. getPhotosPrincipal(toParams)
      }
    )

  },
};
</script>

