<template>
  <div class="pl-1 pr-1 pt-1">
    <sinregistros :title="'Auch, an no has vendido.'" :total="ventas.length" :tipo="'productos'" />

    <v-card rounded-xl elevation="2" min-height="170px" class="ml-2 mr-2 mt-5 mb-6 card-plan">
      <div class="mx-auto px-5 text-center div-publicidad ">
        <p style="font-size: 16pt;">Vende ms rpido con <b>Nuestros planes de publicidad</b>
        </p>
        <v-btn color="yellow" large class="" block rounded @click="elegirProducto()">Comprar plan</v-btn>
      </div>
    </v-card>

    <v-row no-gutters>
      <v-col cols="6" md="3" v-for="(item, i) in ventas" :key="i" class="pl-1 pr-1 pt-1 pb-1">
        <v-card class="mx-auto" elevation="3" style="position: relative;">

          <div v-if="item.id_plan != 5" class="destacado">DESTACADO</div>
          <v-img height="150" v-bind:src="item.thumbnail">
            <v-app-bar flat color="rgba(0, 0, 0, 0)">
              <v-spacer></v-spacer>

              <v-menu bottom left>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn dark icon v-bind="attrs" v-on="on">
                    <v-icon size="40">mdi-dots-vertical-circle-outline</v-icon>
                  </v-btn>
                </template>

                <v-list>
                  <v-list-item v-for="(itemOption, i) in  itemsProducts" :key="i"
                    @click=" elegirOptionProducto(item, itemOption.title)">
                    <v-list-item-title>{{ itemOption.title }}</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
            </v-app-bar>

            <template v-slot:placeholder>
              <v-row class="fill-height ma-0" align="center" justify="center">
                <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
              </v-row>
            </template>
          </v-img>
          <span class="mt-9 margin ml-3" style="font-size: 12pt; font-weight: bolder; margin-top: 5px; color:green">
            Q.{{ item.precio }}
          </span>
          <br />
          <div style="max-width: 370px; font-size: 11pt" class="mb-2 margin-title text-truncate ml-2">
            {{ item.titulo }}
          </div>
        </v-card>


      </v-col>
    </v-row>

    <buyplan v-on:input="onResultados" v-if="buyPro" :product="buyPro" v-model="showScheduleForm"></buyplan>

    <editProduct v-if="id_pro" v-on:input="onResultados" :categoria="id_cat" :id_pro="id_pro" v-model="edit" />

    <v-dialog v-model="activeDelete" persistent max-width="290">
      <v-card text-center style="text-align: center !important">
        <br />
        <v-icon size="80" color="red">mdi-delete</v-icon>
        <p class="text-h5" text-center>Alerta</p>
        <v-card-text>Ests seguro que quieres eliminar permanentemente?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="cancelDelete()">
            Cancelar
          </v-btn>
          <v-btn color="red darken-1" text @click="deleteProductConfir(id_pro)">
            Si, Eliminar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>


    <v-dialog v-model="dialogListPro" max-width="550" transition="dialog-bottom-transition">
      <v-toolbar dark color="green">
        <v-toolbar-title>Elegir Producto</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn icon dark @click="dialogListPro = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-responsive class="overflow-y-auto" max-height="400">
        <v-lazy v-model="isActive" :options="{
      threshold: .5
    }" min-height="800" transition="fade-transition">
          <v-card class="grey lighten-5">
            <products v-if="cajon == 'listadoProductos'" />
          </v-card>
        </v-lazy>
      </v-responsive>


    </v-dialog>

  </div>
</template>

<script>
//import axios from "axios";
import store from '../../store'
import api from "../../models/controller";
//import enviarBoleta from "@/components/EnviarBoleta.vue";
import buyplan from "@/components/Buyplan/Home.vue";
import editProduct from "@/components/Dialogs/EditProduct.vue";
import sinregistros from "@/components/Perfil/SinRegistros";
//import AppBar from "@/components/AppBar/AppbarGeneral.vue";

import products from "@/components/UserProductos/ListProdCompPublicidad.vue";
import { mdiCrownCircle } from "@mdi/js";
import { mdiAccount } from "@mdi/js";
import { mdiCrown } from "@mdi/js";
export default {
  components: { /*enviarBoleta,*/ editProduct, sinregistros, buyplan, products /*AppBar*/ },
  data() {
    return {
      edit: false,
      cajon: null,
      id_pro: null,
      isActive: false,
      id_cat: null,
      dialogListPro: false,
      product: {},
      activeDelete: false,
      buyPro: null,
      showScheduleForm: false,
      ventas: [],
      planes: [],
      itemsProducts: [
        { title: 'Promocionar' },
        { title: 'Editar' },
        { title: 'Eliminar' },
      ],
      icons: {
        mdiCrown,
        mdiCrownCircle,
        mdiAccount,
      },
      open: [1, 2],
      search: null,
      caseSensitive: false,
      galery: []
    };
  },

  methods: {

    cancelDelete: function () {
      this.id_pro = null;
      this.activeDelete = false;
      this.id_cat = null;
    },

    elegirProducto() {
      this.dialogListPro = true;
      this.cajon = 'listadoProductos'
    },
    deleteProductConfir: function (id_pro) {

      api.eliminarProducto(id_pro).then(() => {
        this.activeDelete = false;
        this.getAllProduct();
      })
      /* api.deleteProduct(id_pro).then(() => {
         this.getPhotoProduct(id_pro).then((re) => {
           re.forEach((element) => {
             console.log("DATOS GALERY : ", element)
             this.deletePhoto(element);
           });
           this.activeDelete = false;
           this.getAllProduct();
         })
       }); */
    },

    deletePhoto: function (item) {
      let data = {
        id: item.clave,
        categoria: 'Categoria_' + this.id_cat,
      };
      api.deletePhoto(data);
    },

    getPhotoProduct: function (id_pro) {
      return new Promise((resolve) => {
        try {
          api.getGaleriaProduct(id_pro).then((result) => {
            this.galery = result.data;
            resolve(this.galery);
          });
        } catch (error) {
          console.log(error)
        }
      })
    },

    elegirOptionProducto(item, itemPro) {
      if (itemPro == 'Promocionar') {
        this.buyPro = item.id_pro;
        this.showScheduleForm = true;
      } else if (itemPro == 'Editar') {
        this.edit = true;
        this.id_pro = item.id_pro
        this.id_cat = item.id_categoria
        this.product = item;
      } else if (itemPro == 'Eliminar') {
        this.id_pro = item.id_pro;
        this.activeDelete = true;
        this.id_cat = item.id_SubCategoria;
      }
    },

    comprarPlan(item) {
      this.buyPro = item;
      this.showScheduleForm = true;
    },
    onResultados: function (item) {
      this.showScheduleForm = item;
      this.edit = false;
      this.buyPro = null;
      this.id_pro = null;
      this.id_cat = null;
    },
    deleteProduct(id_pro, idCat) {
      this.id_pro = id_pro;
      this.activeDelete = true;
      this.id_cat = idCat;
    },
    editProduct(item) {
      this.edit = true;
      this.id_pro = item.id_pro
      this.id_cat = item.id_categoria
      this.product = item;
    },

    getAllProduct: function () {
      if (store.getters.isLoggedIn) {
        var data = store.getters.getUser;
        try {
          api.getUserAllProduct(data.id_user).then((result) => {
            this.ventas = result.data;
          });
        } catch (error) {
          console.log(error);
        }
      } else {
        console.log("Hay que salir de ac");
      }
    }
  },
  computed: {
    keywords() {
      if (!this.search) return [];
      const keywords = [];
      for (const search of this.searching) {
        keywords.push(search.keyword);
      }
      return keywords;
    },
    searching() {
      if (!this.search) return this.ventas;
      const search = this.search.toLowerCase();
      return this.ventas.filter((item) => {
        const text = item.titulo.toLowerCase();
        return text.indexOf(search) > -1;
      });
    },
  },
  created() {
    if (store.getters.isLoggedIn) {
      this.getAllProduct();
    }

  },
};
</script>

<style>
.card-plan {
  display: grid;
  border-radius: 20px !important;
  background: rgb(117, 248, 117) !important;
  border: solid rgb(34, 189, 34) 2px !important;
}

.div-publicidad {
  align-self: center;
  justify-self: center;
}

.destacado {
  padding-left: 5px;
  padding-top: 2px;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 5;
  font-size: 10pt;
  width: 93px;
  height: 20px;
  color: white;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  background: green;
}
</style>