<template>
    <div>
        <v-row>
            <v-col v-for="(item, i) in publicidad.slice(0,3)" :key="i" cols="12">
                <v-card flat @click="navegar(item)" class="py-1 px-1">
                    <div class="d-flex flex-no-wrap justify-space-between">
                        <v-avatar class="rounded-xl" size="140" tile>
                            <v-img :src="item.url"></v-img>
                        </v-avatar>
                        <div style=" width: 150px;text-align: left !important;  margin-left: -10px !important;">
                            <p class="mt-3">{{ item.empresa }}</p>
                            <p class="mt-3 font-weight-thin caption"
                                style="font-size: 12pt !important; margin-top: -15px !important;">{{ item.descripcion }}</p>


                            <p class="mt-3 font-weight-light" style="font-size: 10px;">{{ item.pagina }}</p>
                        </div>


                    </div>
                </v-card>


            </v-col>
        </v-row>
    </div>
</template>

<script>
import api from "@/models/controller";
import timePublicidad from "../../events/timePublicidadMarca"
export default {
    data() {
        return {
            publicidad: []
        }
    },
    methods: {

        navegar: function (item) {
            if (item != 4) {


                if (item.linkPrincipal == 'facebook') {
                    window.location.href = `${item.facebook}`
                } else if (item.linkPrincipal == 'instagram') {
                    window.location.href = `${item.instagram}`
                } else if (item.linkPrincipal == 'whatsapp') {
                    window.location.href = `${item.whatsapp}`
                } else if (item.linkPrincipal == 'pagina') {
                    window.location.href = `${item.pagina}`
                }
            } else {
                window.location.href = `https://www.facebook.com/globaltechnologygt`
            }

        },

        reordenar(lista) {
            window.setInterval(() => {
                lista.sort(function () { return Math.random() - 0.5 });
                this.publicidad = lista;

            },10000)

          //  return this.publicidad.slice(0, 3)
        },
        allPublicidad() {
            api.allPublicidadPagada().then((res) => {
                var lista = res.data;
                timePublicidad(lista.result).then((resp) => {
                    this.publicidad = resp;
                    console.log("RESULTADO :", this.publicidad)

                    this.reordenar(this.publicidad)
                })

            })


        }
    },
    created() {
        this.allPublicidad();
    }
}
</script>