<template>
    <div style="background: white;">
        <v-dialog v-model="show" fullscreen hide-overlay transition="dialog-bottom-transition"
            style="    background: rgb(133, 126, 126) !important;">
            <v-card class="mx-auto">
                <v-toolbar dark color="green">
                    <v-btn icon dark @click="show = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>Producto No.{{ idpro }} - Plan {{ titlePlan }}</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-toolbar-items> </v-toolbar-items>
                </v-toolbar>
                <div class=" div-container">
                    <v-container class="container">
                        <div class="div-cajon">


                            <v-snackbar v-model="isForm" color="red" timeout="3000" vertical="vertical">
                                Asegurate que la foto se haya cargado correctamente.

                            </v-snackbar>
                            <h5>Completar compra</h5>
                            <!--<span class="mb-3" style="font-size: 10pt">CUENTA A DEPOSITAR #......... DEL BANCO PROMERICA
                    </span> -->
                            <br>
                            <p>Realiza tu pago en el siguiente enlace :</p>
                            <v-btn block color="blue" style="
          color: #fff;
          
          box-shadow: 1px 1px 6px 2px rgba(255, 251, 3, 0.7);" class="parpadea btn-warning mb-4" dark><a
                                    style="color:white !important" target="_blank"
                                    href="https://mallvirtualvisanet.com.gt/formulario-de-pago/1556/pliis-technology-businesses">
                                    <v-icon>mdi-credit-card-outline</v-icon>Link de PAGO</a></v-btn>
                            <h5 class="mb-3">Llena los siguientes campos</h5>

                            <v-form ref="form" class="mt-2" v-model="form" @submit.prevent="createVenta">

                                <v-text-field v-if="isges == true" dense filled rounded name="message" class="mt-1"
                                    v-model="nGestion" disabled label="No. de Gestin"></v-text-field>

                                <v-label>No. de boleta</v-label>
                                <v-text-field :rules="[rules.required]" dense filled rounded class="mt-1" v-model="nBoleta"
                                    label="Ej: 12345678" required></v-text-field>

                                <v-label>Cant. que deposita</v-label>
                                <v-text-field :rules="[
                                    () => !!precio || 'Campo requerido',
                                    () => !!precio && precio >= 25 || 'Deposito mnimo de Q25',
                                ]" @change="getCalculardias()" label="Cantidad Q." dense filled rounded type="number"
                                    prefix="Q" class="mt-1" v-model="precio"></v-text-field>

                                <v-card flat color="transparent">
                                    <v-subheader>Mnimo y mximo de das</v-subheader>

                                    <v-card-text>
                                        <v-row>
                                            <v-col class="px-4">
                                                <v-range-slider v-model="range" :max="max" :min="min" hide-details
                                                    class="align-center">
                                                    <template v-slot:prepend>
                                                        <v-text-field :value="range[0]" class="mt-0 pt-0" hide-details
                                                            single-line type="number" style="width: 60px"
                                                            @change="$set(range, 0, $event)"></v-text-field>
                                                    </template>
                                                    <template v-slot:append>
                                                        <v-text-field :value="range[1]" class="mt-0 pt-0" hide-details
                                                            single-line type="number" style="width: 60px"
                                                            @change="$set(range, 1, $event)"></v-text-field>
                                                    </template>
                                                </v-range-slider>
                                            </v-col>
                                        </v-row>
                                    </v-card-text>
                                </v-card>

                                <div class="mx-auto-photo">
                                    <photo :photoI="photo" class="interno" v-on:photoPago="onPhotos"></photo>
                                </div>
                                <p>Carga tu boleta o comprobante de pago*</p>
                                <hr>
                                <div style="background: gainsboro; padding: 10px; border-radius: 5px;">
                                    <v-row no-gutters>
                                        <v-col cols="12"><b>DETALLE DE LA COMPRA</b></v-col>
                                        <v-col cols="8">
                                            Plan {{ titlePlan }}
                                        </v-col> <v-col cols="4" class="text-right">
                                            Q.{{ precio }}
                                        </v-col>
                                        <v-col cols="8">
                                            Monedas
                                        </v-col> <v-col cols="4" class="text-right">
                                            Q.0.00
                                        </v-col>
                                        <v-col cols="8">
                                            <b> TOTAL</b>
                                        </v-col> <v-col cols="4" class="text-right">
                                            <b> Q.{{ getPrice() }}</b>
                                        </v-col>
                                    </v-row>
                                </div>
                                <!--<v-btn @click="createVenta()" color="success" outlined class="mt-5" block rounded>Confirmar
                                envio</v-btn>-->
                                <v-text-field style="display: none;" name="to_name" type="text" v-model="user.nombres"
                                    outlined></v-text-field>
                                <v-text-field name="message" style="display: none;" dense v-model="nGestion" outlined
                                    disabled></v-text-field>
                                <v-text-field style="display: none;" name="from_email" type="text" v-model="user.email"
                                    outlined></v-text-field>

                                <input value="Confirmar Envio" :disabled="!form" class="submit" type="submit" name="send" />

                                <!--<v-btn large class="mt-8" @click="createVenta()" block rounded color="success">Confirmar
                                Envio</v-btn>-->

                            </v-form>
                            <br>
                        </div>
                    </v-container>
                </div>
            </v-card>

        </v-dialog>
        <v-dialog v-model="success" persistent>

            <v-card class="mx-auto" style="text-align: center;">
                <v-icon class="mt-6" size="180" color="success">mdi-check-circle</v-icon>
                <v-card-text class="text-h6" style="color: red;">No. Gestion {{ nGestion }}</v-card-text>
                <v-card-text class="text-h6">Tu compra ha sido registrada correctamente.</v-card-text>
                <p class="text-h5">Fecha lmite </p>
                <p><b>{{ sumarDias(new Date()) }}</b></p>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="salir()">
                        Salir
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="progressBoleta" hide-overlay persistent width="300">
            <v-card color="success" dark>
                <v-card-text>
                    Espera un momento...
                    <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>

    </div>
</template>

<script>
import store from '../../store'
import veryemail from "../../models/email"
//import enviarBoleta from "@/components/EnviarBoleta.vue";
import api from '../../models/controller' //../../views/PerfilEdit/CreatePhoto.vue
import photo from "../../views/Photo/SubirPhoto.vue";
export default {
    components: {/* enviarBoleta*/  photo },
    props: {
        value: Boolean,

        idpro: null,
        titlePlan: null,
        idPlan: null,
        price: null,

    },
    data() {
        return {
            isForm: false,
            isges: false,
            precio: 25,
            progressBoleta: false,
            interval: {},
            values: 0,
            overlay: true,
            success: false,
            money: 0,
            planes: [],
            nGestion: null,
            user: null,
            nBoleta: null,
            form: false,
            photo: 'vacio',
            nTelefono: null,
            nameRules: [
                (v) => !!v || "Campo requerido",
                (v) => (v && v.length <= 20) || "Puedes escribir hasta 20 caracteres",
            ],

            rules: {
                length: len => v => (v || '') >= 25 || `Longitud de carcter no vlida, obligatorio ${len}`,
                required: value => !!value || 'Campo requerido',
            },

            min: 1,
            max: 100,
            range: [1, 5],
        }
    },
    beforeDestroy() {
        clearInterval(this.interval)
    },
    watch: {
        overlay(val) {
            val && setTimeout(() => {
                this.overlay = false
            }, 2000)
        },
    },
    computed: {
        show: {
            get() {

                return this.value;
            },
            set(value) {
                this.photo = 'vacio';
                this.$emit("input", value);
            },
        },
    },
    methods: {

        getCalculardias() {
            var toDias = 0;
            //Valor de cobro por dia
            var Qdia = 5;
            toDias = this.precio / Qdia;
            toDias = Math.round(toDias);

            if (this.precio > 0) {
                this.max = toDias;
                this.range[1] = toDias;
            }

            console.log(toDias)

            //Retorna valor dias 
            return toDias;
        },

        register: function (e) {
            console.log(e)
        },
        salir() {
            this.show = false;
            this.success = false;

        },
        getPrice() {
            return this.precio
        },
        onPhotos(item) {
            this.photo = item;
        },
        getMoney: function (id) {
            api.getMoney(id).then((result) => {
                var money = result.data;
                this.money = money.money;
            });
        },
        getPlanes: function () {
            api.getAllPlanes().then((datos) => {
                var planes = datos.data;
                planes.forEach(element => {
                    if (element.id_plan != 5 && element.id_plan != 4) {
                        this.planes.push(element);
                    }
                });
            });
        },
        sumarDias: function (fecha) {
            var dias = 0;
            if (this.idPlan == 1) {
                dias = 30;

            } else if (this.idPlan == 2) {
                dias = 15
            } else if (this.idPlan == 3) {
                dias = 7
            }
            fecha.setDate(fecha.getDate() + dias);
            return fecha.toISOString();
        },
        createVenta: function (e) {
            if (this.$refs.form.validate()) {

                /*  var dias = 0;
                  if (this.idPlan == 1) {
                      dias = 30;
                  } else if (this.idPlan == 2) {
                      dias = 15
                  } else if (this.idPlan == 3) {
                      dias = 7
                  }*/

                if (this.photo != 'vacio') {
                    this.progressBoleta = true;
                    window.setTimeout(() => {
                        var fecha = new Date();

                        let params = {
                            idCliente: this.user.id_user,
                            idUser: 2147483647,
                            tipo_comprobante: 'Ingresos',
                            num_gestion: this.nGestion,
                            num_comprobante: this.nBoleta,
                            total: this.getPrice(),
                            idPlan: 4,
                            idPro: this.idpro,
                            cantidad: 1,
                            precio: this.precio,
                            descuento: 0,
                            foto: this.photo.img,
                            idfoto: this.photo.id,
                            dias: this.getCalculardias(),
                            fecha: fecha.toISOString()
                        }

                        api.createVentaPubli(params).then(() => {
                            console.log(params)
                            console.log(e)

                            this.success = true;
                            this.progressBoleta = false;

                            this.nGestion = null;
                            this.nBoleta = null;
                            this.photo = 'vacio';
                            this.getGestion();
                            this.precio = 25;
                            veryemail.sendEmailCompraPublicidadProductos(e.target)
                        })
                    }, 3000)
                } else {
                    console.log("error foto")
                    this.isForm = true;
                    window.setTimeout(() => {
                        this.isForm = false;
                    }, 3000)

                }

            }
        },

        getdias(precio) {

            var dias = precio / 5;
            return Math.round(dias)
        },

        getGestion: function () {
            var pass = "";
            var str = "1234567890987654321";
            for (let i = 1; i <= 8; i++) {
                var char = Math.floor(Math.random() * str.length + 1);
                pass += str.charAt(char);
            }
            this.nGestion = pass;
        }
    },
    created() {
        if (store.getters.isLoggedIn) {
            var data = store.getters.getUser;
            var pass = "";
            var str = "1234567890987654321";
            for (let i = 1; i <= 8; i++) {
                var char = Math.floor(Math.random() * str.length + 1);
                pass += str.charAt(char);
            }
            this.nGestion = pass;
            this.isLogin = true;
            this.user = data;
            this.getMoney(this.user.id_user);
            //this.precio = this.price;

        }
    }
}
</script>

<style scoped>
.mx-auto-photo {
    width: 100% !important;
    padding: 10px;
    text-align: center;
}

.interno {
    width: 60%;
    margin: 0 auto;
    /*esto fue lo que se agreg*/
}



.submit {
    color: white;
    width: 100%;
    margin-top: 15px;
    background: green;
    padding: 10px;
    border-radius: 20px;
    font-size: 14pt;
}

input::placeholder {
    color: red;
    font-size: 1.2em;
    font-style: italic;
}



.div-container {
    background: ghostwhite;
}

/*https://depositphotos.com/es/photos/web-dise%C3%B1o.html?offset=180&filter=all&qview=102860656
https://depositphotos.com/es/photos/web-dise%C3%B1o.html?offset=180&filter=all&qview=119233684
https://depositphotos.com/es/photos/web-dise%C3%B1o.html?offset=540&filter=all&qview=121655530 */



@media screen and (min-width: 550px) {
    .container {
        padding-top: 0px;
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .div-cajon {
        padding: 10px;
        width: 391px;
        background: white;
      
    }
}
</style>
