<template>
    <v-container>
        <v-img class="file-upload" :src="photoNew"><input class="input-photo" type="file" ref="photo"
                accept="image/jpeg image/jpg image/png" value="" @click="$refs.photo.value = null"
                @change="uploadPhoto($event)" />
        </v-img>

        <v-btn v-if="idPhotoNew != ''" class="mx-2 img-close" small dark fab color="red" @click="deletePhoto(idPhotoNew)">
            <v-icon dark> mdi-close </v-icon>
        </v-btn>
    </v-container>
</template>
    
<script>
import store from '../../store'
import updateChatSoporte from "../../controller/updateChatSoporte";
import api from "../../models/controller";
export default {
    props: ["idUser", "idPhoto", "photoProfile"],
    data: () => ({
        photo: {},
        idPhotoNew: "",
        photoNew: "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg",
        loading: false,
    }),
    methods: {
        onInputClick: function (event) {
            event.target.value = "";
        },
        addImage: function () {
            this.photo = {
                img: "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg",
                id: "",
            };
        },

        uploadPhoto: function (e) {
            try {
                const { files } = this.$refs.photo;
                const file = files[0];
                updateChatSoporte(file).then((result) => {
                    var dataimg = result;
                    this.photoNew = dataimg.url;
                    this.idPhotoNew = dataimg.id;

                    var photo = {
                        img: dataimg.url,
                        id: dataimg.id,
                    };

                    this.enviarFotos(photo);
                });
                //this.cargarImagen(file, i);
                e.currentTarget.files = null;
            } catch (error) {
                console.log("Error ", error);
            }
        },

        deletePhoto: function (item) {
            let data = {
                id: item,
            };
            api.eliminarFotoPostChatSoporte(data).then(() => {
                this.photoNew = this.photoProfile;
                this.idPhotoNew = "";
                var photo = "vacio";
                this.enviarFotos(photo);
            });
        },

        enviarFotos: function (datos) {
            this.$emit("photo", datos);
        },
    },
    created: async function () {
        if (store.getters.isLoggedIn) {
            this.photoNew = "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg";
        } else {
            console.log("NO LOGEADO EL USUARIO");
        }
    },
};
</script>
    
<style lang="scss" scoped>
.img-input {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

}

.input-photo {
    width: 70px;
    height: 90px;
    background: red;

    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0);
}

.file-upload {
    border-radius: 10px;
    width: 150px;
    height: 150px;
    border: solid rgb(255, 255, 255) 1px;
    /*border: 1px solid #01e934;*/
    /*border-radius: 100px;*/
    overflow: hidden;
    position: relative;
}

.file-upload input {
    position: absolute;
    height: 400px;
    width: 400px;
    left: -200px;
    top: -200px;
    background: transparent;
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0);
}

.file-upload img {
    height: 200px;
    width: 200px;
    margin: 10px;
    border-radius: 8px;
    color: rgb(241, 241, 6);
}

.img-close {
    position: absolute;
    top: 15px;
    right: 90px;
    z-index: 3;
    border-radius: 50%;
}
</style>
    
    