<template>
  <v-dialog
    v-model="show"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <v-card>
      <v-toolbar dark style="background: #0eb308 !important">
        <v-btn icon dark @click="show = false">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        <v-toolbar-title>Editar Producto</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>

      <br />

      <v-expansion-panels accordion flat popout v-model="init">
        <v-expansion-panel class="border">
          <v-expansion-panel-header class="text-h6" @click="verProduct">
            Datos del Producto
          </v-expansion-panel-header>
          <v-expansion-panel-content>

            <v-form ref="form" >
              <v-list>
                <v-list-item>
                  <v-list-item-content>
                    <v-text-field
                      label="Titulo"
                      placeholder="Descripcin breve..."
                      v-model="product.titulo"
                      filled
                      rounded
                      dense
                      counter="100"
                    ></v-text-field>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    <v-text-field
                      prefix="Q."
                      label="Precio"
                      placeholder="Precio"
                      v-model="product.precio"
                      filled
                      type="number"
                      dense
                      hint="No agreges la coma."
                      persistent-hint
                    ></v-text-field>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    <v-textarea
                      outlined
                      name="input-7-4"
                      label="Descripcin"
                      v-model="product.descripcion"
                      counter="1000"
                    ></v-textarea>
                  </v-list-item-content>
                </v-list-item>
                <v-container>
                  <v-btn
        
                    color="success"
                    rounded
                    block
                    @click="updateProduct"
                    >Guardar y Actualizar</v-btn
                  >
                </v-container>
                <v-divider></v-divider>
              </v-list>
            </v-form>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel class="border">
          <v-expansion-panel-header class="text-h6" @click="verCategorias">
            Categoria
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-row align="center">
              <v-col cols="5">
                <p class="font-weight-light">Actual</p>
                <span>
                  {{ catSubActual.nombre_categoria }}
                </span>
                <hr />
                <span>
                  {{ catSubActual.nombre }}
                </span>
              </v-col>
              <v-col cols="7">
                <v-checkbox
                  v-model="activarCatSub"
                  :label="activarCatSub ? 'Cambiar' : 'No Cambiar'"
                  required
                  @change="regresar(activarCatSub)"
                ></v-checkbox>
                <v-form ref="form" :disabled="activarCatSub">
                  <v-select
                    v-model="select"
                    :items="categorias"
                    item-text="nombre_categoria"
                    item-value="id_categoria"
                    label="Seleccin"
                    hint="Categoria"
                    persistent-hint
                    return-object
                    single-line
                    @change="obtenerSubCat"
                  ></v-select>
                  <v-select
                    v-model="selectSC"
                    :items="subCategorias"
                    item-text="nombre"
                    item-value="id"
                    hint="Sub-Categoria"
                    label="Seleccin"
                    persistent-hint
                    return-object
                    single-line
                  ></v-select>

                  <v-btn
                    @click="updateCatSub"
                    class="mt-3"
                    color="success"
                    rounded
                    block
                    :disabled="activarCatSub"
                  >
                  Guardar y Actualizar</v-btn
                  >
                </v-form>
              </v-col>
            </v-row>
          </v-expansion-panel-content>
        </v-expansion-panel>
        <v-expansion-panel class="border">
          <v-expansion-panel-header class="text-h6" @click="verDireccion">
            Direccin
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-col cols="12">
              <p class="font-weight-light">Actual</p>
              <span>
                {{ direction.direccion }}
              </span>
            </v-col>
            <v-checkbox
              v-model="editDire"
              :label="editDire ? 'Cambiar' : 'No Cambiar'"
              required
              @change="desactivarDire(editDire)"
            ></v-checkbox>
            <v-form ref="form" :disabled="editDire">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    label="Direccin"
                    placeholder="Direccin"
                    v-model="direccion"
                    filled
                    counter="150"
                    type="text"
                    dense
                    hint="Direccin correcta"
                    persistent-hint
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-select
                    prepend-inner-icon="mdi-map-marker"
                    @change="obtenerMuni"
                    v-model="selectD"
                    :items="departamentos"
                    item-value="id"
                    item-text="nombre"
                    outlined
                    chips
                    flat
                    solo
                    placeholder="Departamento"
                    persistent-hint
                    return-object
                    single-line
                  ></v-select>
                </v-col>
                <v-col cols="12" md="6">
                  <v-select
                    prepend-inner-icon="mdi-map-marker"
                    v-if="municipios.length == 0"
                    disabled
                    persistent-hint
                    return-object
                    single-line
                    outlined
                    chips
                    flat
                    solo
                    placeholder="Municipio"
                  >
                  </v-select>
                  <v-select
                    label="Municipio"
                    prepend-inner-icon="mdi-map-marker"
                    v-else
                    outlined
                    persistent-hint
                    return-object
                    single-line
                    v-model="selectM"
                    :items="municipios"
                    item-value="idMunicipio"
                    item-text="nombreMunicipio"
                    chips
                    flat
                    solo
                    placeholder="Municipio"
                  ></v-select>
                </v-col>
                <v-col cols="12">
                  <v-btn
                    @click="updateDire"
                    class="mt-0 mb-5"
                    color="success"
                    rounded
                    block
                    :disabled="editDire"
                  >
                  Guardar y Actualizar</v-btn
                  >
                </v-col>
              </v-row>
            </v-form>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel class="border">
          <v-expansion-panel-header class="text-h6" @click="verGaleria">
            Galeria de fotos
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-row>
              <v-col
                v-for="(n, i) in galeria"
                :key="i"
                class="rounded-b-0 mx-auto"
                cols="4"
                md="4"
              >
                <v-img
                  :src="n.photo"
                  :lazy-src="n.photo"
                  aspect-ratio="1"
                  class="grey lighten-2 rounded-b-0"
                >
                  <template v-slot:placeholder>
                    <v-row
                      class="fill-height ma-0"
                      align="center"
                      justify="center"
                    >
                      <v-progress-circular
                        indeterminate
                        color="grey lighten-5"
                      ></v-progress-circular>
                    </v-row>
                  </template>
                </v-img>
                <v-btn
                  block
                  class="mt-1"
                  outlined
                  color="red"
                  @click="deletePhoto(n)"
                  >Eliminar</v-btn
                >
              </v-col>
            </v-row>
            <photos
              v-on:photos="onPhotos"
              :listaphotos="this.listaphotos"
              :categoria="categoria"
            ></photos>
            <v-btn block dark class="rounded" color="green" @click="submitImage"
              >SUBIR FOTOS</v-btn
            >
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>

      <v-snackbar
        :timeout="3000"
        :multi-line="multiLine"
        rounded="pill"
        absolute
        centered
        color="success"
        v-model="snackbar"
      >
        {{ text }}
        <template v-slot:action="{ attrs }">
          <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
            Ok
          </v-btn>
        </template>
      </v-snackbar>
    </v-card>
  </v-dialog>
</template>
<script>
//import axios from "axios";
import api from "../../models/controller";
import { mdiUpdate } from "@mdi/js";
import { mdiDelete } from "@mdi/js";
import { mdiTrayArrowUp } from "@mdi/js";
import photos from "../../components/AgregarProducto/CargaFotos.vue";
import { gmapApi } from "vue2-google-maps";
export default {
  props: { value: Boolean, id_pro: null, categoria: null },
  components: { photos },
  data() {
    return {
      init: true,
      listaphotos: [],
      photos: [],
      geocoder: null,
      departamentos: [],
      municipios: [],
      direccion: null,
      progress: 0,
      catSubActual: {},
      select: {},
      selectSC: {},
      direction: {},
      selectD: {},
      selectM: {},
      activar: true,
      editDire: true,
      activarCatSub: true,
      dialog: false,
      notifications: false,
      categorias: [],
      sub: [],
      subCategorias: [],
      sound: true,
      multiLine: true,
      text: "",
      snackbar: false,
      vertical: true,
      widgets: false,
      galeria: [],
      product: {},
      imgData: {
        img: "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg",
        id: "",
      },
      icons: {
        mdiDelete,
        mdiUpdate,
        mdiTrayArrowUp,
      },
      imagenMiniatura:
        "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg",
      progreso: null,
    };
  },
  methods: {
    onPhotos: function (item) {
      console.log("LISTADO DE FOTOS A SUBIR NUEVAS : ", item);
      this.photos = item;
    },
    submitImage: function () {
      for (let i = 0; i < this.photos.length; i++) {
        let photo = {
          id_pro: this.id_pro,
          clave: this.photos[i].id,
          photo: this.photos[i].img,
        };
        api
          .createPhotoGaleria(photo, {
            onUploadProgress: (uploadEvent) => {
              this.progreso = Math.round(
                (uploadEvent.loaded / uploadEvent.total) * 100
              );
            },
          })
          .then(() => {
            this.progreso = null;
            this.updatePrincipalGaleria();
          });
      }
      this.listaphotos = [];
      /*
      console.log(imgData);
      let photo = {
        id_pro: this.id_pro,
        clave: imgData.id,
        photo: imgData.img,
      };
      api
        .createPhotoGaleria(photo, {
          onUploadProgress: (uploadEvent) => {
            this.progreso = Math.round(
              (uploadEvent.loaded / uploadEvent.total) * 100
            );
          },
        })
        .then(() => {
          this.progreso = null;
          (this.imgData = {
            img: "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg",
            id: "",
          }),
            this.updatePrincipalGaleria();
        });*/
    },
    obtener_imagen: function (e) {
      let file = e.target.files[0];
      this.cargarImagen(file);
      let foto = new FormData();
      foto.append("image", file);
      foto.append("categoria", this.categoria);
      if (navigator.userAgentData.platform) {
        api.createPhotoFirebase(foto).then((datafoto) => {
          let dataimg = datafoto.data;
          let urlimagen = dataimg.data;
          this.imgData = {
            img: urlimagen.url,
            id: urlimagen.id_photo,
          };
        });
      }
    },
    cargarImagen(file) {
      let reader = new FileReader();
      reader.onload = (y) => {
        this.imagenMiniatura = y.target.result;
        this.imgData = {
          img: this.imagenMiniatura,
        };
      };
      reader.readAsDataURL(file);
    },

    deleteFirebasePhoto: function (n) {
      let data = {
        id: n,
        categoria: this.categoria,
      };
      api.deletePhoto(data).then(() => {
        this.imgData = {
          img: "https://icon-library.com/images/add-image-icon-png/add-image-icon-png-14.jpg",
          id: "",
        };
      });
    },
    deletePhoto: function (n) {
      let data = {
        id: n.clave,
        categoria: this.categoria,
      };
      api.deletePhoto(data).then(() => {
        api.deletePhotoGaleria(n.id_photo).then(() => {
          this.updatePrincipalGaleria();
        });
      });
    },
    updatePhoto: function (n) {
      console.log("UPDATE FOTO : ", n);
    },
    updateDire: function () {
      api
        .updateProductByDireccion(
          this.id_pro,
          this.selectM.idMunicipio,
          this.direccion
        )
        .then(() => {
          this.geocoder.geocode(
            {
              address: this.selectM.nombreMunicipio + " " + this.selectD.nombre,
            },
            (results, status) => {
              if (status == "OK") {
                // this.address = results[0].formatted_address;
                let tamanio = results[0].address_components.length;
                let data = {
                  id_pro: this.id_pro,
                  dire:
                    this.selectM.nombreMunicipio +
                    ", " +
                    this.selectD.nombre +
                    ", " +
                    results[0].address_components[tamanio - 1].long_name,
                  lat: results[0].geometry.location.lat(),
                  lng: results[0].geometry.location.lng(),
                };

                api.updateUbicacion(data);
              }
            }
          ); //returns nothing!!

          this.editDire = true;
        });
    },
    verDireccion: function () {
      this.departamentos = [];
      this.municipios = [];
      api.getProductByDireccion(this.id_pro).then((dire) => {
        this.direction = {
          direccion:
            dire.data[0].direccion +
            ", " +
            dire.data[0].Municipio +
            ", " +
            dire.data[0].Departamento,
          idDepa: dire.data[0].idDepa,
          idMuni: dire.data[0].idMuni,
          muni: dire.data[0].Municipio,
          depa: dire.data[0].Departamento,
          dire: dire.data[0].direccion,
        };
        this.direccion = dire.data[0].direccion;
        this.selectD = {
          id: dire.data[0].idDepa,
          nombre: dire.data[0].Departamento,
        };

        this.selectM = {
          idMunicipio: dire.data[0].idMuni,
          nombreMunicipio: dire.data[0].Municipio,
        };
        api.getDepartamento().then((result) => {
          var depa = result.data;
          depa.forEach((element) => {
            this.departamentos.push({
              nombre: element.nombreDepartamento,
              id: element.idDepartamento,
            });
          });
          //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU

          api.getMunicipiosDepa(dire.data[0].idDepa).then((munis) => {
            this.municipios = munis.data;
          });
        });
        /* axios
          .get("https://vendeloyaglobal.herokuapp.com/api/departamentos")
          .then((result) => {
            var depa = result.data;
            depa.forEach((element) => {
              this.departamentos.push({
                nombre: element.nombreDepartamento,
                id: element.idDepartamento,
              });
            });
            //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU

            api.getMunicipiosDepa(dire.data[0].idDepa).then((munis) => {
              this.municipios = munis.data;
            });
          });*/
      });
    },

    obtenerMuni: function (id) {
      this.municipios = [];
      api.getMunicipiosDepa(id.id).then((munis) => {
        this.municipios = munis.data;
      });
    },
    obtenerSubCat: function (e) {
      this.subCategorias = [];

      api.getSubCategoriaByProduct(e.id_categoria).then((resultsub) => {
        this.subCategorias = resultsub.data;
      });
      /*
      axios
        .get(
          "https://vendeloyaglobal.herokuapp.com/api/subCategoria/" +
            e.id_categoria
        )
        .then((resultsub) => {
          this.subCategorias = resultsub.data;
        });*/
    },
    updateCatSub: function () {
      this.progress = true;
      api
        .updateByProductCatSub(
          this.id_pro,
          this.select.id_categoria,
          this.selectSC.id
        )
        .then((result) => {
          if (result.statusText == "OK") {
            this.progress = false;
            this.text = "  Datos actualizados correctamente!";
            this.activar = true;
            this.snackbar = true;
          }
        });
    },
    regresar: function (activarCatSub) {
      if (activarCatSub == true) {
        api
          .getSubCategoriaByProduct(this.catSubActual.id_categoria)
          .then((resultsub) => {
            this.subCategorias = resultsub.data;

            this.selectSC = {
              nombre: this.catSubActual.nombre,
              id: this.catSubActual.id,
            };
            this.select = {
              nombre_categoria: this.catSubActual.nombre_categoria,
              id_categoria: this.catSubActual.id_categoria,
            };
          });
        /*axios
          .get(
            "https://vendeloyaglobal.herokuapp.com/api/subCategoria/" +
              this.catSubActual.id_categoria
          )
          .then((resultsub) => {
            this.subCategorias = resultsub.data;

            this.selectSC = {
              nombre: this.catSubActual.nombre,
              id: this.catSubActual.id,
            };
            this.select = {
              nombre_categoria: this.catSubActual.nombre_categoria,
              id_categoria: this.catSubActual.id_categoria,
            };
          });*/
      }
    },
    desactivarDire: function (editDire) {
      if (editDire == true) {
        this.selectD = {
          id: this.direction.idDepa,
          nombre: this.direction.depa,
        };
        this.direccion = this.direction.dire;

        api.getMunicipiosDepa(this.direction.idDepa).then((munis) => {
          this.municipios = munis.data;
          this.selectM = {
            idMunicipio: this.direction.idMuni,
            nombreMunicipio: this.direction.muni,
          };
        });
      }
    },
    verCategorias: function () {
      this.subCategorias = [];
      this.categorias = [];
      api.getProductByCatSub(this.id_pro).then((resultCat) => {
        this.select = {
          nombre_categoria: resultCat.data[0].categoria,
          id_categoria: resultCat.data[0].id_categoria,
        };
        this.selectSC = {
          nombre: resultCat.data[0].subCategoria,
          id: resultCat.data[0].id_SubCategoria,
        };

        this.catSubActual = {
          nombre_categoria: resultCat.data[0].categoria,
          nombre: resultCat.data[0].subCategoria,
          id_categoria: resultCat.data[0].id_categoria,
          id: resultCat.data[0].id_SubCategoria,
        };

        api
          .getSubCategoriaByProduct(resultCat.data[0].id_categoria)
          .then((resultsub) => {
            //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU
            this.subCategorias = resultsub.data;
            api.getAllCategorias().then((result) => {
              //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU
              this.categorias = result.data;
            });
          });
        /* axios
          .get(
            "https://vendeloyaglobal.herokuapp.com/api/subCategoria/" +
              resultCat.data[0].id_categoria
          )
          .then((resultsub) => {
            //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU
            this.subCategorias = resultsub.data;

            api.getAllCategorias().then((result) => {
                //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU
                this.categorias = result.data;
            })

            axios
              .get("https://vendeloyaglobal.herokuapp.com/api/categorias")
              .then((result) => {
                //ENVIAMOS TODOS LOS ARREGLOS FINALES AL ARRAY FINAL DEL MENU
                this.categorias = result.data;
              });
          });*/
      });
    },
    updateProduct: function () {
      api
        .updateByProduct(
          this.product.id_pro,
          this.product.titulo,
          this.product.descripcion,
          this.product.precio
        )
        .then((result) => {
          if (result.statusText == "OK") {
            this.text = "  Datos actualizados correctamente!";
            this.activar = true;
            this.snackbar = true;
          }
        });
    },

    verProduct: function () {
      if (localStorage.getItem("user")) {
        api.getProductByIdEdit(this.id_pro).then((product) => {
          this.product = product.data[0];
        });
      }
    },
    verGaleria: function () {
      this.galeria = [];
      if (localStorage.getItem("user")) {
        api.getGaleriaProduct(this.id_pro).then((result) => {
          // console.log("Galeria: ", result.data);
          this.galeria = result.data;
        });
      }
    },
    updatePrincipalGaleria: function () {
      this.galeria = [];
      if (localStorage.getItem("user")) {
        api.getGaleriaProduct(this.id_pro).then((result) => {
          //
          this.galeria = result.data;
          let data = {
            id_pro: this.id_pro,
            photo: this.galeria[0].photo,
          };
          api.updatePhotoProduct(data);
        });
        /*axios
          .get(
            `https://vendeloyaglobal.herokuapp.com/api/galeria/${this.id_pro}`
          )
          .then((result) => {
            //
            this.galeria = result.data;
            let data = {
              id_pro: this.id_pro,
              photo: this.galeria[0].photo,
            };
            api.updatePhotoProduct(data);
          });*/
      }
    },
  },
  computed: {
    google: gmapApi,
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
        this.init=-1;
      },
    },
  },
  mounted() {
    this.$gmapApiPromiseLazy().then(() => {
      if (this.google) {
        this.geocoder = new this.google.maps.Geocoder();
      }
    });
    this.SubCategoriaproductos = false;
  },

  created() {
    console.log("CATEGORIA : ", this.categoria, this.id_pro);
  },
};
</script>

<style>
.border {
  border-top: none !important;
  border-left: none !important;
  border-right: none !important;
}

.button-file {
  border-radius: 3px;
}
.file-upload {
  border-radius: 3px;
  width: 100%;
  /*border: 1px solid #01e934;*/
  /*border-radius: 100px;*/
  overflow: hidden;
  position: relative;
}
.file-upload input {
  position: absolute;
  height: 400px;
  width: 400px;
  left: -200px;
  top: -200px;
  background: transparent;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
