<template>
  <div>
    <AppBarCategoria
      v-on:result_subCat="getProducts"
      v-on:resultados="onFilter"
      v-on:searchProducts="onSearch"
      v-on:result_price="onRangePrice"
      :title="folder"
      :categoria="id"
    />
    <!--<v-breadcrumbs :items="items" large></v-breadcrumbs>-->
    <!-----------------------FILTROS DE ESTA VISTA---------------------->
    <br />
    <!--<span class="mt-4 ml-3">Buscar por rango de precio:</span>
    <filterprice /> -->
    <v-btn text class="text-truncate text-sm-body-2" @click="activarUbi"
      ><v-icon color="blue">{{ icons.mdiMapMarker }}</v-icon>
      <a
        class="d-inline-block text-truncate mt-1 font-weight-light"
        style="max-width: 240px; font-size: 8.5pt"
      >
        {{ address }}
      </a>
    </v-btn>

    <meubication
      :cord="center"
      :lat="center.lat"
      :lng="center.lng"
      :address="address"
      v-model="activarMapa"
      v-on:filterUbi="onUbicacion"
    />
    <!--------------------------------------------->
    <div class="container">
      <h4 class="h-3 mt-2">Resultados</h4>
      <div
        v-if="sinProductos == 'Vacio'"
        class=" contenedor rounded-lg text-center mx-auto h-100 p-6"
      >
        <v-icon
          size="70"
          aria-label="My Account"
          role="img"
          color="red"
          aria-hidden="false"
        >
          {{ icons.mdiTagSearchOutline }}</v-icon
        >
        <br />
        <h6>
          <strong class="text-h4 font-weight-bolder">Ops, </strong> lo
          sentimos. No se han encontrado coincidencias para tu busqueda.
        </h6>
        <br />
        <b-row>
          <b-col
            v-for="item in [0, 1]"
            :key="item"
            cols="6"
            sm="4"
            md="3"
            lg="3"
            xl="3"
          >
            <v-sheet
              :color="`grey ${theme.isDark ? 'darken-2' : 'lighten-4'}`"
              class="pa-1"
              style="border-radius: 3px"
              ><v-skeleton-loader
                class="mx-auto"
                max-width="300"
                type="card"
              ></v-skeleton-loader>
            </v-sheet>
          </b-col>
        </b-row>
      </div>
      <div v-else>
        <v-row v-if="!searching" no-gutters>
          <v-col
            cols="6"
            sm="4"
            md="3"
            lg="3"
            xl="3"
            v-for="item in productos"
            :key="item.id_pro"
          >
            <v-card
              style="border-radius: 2px; box-shadow: none !important"
              max-width="350"
              :loading="loading"
              class="mx-auto pa-1"
              @click="verDetalle(item.id_pro)"
            >
              <v-img
                style="border-radius: 2px"
                class="mb-1"
                v-bind:src="item.thumbnail"
                height="225px"
              >
                <v-app-bar style="widht: 100%" flat color="rgba(0, 0, 0, 0)">
                  <div @click="anunciar(item.id_pro)" v-if="idUser == item.id_user" class="flag destaca">DESTACA YA</div>
              <div v-else class="flag">COMPRALO YA</div>
              <v-spacer></v-spacer>

              <div class="backIcon"> <v-icon large :color="item.color" role="img" aria-hidden="false">
                  {{ icons.mdiCrownCircle }}
                </v-icon></div>
                </v-app-bar>
              </v-img>
              <span
                class="mt-2 margin"
                style="font-size: 12pt; font-weight: bolder"
                v-text="'Q.' + item.precio"
              >
              </span>
              <br />
              <div
                style="max-width: 370px; font-size: 12pt"
                class="mb-2 margin-title text-truncate"
              >
                {{ item.titulo }}
              </div>
              <v-btn class="mb-2" @click="anunciar(item.id_pro)" small dark color="rgb(11, 197, 5)" block
            v-if="idUser == item.id_user"> <v-icon class="mb-0" size="20" color="white">mdi-bullhorn</v-icon> DESTACA
            YA</v-btn>
            </v-card>
          </v-col>
        </v-row>
        <v-row v-else no-gutters>
          <v-col
            cols="6"
            sm="4"
            md="3"
            lg="3"
            xl="3"
            v-for="item in searching"
            :key="item.id_pro"
          >
            <v-card
              style="border-radius: 2px; box-shadow: none !important"
              max-width="350"
              :loading="loading"
              class="mx-auto pa-1"
              @click="verDetalle(item.id_pro)"
            >
              <v-img
                style="border-radius: 2px"
                class="mb-1"
                v-bind:src="item.thumbnail"
                height="225px"
              >
                <v-app-bar style="widht: 100%" flat color="rgba(0, 0, 0, 0)">
                  <div v-if="idUser == item.id_user" class="flag">DESTACA YA</div>
              <div v-else class="flag">COMPRALO YA</div>
              <v-spacer></v-spacer>
              <div class="backIcon"> <v-icon large :color="item.color" role="img" aria-hidden="false">
                  {{ icons.mdiCrownCircle }}
                </v-icon></div>
                </v-app-bar>
              </v-img>
              <span
                class="mt-2 margin"
                style="font-size: 12pt; font-weight: bolder"
                v-text="'Q.' + item.precio"
              >
              </span>
              <br />
              <div
                style="max-width: 370px; font-size: 12pt"
                class="mb-2 margin-title text-truncate"
              >
                {{ item.titulo }}
              </div>
              <v-btn class="mb-2" @click="anunciar(item.id_pro)" small dark color="rgb(11, 197, 5)" block
            v-if="idUser == item.id_user"> <v-icon class="mb-0" size="20" color="white">mdi-bullhorn</v-icon> DESTACA
            YA</v-btn>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </div>
    <detalleProducto :product="product" v-model="showScheduleForm" />
    <login v-model="isLogin" />
    <buyplan v-on:input="onResultados" v-if="buyPro" :product="buyPro" v-model="showScheduleForm1"></buyplan>

    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </div>
</template>
<script>
import api from "../models/controller";
import { mdiAccount } from "@mdi/js";
import { mdiFilterMenu } from "@mdi/js";
import { mdiTagSearchOutline } from "@mdi/js";
import { mdiCrown } from "@mdi/js";
import { mdiTagSearch } from "@mdi/js";
import { mdiBookSearch } from "@mdi/js";
import { mdiTuneVariant } from "@mdi/js";
import meubication from "@/components/Mapa/UbiCategoria";
import { mdiMapMarker } from "@mdi/js";
import detalleProducto from "@/components/DetalleProducto.vue";
import login from "@/components/Login/Login.vue";
import { gmapApi } from "vue2-google-maps";
import { mdiCrownCircle } from "@mdi/js";
import store from "@/store"
//import busquedaCategoria from "@/components/Filtros/Categorias.vue";
//import filterprice from "@/components/Filtros/FilterPrice.vue";
import { mdiArrowLeftCircle } from "@mdi/js";


import buyplan from "@/components/Buyplan/Home.vue";
import AppBarCategoria from "@/components/AppBar/AppBarCategoria.vue";
//import bottomSheet from "@/components/Filtros/BottomSheets.vue";
export default {
  name: "ProductosCategorias",
  components: {
    detalleProducto,
    login,    buyplan,
    /*busquedaCategoria,filterprice, */
    AppBarCategoria,
    meubication,
  },
  data() {
    return {
      idUser: null,
      buyPro: null,
      showScheduleForm1: false,
      address: "Mi ubicacin",
      activarMapa: false,
      geocoder: null,
      title: "Comprar",
      filter: "ubication",
      km: 10,
      center: {},
      overlay: false,
      search: null,
      menu: [],
      maxHeight: "90%",
      clickToClose: true,
      id_catego: null,
      activeBottom: false,
      product: {},
      showScheduleForm: false,
      isLogin: false,
      sinProductos: null,
      electionRangeSubConsulta: "Categoria",
      selecciocat: "",
      rating: 4.5,
      loading: false,
      SubCategoriaproductos: false,
      productos: [],
      sinResultados: false,
      nombre_subCategoria: "",
      subcategoria: [],
      select: { title: "Seleccionar", id: null },
      icons: {
        mdiCrown,
        mdiCrownCircle,
        mdiAccount,
        mdiFilterMenu,
        mdiTagSearchOutline,
        mdiTagSearch,
        mdiBookSearch,
        mdiTuneVariant,
        mdiArrowLeftCircle,
        mdiMapMarker,
      },
      id_pro: null,
      id: null,
      folder: null,
      items: [
        {
          text: "Inicio",
          disabled: false,
          href: "/",
        },
        {
          text: `${this.folder}`,
          disabled: false,
          href: `/categoria/${this.folder}/${this.id}`,
        },
      ],
    };
  },
  inject: {
    theme: {
      default: { isDark: false },
    },
  },
  methods: {
        
    anunciar: function (item) {
      this.buyPro = item;
      this.showScheduleForm1 = true;
    },
    onUbicacion(dire) {
      this.center = {
        lat: dire.lat,
        lng: dire.lng,
      };
      this.km = dire.km;
      this.address = dire.dire;
      // this.center

      this.SubCategoriaproductos = true;
      this.productos = [];
      api
        .getFilterCatProducts(
          this.center.lat,
          this.center.lng,
          this.km,
          this.id
        )
        .then((result) => {
          var datos = result.data;
          if (datos.data.length > 0) {
            this.sinResultados = false;
            this.nombre_subCategoria = datos.data[0].nombre_subCategoria;
            this.productos = datos.data;

            this.sinProductos = "Lleno";
          } else {
            this.sinProductos = "Vacio";
            this.sinResultados = true;
            this.nombre_subCategoria =
              "Ops, lo sentimos. No se han encontrado coincidencias para tu busqueda.";
          }
        });
    },
    activarUbi: function () {
      this.$gmapApiPromiseLazy().then(() => {
        navigator.geolocation.getCurrentPosition(async (position) => {
          this.center = {
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          };
          this.activarMapa = !this.activarMapa;
        });
      });
    },
    onSearch: function (data) {
      console.log(data);
      this.search = data;
    },
    retrocer() {
      this.$router.go(-1);
    },
    verSeleccion(item) {
      console.log("DATOS QUE SELECCIONO : ", item);
    },
    openSubCat() {
      this.$refs.myBottomSheet.open();
    },
    open() {
      this.$refs.myBottomSheet.open();
    },
    close() {
      this.$refs.myBottomSheet.close();
    },
    onFilter: function (item) {
      //console.log("datos vienen filtro : ", item)

      // this.close();
      this.id_catego = item;

      const path = `/categoria/${this.id_catego.nombre_categoria}/${this.id_catego.id_categoria}`;
      if (this.$route.path !== path) this.$router.push(path);
    },
    islogen(id) {
      if (window.localStorage.getItem("user")) {
        this.isLogin = false;
        this.$router.push({
          path: `/detalleProducto/${id}`,
          id_pro: { id },
        });
      } else {
        this.isLogin = true;
        console.log("NO LOGEADO EL USUARIO");
      }
    },
    verProduct(e) {
      //console.log("Datos del producto :", e);
      this.product = e;
      this.showScheduleForm = true;
    },
    verDetalle(id) {

      if (store.getters.isLoggedIn) {
        let user = store.getters.getUser;
      
        let vista = {
          id_user: user.id_user,
          id_pro: id,
          like: 0,
        };
        api.createView(vista).then(() => {
        });
      }
      // this.$router.push("/detalleProducto", {id_pro: id});
      this.$router.push({
        path: `/detalleProducto/${id}`,
        id_pro: { id },
      });
    },
    getProducts(e) {
      this.electionRangeSubConsulta = "";
      if (e != "Todos") {
        if (e.id) {
          this.id_pro = e.id;
          this.electionRangeSubConsulta = "SubCategoria";
        } else {
          this.id_pro = e;
          this.electionRangeSubConsulta = "Categoria";
        }
      } else {
        this.electionRangeSubConsulta = "Categoria";
        this.getAllProducts(this.id);
      }

      this.SubCategoriaproductos = true;
      this.productos = [];
      api
        .getFilterSubCatProducts(
          this.center.lat,
          this.center.lng,
          this.km,
          this.id_pro
        )
        .then((result) => {
          var datos = result.data;
          if (datos.data.length > 0) {
            this.sinResultados = false;
            this.nombre_subCategoria = datos.data[0].nombre_subCategoria;
            this.productos = datos.data;

            this.sinProductos = "Lleno";
          } else {
            this.sinProductos = "Vacio";
            this.sinResultados = true;
            this.nombre_subCategoria =
              "Ops, lo sentimos. No se han encontrado coincidencias para tu busqueda.";
          }
        });
    },

    obtenerSubCateborias(id) {
      api.getProductBySubCategoria(id).then((result) => {
        this.subcategoria = result.data;
        this.id_pro = this.subcategoria[0].id;
        this.getProducts(this.id_pro);
      });
    },

    getAllProducts(idcat) {
      this.SubCategoriaproductos = true;
      this.productos = [];
      api
        .getFilterCatProducts(this.center.lat, this.center.lng, this.km, idcat)
        .then((result) => {
          var datos = result.data;
          if (datos.data.length > 0) {
            this.sinResultados = false;
            this.nombre_subCategoria = datos.data[0].nombre_subCategoria;
            this.productos = datos.data;

            this.sinProductos = "Lleno";
          } else {
            this.sinProductos = "Vacio";
            this.sinResultados = true;
            this.nombre_subCategoria =
              "Ops, lo sentimos. No se han encontrado coincidencias para tu busqueda.";
          }
        });
    },

    onRangePrice(range) {

      if (this.electionRangeSubConsulta == "Categoria") {
        this.SubCategoriaproductos = true;
        this.productos = [];
        api
          .getFilterPriceCatProducts(
            this.center.lat,
            this.center.lng,
            this.km,
            this.id,
            range.min,
            range.max
          )
          .then((result) => {
            var datos = result.data;
            if (datos.data.length > 0) {
              this.sinResultados = false;
              this.nombre_subCategoria = datos.data[0].nombre_subCategoria;
              this.productos = datos.data;

              this.sinProductos = "Lleno";
            } else {
              this.sinProductos = "Vacio";
              this.sinResultados = true;
              this.nombre_subCategoria =
                "Ops, lo sentimos. No se han encontrado coincidencias para tu busqueda.";
            }
          });
      } else {
        this.SubCategoriaproductos = true;
        this.productos = [];
        api
          .getFilterPriceSubCatProducts(
            this.center.lat,
            this.center.lng,
            this.km,
            this.id_pro,
            range.min,
            range.max
          )
          .then((result) => {
            var datos = result.data;
            if (datos.data.length > 0) {
              this.sinResultados = false;
              this.nombre_subCategoria = datos.data[0].nombre_subCategoria;
              this.productos = datos.data;

              this.sinProductos = "Lleno";
            } else {
              this.sinProductos = "Vacio";
              this.sinResultados = true;
              this.nombre_subCategoria =
                "Ops, lo sentimos. No se han encontrado coincidencias para tu busqueda.";
            }
          });
      }
    },
  },
  computed: {
    google: gmapApi,
    searching() {
      if (!this.search) return this.productos;

      const search = this.search.toLowerCase();

      return this.productos.filter((item) => {
        const text = item.titulo.toLowerCase();

        return text.indexOf(search) > -1;
      });
    },
  },

  created() {
    this.$gmapApiPromiseLazy().then(() => {
      navigator.geolocation.getCurrentPosition((position) => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        };
        this.id = this.$route.params.id;
        this.folder = this.$route.params.categoria;
        this.$watch(
          () => this.$route.params,
          (toParams) => {
            this.id = toParams.id;
            this.folder = toParams.categoria;
            // this.obtenerSubCateborias(this.id);
            this.getAllProducts(this.id);
            this.electionRangeSubConsulta = "Categoria";
          }
        );
        //this.obtenerSubCateborias(this.id);
        this.getAllProducts(this.id);
        this.electionRangeSubConsulta = "Categoria";

        try {
          this.geocoder.geocode(
            { location: this.center },
            (results, status) => {
              if (status == "OK") {
                this.address = results[0].formatted_address;
                //console.log("Direccin al entrar", results[0]);
              }
            }
          ); //returns nothing!!
        } catch (error) {
          console.log(error.message);
        }
      });
    });

    
    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
      this.idUser = data.id_user;
    }
  },
  beforeCreate() {
    this.overlay = true;
  },
  watch: {
    overlay(val) {
      val &&
        setTimeout(() => {
          this.overlay = false;
        }, 3000);
    },
  },
  mounted() {
    this.$gmapApiPromiseLazy().then(() => {
      if (this.google) {
        this.geocoder = new this.google.maps.Geocoder();
      }
    });
  },
};
</script>


<style scoped>
.margin {
  padding-left: 5px;
  margin-top: 5px;
}

.margin-title {
  padding-left: 5px;
  margin-top: -5px;
  margin-bottom: 8px;
}

.flag {
  font-size: 12px;
  position: absolute;
  top: 5px;
  left: 0px;
  color: white;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  padding-left: 5px;
  padding-right: 5px;
  background: orange;
}

.backIcon {
  right: 5px;
  position: absolute;
  top: 5px;
  background: white;
  border-radius: 55%;
}

.destaca {
  z-index: 10;
}
</style>

