<template>
  <v-container>
    <div class="externo">
      <div class="interno">
        <v-list-item>
          <v-list-item-icon>
            <v-icon color="success">
              mdi-email
            </v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <p style="font-size: 16pt;">Envia tus quejas o sugerencias a este correo:</p>
            <div>administracion@vendeloyaglobal.com</div>
            <v-list-item-subtitle>Correo electrnico <b v-if="isCopy"
                style="margin-left: 10px; color:green; font-weight: bolder;">Copiado</b></v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-icon>
            <v-btn icon color="indigo" @click="copyEmail('administracion@vendeloyaglobal.com')">
              <v-icon color="success">
                mdi-content-copy
              </v-icon>
            </v-btn>
          </v-list-item-icon>
        </v-list-item>
      </div>
    </div>
    <v-btn rounded-lg rounded class="btn-icon" color="success" @click="irChat()">
      <v-icon size="30">mdi-chat</v-icon>
    </v-btn>
  </v-container>
</template>
<script>

export default {
  data() {
    return {
      isCopy: false

    }
  },
  methods: {
    irChat: function () {
      this.$router.push("/chatsoporte").catch(() => { });
    },

    async copyEmail(mytext) {
      try {
        this.isCopy = true;
        await navigator.clipboard.writeText(mytext);
        window.setTimeout(() => {
          this.isCopy = false
        }, 2000)
        // alert('Copied');
      } catch ($e) {
        //alert('Cannot copy');
      }
    }
  },
  created() {
  }
}
</script>

<style>
.btn-icon {
  height: 50px !important;
  position: absolute;
  bottom: 70px;
  right: 10px;
}

.externo {
  width: 100%;
  height: 100vh;
  align-items: center;
  display: flex;
  justify-content: center;
}
.interno {
}
</style>