<template>
  <div class="mr-2 mt-2 ma-0 mb-2">
    <v-card class="mx-auto">
      <v-img :src="product.thumbnail" height="200px">
        <template v-slot:placeholder>
          <v-row class="fill-height ma-0" align="center" justify="center">
            <v-progress-circular
              indeterminate
              color="grey lighten-5"
            ></v-progress-circular>
          </v-row>
        </template>
      </v-img>

      <v-card-title class="text-truncate ml-2 mr-4 ma-a">
        <a class="span-margin">{{ product.titulo }}</a>
      </v-card-title>
    </v-card>
  </div>
</template>

<script>
import api from "../../models/controller";
export default {
  props: { id: null },
  data() {
    return {
      product: {},
    };
  },
  methods: {

  },
  created() {
    console.log("ID PREGINTA : ", this.id);
    if (localStorage.getItem("user")) {
      //OBTENER LOS CHAT
      api.getProductById(this.id).then((result) => {
        console.log("RESUTLADOS del producto : ", result.data);
        this.product = result.data[0];
      });
    }
  },
};
</script>

<style>
.btn-size {
  font-size: 6pt;
}

.span-size {
  font-size: 8.5pt;
}

.span-margin {
  font-size: 12.5pt;
}

.ma-a {
  margin: 0px;
  padding: 0px;
}
</style>