<template>
  <div class="container-background" ref="messagesContainer">
    <div v-for="item in messages" :key="item.id">
      <div class="box-left msgTo" v-if="item.from_id != idUser">
        <product v-if="item.id_questions == 1" :id="item.id_pro" />
        <div class="text-left">
          <p class="mensaje">
            {{ item.mensaje }}
            <br />
            <answer
              class="mt-2"
              :id_pro="item.id_pro"
              :fromId="item.from_id"
              :id="item.id_questions"
            />
            <br />
            <small class="small-font" v-text="getDate(item.fecha)"></small>
          </p>
        </div>
      </div>

      <!-- CAJON LADO DERECHO-->
      <div class="box-rigth msg" v-else>
        <div class="text-rigth">
          <p class="mensaje">
            {{ item.mensaje }}
            <br />
            <small class="small-font" v-text="getDate(item.fecha)"></small>
          </p>
        </div>
      </div>
    </div>
    <br />
    <br />
    <br />
    <br />
    <br />
  </div>
</template>
<script>
import io from "socket.io-client";
import api from "../../models/controller";
import answer from "@/components/ChatBot/BotAnswer.vue";
import product from "@/components/ChatBot/BotProduct.vue";
import moment from "moment";
export default {
  components: { answer, product },
  props: { id_user: null },
  data() {
    return {
      socket: io("https://vendeloyaglobal.herokuapp.com"),
      messages: [],
      idUser: null,
    };
  },
  updated() {
    //this.$nextTick(() => this.scrollToEnd());
  },
  methods: {
    sendMessage() {
      if (this.message != "") {
        this.socket.emit("nuevoChat", {
          to_id: this.id_user,
          from_id: this.user.id_user,
          mensaje: this.message,
          estado: 0,
          author: this.user.nombres + " " + this.user.apellidos,
          fecha: moment(),
          rol: "user",
        });
        this.resetIcon();
        this.clearMessage();
      } else {
        console.log("SIN COMENTARIO");
      }
    },
    getDate(fecha) {
      if (fecha != "Hace un momento") {
        return moment(fecha).format("HH:mm a");
      } else {
        return "Hace un momento";
      }
    },
    scrollToEnd: function () {
      var content = this.$refs.messagesContainer;
      content.scrollTop = content.scrollHeight;
    },
  },

  created() {
    if (localStorage.getItem("user")) {
      var credential = JSON.parse(localStorage.getItem("user"));
      const idUser = credential.user;
      this.idUser = idUser.id_user;
      //OBTENER LOS CHAT
      api.getChatBot(this.id_user, this.idUser).then((result) => {
        let message = result.data;
        message.forEach((element) => {
          this.messages = [...this.messages, element];
       
          console.log("Chat Actual", this.messages);
        });
      });
    }
  },
  mounted() {
    this.socket.on("refrescarChat", (data) => {
      let datos = {
        id: data.id,
        to_id: data.to_id,
        from_id: data.from_id,
        mensaje: data.mensaje,
        estado: data.estado,
        id_questions: data.id_questions,
        author: data.author,
        rol: data.rol,
        fecha: "Hace un momento",
      };
      this.messages = [...this.messages, datos];
    });
  },
};
</script>

<style >
.box-left {
  background: gainsboro;
  margin-bottom: 25px;
  margin-right: 50px;
  text-align: left;
  margin-left: 20px;
}

.box-rigth {
  text-align: right;
  margin-left: 50px;
  margin-right: 20px;
  margin-bottom: 25px;
  background: gainsboro;

}

.text-left {
  text-align: left;
  padding-left: 8px;
  padding-top: 0px;
  padding-right: 8px;
}

.text-rigth {
  text-align: right;
  padding-right: 8px;
  padding-top: 8px;
  padding-left: 8px;
}

.mensaje {
  font-weight: 500;
}

.small-font {
  font-weight: 300;
}

.msg {
  border: 1px solid lightgreen;
  position: relative;
  padding-right: 10px;
  border-radius: 10px;
  filter: drop-shadow(0 0 2px #999);
  background: #fff;
}
.msg::before {
  content: "";
  position: absolute;
  right: -10px;
  top: 44px;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 10px solid lightgreen;
}
.msg::after {
  content: "";
  position: absolute;
  right: -8px;
  top: 44px;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 10px solid #fff;
}

.msgTo {
  border: 1px solid lightgreen;
  position: relative;
  padding-left: 10px;
  border-radius: 10px;
  filter: drop-shadow(0 0 2px #999);
  background: #fff;
}
.msgTo::before {
  content: "";
  position: absolute;
  left: -10px;
  top: 44px;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-right: 10px solid lightgreen;
}
.msgTo::after {
  content: "";
  position: absolute;
  left: -8px;
  top: 44px;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-right: 10px solid #fff;
}
</style>