import axios from "axios"
import api from "./controller"

class notificar {

    notifiCliente(tipo, userId, titulo, texto) {
        api.obtenerIdOnesignal(userId).then((result) => {
            let idsOneSignal= result.data;
            var headers = {
                "Content-Type": "application/json"
            }
            let postParams = {
                app_id: "9a04ef9f-fde1-4cec-9570-d5ac687a3265",
                include_player_ids: [idsOneSignal.result],
                data: {
                    tipo: tipo,
                    idCliente: userId,
                    titulo: titulo,
                    texto: texto,
                },
                /*android_sound: "alert",
                android_channel_id: "34ded683-64a9-48e4-8612-5b5da4fcd14d",*/
                contents: { en: '"' + texto.toString() + '"' },
                headings: { en: '"' + titulo.toString() + '"' },
            };

            axios
                .post(
                    "https://onesignal.com/api/v1/notifications",
                    postParams,
                    headers
                );
        }
        );
    }

}

export default new notificar();