<template>
  <!--prominent-->
  <v-app-bar style="background: #0eb308 !important" dark app :prominent="promi">
    <!--  <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>-->
    <v-btn icon @click="$router.push({ path: '/' })">
      <v-icon>{{ icons.mdiArrowLeft }}</v-icon>
    </v-btn>
    <v-toolbar-title v-if="promi == false">{{ title }}</v-toolbar-title>
    <v-container text-center class="mt-6 containner-modifi" v-else>
      <!--    <a href=""> <p class="anuncio-vender text-decoration-underline">Vende Aqu!</p> </a> -->
      <v-text-field
        dark
        dense
        ref="search"
        v-model="search"
        class="border-3 text-field"
        rounded
        filled
        clear-icon="mdi-close-circle"
        clearable
        label="Qu ests buscando?"
        append-icon="search"
        v-on:input="getSearch"
      ></v-text-field
    ></v-container>

    <v-spacer></v-spacer>

    <v-btn icon @click.stop="promi = !promi">
      <v-icon>{{ promi ? "mdi-close" : "mdi-magnify" }} </v-icon>
    </v-btn>

    <v-btn icon @click="filter = true">
      <v-icon>mdi-filter</v-icon>
    </v-btn>

    <!--  <v-menu bottom origin="center center" transition="scale-transition">
      <template v-slot:activator="{ on, attrs }">
        <v-btn dark v-bind="attrs" v-on="on" icon>
          <v-icon>{{ icons.mdiFileTree }}</v-icon>
        </v-btn>
      </template>

      <v-list>
        <v-subheader>Disponible en:</v-subheader>
        <v-list-item @click="obtenerSubCat('Todos')">
          <v-list-item-title>Todos</v-list-item-title>
        </v-list-item>
        <v-list-item
          v-for="(item, i) in subcategoria"
          :key="i"
          @click="obtenerSubCat(item)"
        >
          <v-list-item-title>{{ item.nombre }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>-->

    <v-btn icon @click.stop="dialog = !dialog">
      <v-icon>{{ icons.mdiDotsVertical }} </v-icon>
    </v-btn>

    <!--  <v-menu bottom origin="center center" transition="scale-transition">
      <template v-slot:activator="{ on, attrs }">
        <v-btn dark v-bind="attrs" v-on="on" icon>
          <v-icon>{{ icons.mdiOrderBoolDescending }}</v-icon>
        </v-btn>
      </template>

      <v-list>
        <v-subheader>Ordenar Por:</v-subheader>
        <v-list-item
          v-for="(item, i) in orders"
          :key="i"
          @click="obtenerOrder(item)"
        >
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>-->

    <!-- BTN SHEET FILTER -->
    <v-bottom-sheet v-model="filter">
      <v-list>
        <v-subheader>Filtrar Por:</v-subheader>
        <v-list-item v-for="filter in categorias" :key="filter.id_categoria">
          <v-list-item-title @click="buscarDatos(filter)">{{
            filter.nombre_categoria
          }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-bottom-sheet>

    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar flat dark color="white">
          <v-btn icon dark @click="dialog = false">
            <v-icon large color="black">{{ icons.mdiArrowLeft }}</v-icon>
          </v-btn>

          <v-spacer></v-spacer>
        </v-toolbar>
        <v-container class="mx-auto">
          <h1 class="mt-9 mb-9 ml-5">Ms opciones:</h1>
          <v-row justify="center">
            <v-expansion-panels flat light accordion>
              <v-expansion-panel class="border-s" flat>
                <v-expansion-panel-header class="tamanio"
                  >Categoria</v-expansion-panel-header
                >
                <v-expansion-panel-content>
                  <v-list-item @click="obtenerSubCat('Todos')">
                    <v-list-item-title class="font-weight-medium"
                      >Todos</v-list-item-title
                    >
                  </v-list-item>
                  <v-list-item
                    v-for="(item, i) in subcategoria"
                    :key="i"
                    @click="obtenerSubCat(item)"
                  >
                    <v-list-item-title class="font-weight-light">{{
                      item.nombre
                    }}</v-list-item-title>
                  </v-list-item>
                </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel class="border-s" flat>
                <v-expansion-panel-header class="tamanio"
                  >Precio</v-expansion-panel-header
                >
                <v-expansion-panel-content>
                  <p class="mb-3">Ingrese un rango:</p>
                  <v-row no-gutters>
                    <v-col cols="5">
                      <v-text-field
                        label="mnimo"
                        v-model="min"
                        rounded
                        filled
                        class="mt-0 pt-0 subtitle-1 format "
                        hide-details
                        type="number"
                        single-line
                        style="width: 100%"
                        prefix="Q."
                        dense
                      ></v-text-field
                    ></v-col>
                    <v-col cols="1">
                      <v-btn icon dark class="pa-2">
                        <v-icon color="black">{{
                          icons.mdiUnfoldMoreVertical
                        }}</v-icon>
                      </v-btn></v-col
                    >
                    <v-col cols="5"
                      ><v-text-field
                        label="mximo"
                        v-model="max"
                        rounded
                        filled
                        class="mt-0 pt-0 subtitle-1 format ml-1"
                        hide-details
                        type="number"
                        single-line
                        style="width: 100%"
                        prefix="Q."
                        dense
                      ></v-text-field
                    ></v-col>
                    <v-col cols="1">
                      <v-btn icon dark class="ml-1" @click="obtener">
                        <v-icon size="30" color="success"
                          >{{
                            max
                              ? icons.mdiArrowRightCircle
                              : icons.mdiArrowRightThinCircleOutline
                          }}
                        </v-icon>
                      </v-btn></v-col
                    >
                  </v-row>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
  </v-app-bar>
</template>
<script>
import api from "../../models/controller";
import axios from "axios";
import { mdiArrowLeft } from "@mdi/js";
import { mdiOrderBoolDescending } from "@mdi/js";
import { mdiUnfoldMoreVertical } from "@mdi/js";
import { mdiDotsVertical } from "@mdi/js";
import { mdiArrowRightThinCircleOutline } from "@mdi/js";
import { mdiArrowRightCircle } from "@mdi/js";
import { mdiFileTree } from "@mdi/js";
export default {
  props: { title: null, filtro: null, categoria: null },
  data: () => ({
    dialog: false,
    notifications: false,
    sound: true,
    widgets: false,
    sub: false,
    filter: false,
    drawer: false,
    promi: false,
    group: null,
    search: "",
    categorias: [],
    icons: {
      mdiArrowLeft,
      mdiOrderBoolDescending,
      mdiFileTree,
      mdiDotsVertical,
      mdiUnfoldMoreVertical,
      mdiArrowRightCircle,
      mdiArrowRightThinCircleOutline,
    },
    subcategoria: [],
    orders: [
      { id: 1, title: "Precio (ms alto)", variable: "precio", value: "DESC" },
      { id: 2, title: "Precio (ms bajo)", variable: "precio", value: "ASC" },
      {
        id: 3,
        title: "Fecha (ms reciente)",
        variable: "fecha_alta",
        value: "DESC",
      },
      {
        id: 4,
        title: "Fecha (ms antigua)",
        variable: "fecha_alta",
        value: "ASC",
      },
    ],

    items: [
      { id: 1, title: "Categoria" },
      { id: 2, title: "Precio" },
    ],
    min: null,
    max: null,
    range: [0, 0],
  }),
  methods: {
    obtener() {
      var rango = {
        min: this.min,
        max: this.max,
      };
      this.min=null;
       this.max=null;
      this.dialog = false;
      this.$emit("result_price", rango);
      
    },
    obtenerSubCat(item) {
      //  this.value=false;
      this.dialog = false;
      this.$emit("result_subCat", item);
    },
    buscarDatos: function (e) {
      this.filter = false;
      this.$emit("resultados", e);
    },
    getSearch: function () {
      if (this.search == null) {
        this.search = "";
      }

      console.log("Datos de la busqueda : ", this.search);
      this.$emit("searchProducts", this.search);
    },
  },
  watch: {
    group() {
      this.drawer = false;
    },
  },
  created() {
    axios
      .get("https://vendeloyaglobal.herokuapp.com/api/categorias")
      .then((result) => {
        this.categorias = result.data;
        this.categorias.sort((a, b) => {
          a.id_categoria - b.id_categoria;
        });
        //console.log("MENU ", this.menu);
      });

    api.getProductBySubCategoria(this.categoria).then((result) => {
      this.subcategoria = result.data;
    });
    this.$watch(
      () => this.categoria,
      (toParams) => {
        api.getProductBySubCategoria(toParams).then((result) => {
          this.subcategoria = result.data;
        });
      }
    );
  },
};
</script>

<style >
.text-field {
  width: 100%;
}

.anuncio-vender {
  font-size: 18px;
  color: white;
  margin: 0px !important;
  padding: 0px !important;
  margin-bottom: 8px !important;
}

.containner-modifi {
  width: 100%;
  position: absolute;
  z-index: 3;
  top: 25px;
  left: 1px;
  right: 2px !important;
}

.tamanio {
  font-size: 15pt !important;
}

.border-s {
  border-radius: 0px !important;
  border-top: solid 1px rgb(199, 198, 198);
  border-bottom: solid 1px rgb(199, 198, 198);
}

.card-edit {
  padding: -6px !important;
  margin: 0px;
}
.row-edit {
  padding: 0px;
  margin: 0px;
}

.col-edit {
  padding: 0px;
  margin: 0px;
}
.sub-edit {
  padding: 0px;
  margin: -10px;
  padding-bottom: -10px !important;
}

.format {
  box-shadow: none !important;
  border: none !important;
  background: none !important;
}
</style>