import api from '@/models/controller'
import moment from 'moment'

const timePublicidad = (file) => {
    return new Promise((resolve, reject) => {
        try {

            var list = [];
            var fecha = new Date();
            var fhoy = moment(fecha).format('').toString()
            for (let i = 0; i < file.length; i++) {
                //COMPARA LAS FECHAS DE CADA PRODUCTO CON LA ACTUAL. 
                if (fhoy >= moment(file[i].fecha_fin).format('').toString()) {
                    api.updatePublicidadMarca(file[i].id, 'Detenido')
                } else {
                    list.push(file[i])
                }
                //DEVUELVE LOS PRODUCTOS AN CON FECHA VIGENTE DE PUBLICIDAD
                resolve(list)
            }

        } catch (error) {
            reject(error)
        }
    })


}
export default timePublicidad