<template>
    <v-app>
        <v-app-bar app dark style="background:#0eb308;">
            <!-- -->
            <v-btn icon @click="regresar()">
                <v-icon>mdi-keyboard-backspace</v-icon>
            </v-btn>
            <v-toolbar-title>Chat Soporte</v-toolbar-title>
        </v-app-bar>
        <v-container class="top" >
            <div  ref="chat">
                <p v-for="(message, ipo) in messages" class="message" :key="message.idChat" style="position: relative;"
                    :class="{ 'message-out': message._to === userId, 'message-in': message._to !== userId }">
                    <img v-if="message.foto != ''" :src="message.foto" alt="" />
                    {{ message.mensaje }}
                    <br>
                    <span
                        :class="{ 'message-out-fecha': message._to === userId, 'message-in-fecha': message._to !== userId }">{{
                            message.fecha }}</span>
                    <span v-if="message.estado == 'L' && message._to === userId" class="Leido">Leido</span>
                    <v-menu v-if="message._to === userId" bottom>
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn style="position: absolute; top:0px; right: -8px; font-weight: bolder;" dark icon
                                v-bind="attrs" v-on="on">
                                <v-icon>mdi-dots-vertical</v-icon>
                            </v-btn>
                        </template>

                        <v-list>
                            <v-list-item v-for="(item, i) in items" :key="i">
                                <v-list-item-title @click="eliminar(message, ipo)">{{ item.title }}</v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </v-menu>
                </p>

            </div>
            <br>
        </v-container>

        <v-footer app>
            <v-form style="width: 100%; margin-bottom: -20px;" class="mt-2">
                <div class="grid">
                    <div class="grid__content">
                        <photo v-if="newEdit == true" v-on:photo="onPhotos" :idPhoto="0" :photoProfile="0"></photo>
                    </div>
                </div>
                <v-text-field filled rounded dense v-model="message" :prepend-icon="icon"
                    :append-outer-icon="message || PhotoChat ? 'mdi-send' : ''" clear-icon="mdi-close-circle" clearable
                    label="Escribe tu mensaje..." @keyup.enter="sendMessage" type="text" @click:append="toggleMarker"
                    @click:append-outer="sendMessage" @click:clear="clearMessage"
                    @click:prepend="changeImage"></v-text-field>
            </v-form>
        </v-footer>
    </v-app>
</template>
<script>
//inicializamos la variable socket que utilizaremos en created
import moment from "moment";
import store from '../../store'
import api from '../../models/controller'
import io from "socket.io-client";

///import photo from "../../views/PerfilEdit/CreatePhoto.vue";
import photo from "../../views/PerfilEdit/CreatePhotoChatSoporte.vue"
export default {
    components: { photo },
    data() {
        return {
            hasScrolledToBottom: false,
            socket: io("https://vendeloyaglobal.herokuapp.com"),
            messages: [],
            password: 'Password',
            show: false,
            message: '',
            userId: null,
            marker: true,
            iconIndex: 0,
            icons: [
                'mdi-image'
            ],
            items: [
                { title: 'Eliminar para todos' }],
            PhotoChat: '',
            newEdit: false,
        }
    },
    updated:{
    },
    computed: {

        icon() {
            return this.icons[this.iconIndex]
        },
    },
    methods: {

        eliminar: function (message, i) {
            api.deleteChatSoporte(message.idChat).then(() => {
                this.messages.splice(i, 1);
            })
        },
        regresar: function () {
            window.history.back();
        },
        onPhotos: function (item) {
            if (item != "vacio") {
                this.newEdit = true;
                this.PhotoChat = item.img
            } else {
                this.PhotoChat = ""
                this.newEdit = false;
            }
        },
        changeImage: function () {
            this.newEdit = !this.newEdit
        },

        toggleMarker() {
            this.marker = !this.marker
        },
        sendMessage: function () {
            if (this.message != "" || this.PhotoChat != "") {
                var fecha = moment(new Date()).format('YYYY/MM/DD HH:mm:ss')
                this.socket.emit("chatsoporte", {
                    idUser: this.idUser.id_user,
                    idUserSoporte: 2147483647,
                    _to: this.idUser.id_user,
                    _from: 2147483647,
                    mensaje: this.message,
                    fotoUser: this.idUser.photo,
                    foto: this.PhotoChat,
                    estado: 'N',
                    fecha: fecha
                });
                // this.getMessage();
                this.newEdit = false;
                window.setTimeout(() => {
                    this.sendMessageAutomatic()
                }, 2000)
                this.resetIcon();
                this.clearMessage();
                //this.getMessage();

            } else {
                console.log("SIN COMENTARIO");
            }
        },

        sendMessageAutomatic: function () {

            var fecha = moment(new Date()).format('YYYY/MM/DD HH:mm:ss')
            this.socket.emit("chatsoporte", {
                idUser: this.idUser.id_user,
                idUserSoporte: 2147483647,
                _to: 2147483647,
                _from: this.idUser.id_user,
                mensaje: "Espera un momento, un colaborar se pondr en contacto contigo!.",
                fotoUser: this.idUser.photo,
                foto: this.PhotoChat,
                estado: 'N',
                fecha: fecha
            });
            api.updateLeidoChat(this.user.id_user, 2147483647);
        },

        getMessage: function () {
            this.messages = [];
            try {
                if (store.getters.isLoggedIn) {
                    var data = store.getters.getUser;
                    const idUser = data;
                    this.idUser = idUser;
                    this.userId = idUser.id_user;

                    api.getUser(idUser.id_user).then((result) => {
                        this.user = result.data[0];
                        api.getChatSoporte(this.idUser.id_user).then((result) => {
                            var chatsoporte = [];
                            var chats = result.data;
                            chatsoporte = chats.result;
                            chatsoporte.forEach((element) => {
                                this.messages = [...this.messages, element];
                            });
                        });
                    });


                } else {
                    this.isLogin = false;
                }
            } catch (error) {
                console.log(error);
            }
        },
        clearMessage() {
            this.message = ''
        },
        resetIcon() {
            this.iconIndex = 0
        },
        changeIcon() {
            this.iconIndex === this.icons.length - 1
                ? this.iconIndex = 0
                : this.iconIndex++
        },
        scrollToBottom() {
            const targetRef = this.$refs.chat;
            this.$nextTick(() => {
                targetRef.scrollTo(
                    {
                        top: targetRef.scrollHeight,
                        left: 0,
                        behavior: "smooth"
                    }
                );
            });
        }
    },
    //Inicializacin de la variable socket
    created: function () {
        if (store.getters.isLoggedIn) {
            var data = store.getters.getUser;
            const idUser = data;
            this.idUser = idUser;
            this.userId = idUser.id_user;
            api.updateLeidoChat(2147483647, idUser.id_user);
            this.socket.on('refrescarChat', (data) => {
                this.messages = [...this.messages, data];
                api.updateLeidoChat(idUser.id_user, 2147483647);
            });
            window.setTimeout( ()=> { 
            document.querySelector(".top").scrollIntoView({ behavior: 'smooth' });
            this.scrollToBottom()
             }, 2000);
        }
    },
    //Se reciben los mensajes y se aaden al array
    mounted: function () {
        this.getMessage();
    }

}

</script>
<style>
.headline {
    text-align: center;
    font-weight: 100;
    color: white;
}

.chat-area {
    /*   border: 1px solid #ccc; */
    background: rgb(255, 255, 255);
    padding: 0.1em;
    max-width: 100%;
    /*margin: 0 auto 2em auto;*/
    /* box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3) */
}

.message {
    border-radius: 10px;
    padding: .5em;
    /*   margin-bottom: .5em; */
    font-size: .9em;
}

.message-out {
    width: auto;
    background: #10b800;
    color: white;
    margin-left: 40%;
    margin-bottom: 25px !important;
}

.message-in {
    margin-bottom: 25px !important;
    background: #F1F0F0;
    color: black;
    margin-right: 40%;
}

.message-out-fecha {
    font-size: 10px;

    right: 0px;
    margin-left: 55%;
    text-align: right !important;
}

.message-in-fecha {
    font-size: 10px;
    text-align: left;
}

.chat-inputs {
    display: flex;
    justify-content: space-between;
}

#person1-input {
    padding: .5em;
}

#person2-input {
    padding: .5em;
}

.grid {
    display: grid;
    place-items: center;
    width: 100%;
    color: #333;
}

.grid__content {
    margin: auto;
}

.Leido {
    color: blue;
    font-size: 10px;
    position: absolute;
    bottom: -15px;
    right: 3px !important;

}
</style>