<template>
  <v-footer
  class="color-background"
  color="success"
    app
    style="margin-bottom: 0px !important; padding-bottom: 0px"
  >
    <!-- -->
    <v-form
      style="margin: 0px; margin-bottom: 0px; padding-bottom: 0px; width: 100%"
    >
      <v-row
        style="margin-bottom: 0px !important; padding-bottom: 0px !important"
      >
        <v-col
        class="pb-3"
          cols="12"
          style="margin-bottom: 0px !important; padding-bottom: 0px"
        >
          <v-text-field
          class="text-color mb-3"
            style="margin-bottom: 0px !important"
            rounded
            v-model="message"
            :append-outer-icon="message ? 'mdi-send' : ''"
            filled
            clear-icon="mdi-close-circle"
            clearable
            label="Mensaje"
            type="text"
  solo
            @click:append="toggleMarker"
            @click:append-outer="sendMessage"
            @click:prepend="changeIcon"
            @click:clear="clearMessage"
          ></v-text-field>
        </v-col>
      </v-row>
    </v-form>
  </v-footer>
</template>

<script>
import io from "socket.io-client";
import store from "../../store"
import api from "../../models/controller";
import moment from "moment";
export default {
  props: { id_user: null },
  data: () => ({
    password: "Password",
    show: false,
    user: {},
    progress: null,
    messages: [],
    message: "",
    socket: io("https://vendeloyaglobal.herokuapp.com"),
    marker: true,
    iconIndex: 0,
    icons: [
      "mdi-emoticon",
      "mdi-emoticon-cool",
      "mdi-emoticon-dead",
      "mdi-emoticon-excited",
      "mdi-emoticon-happy",
      "mdi-emoticon-neutral",
      "mdi-emoticon-sad",
      "mdi-emoticon-tongue",
    ],
  }),

  computed: {
    icon() {
      return this.icons[this.iconIndex];
    },
  },

  methods: {
    sendMessage() {
      if (this.message != "") {
        this.socket.emit("nuevoChat", {
          to_id: this.id_user,
          from_id: this.user.id_user,
          mensaje: this.message,
          estado: 0,
          author: this.user.nombres + " " + this.user.apellidos,
          fecha: moment(),
          rol: 'user'
          
        });
        this.resetIcon();
        this.clearMessage();
      } else {
        console.log("SIN COMENTARIO");
      }
    },

    toggleMarker() {
      this.marker = !this.marker;
    },

    clearMessage() {
      this.message = "";
    },
    resetIcon() {
      this.iconIndex = 0;
    },
    changeIcon() {
      this.iconIndex === this.icons.length - 1
        ? (this.iconIndex = 0)
        : this.iconIndex++;
    },
  },
  created() {
    //console.log("Datos del usuario : ", this.id_user)
    this.$emit("result", this.id_user);
      if (store.getters.isLoggedIn) {
      const idUser = store.getters.getUser;
      try {
        api.getUser(idUser.id_user).then((result) => {
          this.user = result.data[0];
        });
      } catch (error) {
        console.log(error);
      }
    }
  },
};
</script>

<style>
.text-fields {
  width: 100% !important;
}

.color-background{
}

.text-color{
  height: 56px;
  margin-bottom: 5px;
}
</style>