            <v-btn class="mb-3" large rounded outlined color="green" block @click="irInicio" text>
              Iniciar sesin
            </v-btn>
            <br>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>
  
<script>
//import Vue from 'vue';
//
//import auth from "@/Auth/auth";
import api from "../models/controller";
import veryemail from '../models/email'
//import pdf from 'vue-pdf'

//var loadingTask = pdf.createLoadingTask('https://firebasestorage.googleapis.com/v0/b/storage-imge.appspot.com/o/Terminos%26Condiciones%2Fterminos_condiciones.pdf?alt=media&token=9c36a467-a7cf-4d4b-8228-f72708115583');


//import config from "../models/apirest.js";
//import axios from "axios";
export default {

  data() {
    return {
      serviceUrl: "https://ej2services.syncfusion.com/production/web-services/api/pdfviewer",
      documentPath: "static/tc.pdf",
      src: 'loadingTask',
      numPages: undefined,
      showDialog: false,
      codeActive: true,
      code_very: Math.floor(Math.random() * 9000 + 1000),
      interval: {},
      verify: null,
      value: 0,
      plataforma: "",
      referido: false,
      codigo: "",
      cargandoEnvio: null,
      notificarCreacion: false,
      show1: false,
      show2: false,
      selectPais: { nombre: "Seleccionar", id: null },
      selectMuni: { nombre: "Seleccionar", id: null },
      selectDepa: { nombre: "Seleccionar", id: null },
      paises: [],
      departamentos: [],
      municipios: [],
      passwordRepeat: undefined,
      password_repeat: undefined,
      nombres: undefined,
      apellidos: undefined,
      departamento: undefined,
      municipio: undefined,
      items: ["Foo", "Bar", "Fizz", "Buzz"],
      agreement: false,
      bio: "Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts",
      dialog: false,
      email: undefined,
      form: false,
      isLoading: false,
      password: undefined,
      phone: undefined,
      rules: {
        email: (v) =>
          !!(v || "").match(/@/) || "Por favor ingresa un correo vlido",
        length: (len) => (v) =>
          (v || "").length >= len ||
          `Longitud de carcter no vlida, obligatorio ${len}`,
        password: (v) =>
          !!(v || "").match(
            /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*(_|[^\w])).+$/
          ) ||
          "La contrasea debe contener una letra mayscula, un carcter numrico y un carcter especial, Ejemplo: Abc123@456",
        passRepeat: (v) =>
          v == this.password || "Las contraseas no son iguales.",
        required: (v) => !!v || "Este campo es obligatorio",
      },
    };
  },


  methods: {

    activarTC() {
      this.showDialog = true;
      /* var pdfDocument = pdf.createLoadingTask(
         "../assets/tc.pdf"
       );
       this.src = pdfDocument;
       this.src.promise.then((pdf) => {
         this.numPages = pdf.numPages;
       });*/
    },

    regresar: function () {
      window.location.href = "/";
    },
    verifyqueEmail(email) {
      if (email != "") {
        api.verifyqueEmail(email).then((result) => {
          var data = result.data;
          this.verify = data.msg;
        });
      } else {
        this.verify = null;
      }
    },
    irInicio: function () {
      this.notificarCreacion = false;
      //this.$
      //window.location.href = "/";
      this.$router.replace("/").catch(() => { });
      // this.$router.push("/edit-profile").catch(() => {});
    },
    register: function (e) {
      var tipoRegistro = 1;
      if (this.referido == false) {
        tipoRegistro = 1
      } else {
        tipoRegistro = 0
      }

      this.cargandoEnvio = true;
      let params = {
        id_muni: this.selectMuni,
        id_plan: 5,
        id_status: 1,
        tregistro: tipoRegistro,
        code: this.code_very,
        fichas: 100,
        fichas_detalle: "+ Por registrase en VENDELO YA",
        nombres: this.nombres,
        apellidos: this.apellidos,
        password: this.password,
        photo:
          "https://thumbs.dreamstime.com/b/default-avatar-profile-icon-vector-social-media-user-photo-183042379.jpg",
        email: this.email,
        telefono: this.phone,
        direccion: "ninguna",
        password_repeat: this.password_repeat,

      };


      api.registerUser(params).then((result) => {
        veryemail.sendEmail(e.target)
        if (this.referido == true && this.codigo != "") {
          api.obtenerReferido(this.codigo).then((result) => {
            var user = result.data;
            var detalle = user.user;
            var data = {
              id_user: detalle.id_user,
              fichas: 200,
              fichas_detalle: "+ Por referir a nuevos usuarios.",
            };

            api.CargarFichasUserRefiere(data).then((result) => {
              window.localStorage.setItem(
                "user",
                JSON.stringify(result.data)
              );
              this.cargandoEnvio = false;
              this.notificarCreacion = true;
              this.$refs.form.reset();
            });
          });
        } else {
          window.localStorage.setItem("user", JSON.stringify(result.data));
          this.cargandoEnvio = false;
          this.notificarCreacion = true;
          this.$refs.form.reset();
        }
      });
    },
    limpiar: function () {
      this.selectMuni = { nombre: "Seleccionar", id: null };
      this.selectDepa = { nombre: "Seleccionar", id: null };
      this.nombres = undefined;
      this.apellidos = undefined;
      this.password = undefined;
      this.email = undefined;
      this.phone = undefined;
      this.password_repeat = undefined;
      this.municipio = undefined;
      this.departamento = undefined;
      this.code_very = Math.floor(Math.random() * 9000 + 1000);
    },


    getAllPais: async function () {
      await api.getAllPais().then((result) => {
        var pa = result.data;
        var list = pa.data;
        list.forEach((element) => {
          this.paises.push({
            nombre: element.nombrePais,
            id: element.idPais,
          });
        });
      });
    },
    getDepartamento: async function (e) {
      this.departamentos = [];
      if (e) {
        let id = e.id;
        await api.getPaisByDepartamento(id).then((result) => {
          var depa = result.data;
          var list = depa.data;
          list.forEach((element) => {
            this.departamentos.push({
              nombre: element.nombreDepartamento,
              id: element.idDepartamento,
            });
          });
        });
      }
    },
    obtenerMuni: async function (e) {
      this.municipios = [];
      let id = e.id;
      await api.getMunicipiosDepa(id).then((result) => {
        let muni = result.data;
        this.municipios = [];
        muni.forEach((element) => {
          this.municipios.push({
            id: element.idMunicipio,
            nombre: element.nombreMunicipio,
          });
        });
      });
    },
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  mounted() {
    this.interval = setInterval(() => {
      if (this.value === 100) {
        return (this.value = 0);
      }
      this.value += 10;
    }, 1);


    /*this.src.promise.then(pdf => {
    
    this.numPages = pdf.numPages;
    });*/
  },
  created() {
    //DEPARTAMENTOS
    window.localStorage.removeItem('user');
    this.getAllPais();
    this.getDepartamento();

    this.code_very = Math.floor(Math.random() * 9000 + 1000);

  },
};
</script>
  
