<template>
    <v-container class="text-center" style="position: relative;">
        <v-icon @click="back()" size="40" class="icon-close">mdi-keyboard-backspace</v-icon>
        <v-icon class="mt-8" color="warning" size="150">mdi-shield-key</v-icon>
        <p class="font-weight-black text-h6">OLVIDASTE TU CONTRASEA?</p>
        <br>
        <span style="text-align: left !important;">Ingrese su correo electrnico asociada a su cuenta y estarmos enviando
            instrucciones para restablecer su contrasea.</span>
        <v-form ref="form" style="position: relative;" class="mt-5" @submit.prevent="restorePassword">
            <v-text-field class="rounded-lg" name="from_email" v-model="email" :rules="[rules.email]" label="Ingrese correo"
                outlined></v-text-field>
            <v-text-field class="code_message" name="message" v-model="code_very" label="Ingrese correo"
                outlined></v-text-field>
            <input class="submit" type="submit" name="send" />
        </v-form>

        <v-snackbar top v-model="snackbar" color="success" :timeout="3000">
            Correo enviado, revisa tu bandeja de entrada!
        </v-snackbar>
        <v-dialog v-model="loading" hide-overlay persistent width="300">
            <v-card color="success" dark class="pt-1">
                <v-card-text class="mt-2">
                    Por favor espere
                    <v-progress-linear indeterminate color="white" class="mb-0 mt-2"></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>
    </v-container>
</template>

  
<script>
import veryemail from '../../models/email'
import api from '../../models/controller'
export default {

    data() {
        return {
            loading: false,
            snackbar: false,
            form: '',
            code_very: Math.floor(Math.random() * 9000 + 1000),
            email: '',
            rules: {
                email: (v) =>
                    !!(v || "").match(/@/) || "Por favor ingresa un correo vlido",
                required: (v) => !!v || "Este campo es obligatorio",
            },
        }
    },
    methods: {
        restorePassword: function (e) {
            this.loading = true;
            window.setTimeout(() => {
            let data = {
                email: this.email,
                code: this.code_very,
            }
            api.updateVeryEmailByUserPassword(this.email, data).then(() => {
      
                    veryemail.sendEmailPassword(e.target);
                    this.code_very = Math.floor(Math.random() * 9000 + 1000);
                    this.email = '';
                    this.snackbar = true;
                    this.loading = false;
                    const path = `/check-email`
                    if (this.$route.path !== path) this.$router.push(path)
              
            })

        }, 2000)
        },
        back() {
            window.history.back();
        }
    }
}
</script>

<style scoped>
.icon-close {
    position: absolute;
    font-weight: bolder;
    top: 5px;
    left: 10px;
}

.submit {
    width: 100%;
    background: green;
    padding: 12px;
    color: white;
    font-size: 14pt;

    border-radius: 10px;
}

.code_message {
    position: absolute;
    z-index: -1;
}
</style>