<template>
  <v-dialog v-model="show" max-width="550px">
    <v-card>
      <v-toolbar flat
            dark
            color="success"
            class="card-toolbar"><v-toolbar-title style="font-size:1.6rem; text-center; font-weight:bolder">Vista Previa</v-toolbar-title>   <v-spacer></v-spacer>
          <v-icon  @click="show = false">mdi-close</v-icon></v-toolbar>

        <v-img :src="product.thumbnail"  max-height="100%"  max-width="100%"></v-img>
        <v-card-title>
          {{ product.titulo }}
        </v-card-title>

        <v-card-subtitle>
               <span class="title"> Q.{{product.precio}}</span>
        </v-card-subtitle>
     
        <v-card-text>
            {{product.descripcion}}
        </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    value: Boolean,
    product: null,
  },

  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
};
</script>