<template>
  <div>
    <v-row justify="center">
      <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
      >
        <v-card style="height: 90vh; width: 100%" class="mb-5">
          <v-toolbar dark color="success" id="header">
            <v-btn icon dark @click="dialog = false">
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Ubicacin</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon @click="meUbication">
              <v-icon role="img" aria-hidden="false">{{
                icons.mdiCrosshairsGps
              }}</v-icon>
            </v-btn>
          </v-toolbar>

          <v-container id="myAutocomplete" class="autocomplete pl-6 pr-6">
            <GmapAutocomplete
              placeholder="Ingrese una direccin"
              class="pa-3 mt-3 mb-2"
              style="
                width: 100%;
                height: 40px;
                border-radius: 20px;
                color: gray;
                background: white;
                box-shadow: 0px 0px 4px 6px rgba(0, 0, 0, 0.2);
              "
              @place_changed="setPlace"
              :options="{
                fields: ['geometry', 'formatted_address', 'address_components'],
              }"
            >
            </GmapAutocomplete>
          </v-container>

          <GmapMap
            ref="mapRef"
            :center="center"
            :zoom="zoom"
            style="width: 100%; height: 90vh"
            :options="{
              zoomControl: false,
              mapTypeControl: false,
              scaleControl: false,
              streetViewControl: false,
              rotateControl: false,
              fullscreenControl: false,
              disableDefaultUi: true,
              gestureHandling: 'greedy',
            }"
            ><GmapMarker
              :position="center"
              :icon="icon"
              :draggable="true"
              @drag="updateCoordinates"
            />
            <GmapCircle
              ref="circleRef"
              :center="center"
              :radius="slider * 1000"
              :visible="true"
              :options="{
                fillColor: '#068ddb',
                fillOpacity: 0.3,
                strokeOpacity: 0.9,
                strokeWeight: 1,
                strokeColor: '#0278bd',
              }"
            ></GmapCircle>
          </GmapMap>
          <div id="rango" class="input-group col-md-6 white">
            <v-row class="ml-1 mr-3 pd-3">
              <v-col cols="12" class="pa-0 mt-1">
                <v-slider
                  style="width: 100%"
                  v-model="slider"
                  class="align-center"
                  :max="max"
                  :min="min"
                  hide-details
                  color="success"
                  thumb-label="always"
                >
                  <template v-slot:append>
                    <v-text-field
                      v-model="slider"
                      class="mt-0 pt-0 field h-4"
                      hide-details
                      filled
                      dense
                      rounded
                      type="number"
                      suffix="km"
                      style="width: 126px"
                    ></v-text-field>
                    <!--  <span style="width: 80px; font-size: 18pt">
                      {{ slider
                      }}<span
                        style="font-size: 16pt"
                        class="d-inline font-weight-bold"
                      >
                        km</span
                      ></span
                    > -->
                  </template>
                </v-slider>
              </v-col>
              <v-col cols="12" class="pa-1 mt-2 mb-8">
                <v-btn color="success" block rounded dark @click="searchUbi"
                  >Aplicar</v-btn
                >
              </v-col>
            </v-row>
          </div>
        </v-card>
      </v-dialog>
    </v-row>
    <v-snackbar
      :timeout="timeout"
      v-model="snackbar"
      color="red"
      dark
      class="text-center"
      rounded="pill"
      text-center
    >
      Debes ingresar una direccin.
    </v-snackbar>
  </div>
</template>
<script>
import { mdiCrosshairsGps } from "@mdi/js";
import { gmapApi } from "vue2-google-maps";
export default {
  props: {
    value: Boolean,
    cord: null,
    lat: null,
    lng: null,
    address: null,
  },

  data() {
    return {
      geocoder: null,
      zoom: 12,
      marker: "",
      icon: {
        url: "https://cdn-icons-png.flaticon.com/128/1673/1673221.png",
        scaledSize: { width: 30, height: 30 },
      },
      timeout: 2000,
      snackbar: false,
      min: 0,
      max: 200,
      slider: 3,
      center: { lat: this.cord.lat, lng: this.cord.lng },
      currentPlace: null,
      markers: [],
      places: [],
      actMap: false,
      icons: { mdiCrosshairsGps },
    };
  },
  methods: {
    updateCoordinates(location) {
      this.$refs.mapRef.$mapPromise.then((map) => {
        this.center = {
          lat: location.latLng.lat(),
          lng: location.latLng.lng(),
        };
        map.panTo({
          lat: location.latLng.lat(),
          lng: location.latLng.lng(),
        });
      });
    },
    meUbication: function () {
      this.$refs.mapRef.$mapPromise.then((map) => {
        navigator.geolocation.getCurrentPosition((position) => {
          this.center = {
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          };
          map.panTo({
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          });
        });
      });
    },
    async searchUbi() {
      var data;
      if (this.currentPlace != null) {
        data = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng(),
          km: this.slider,
          dire: this.currentPlace.formatted_address,
        };
        this.$emit("filterUbi", data);
        this.dialog = false;
      } else {
        data = {
          lat: this.center.lat,
          lng: this.center.lng,
          km: this.slider,
          dire: this.address,
        };
        this.$emit("filterUbi", data);
        this.dialog = false;
      }
    },
    async buscarDatos(parametros) {
      this.$refs.mapRef.$mapPromise.then((map) => {
        this.center = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng(),
        };
        map.panTo({
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng(),
        });

        this.$emit("meUbi", parametros);
      });
      //this.dialog = false;
    },
    setPlace(place) {
      if (place != null) {
        this.currentPlace = place;
        this.buscarDatos(this.currentPlace.formatted_address);
      } else {
        this.snackbar = true;
      }
    },
  },
  created() {
    navigator.geolocation.getCurrentPosition((position) => {
      this.center = {
        lat: position.coords.latitude,
        lng: position.coords.longitude,
      };
    });

    this.$watch(
      () => this.slider,
      (toParams) => {
        if (toParams >= 0 && toParams < 7) {
          this.zoom = 12;
        } else if (toParams > 8 && toParams < 11) {
          this.zoom = 11;
        } else if (toParams > 12 && toParams < 19) {
          this.zoom = 10;
        } else if (toParams > 20 && toParams < 45) {
          this.zoom = 9;
        } else if (toParams > 46 && toParams < 79) {
          this.zoom = 8;
        } else if (toParams > 81 && toParams < 100) {
          this.zoom = 7;
        }
      }
    );
  },
  computed: {
    google: gmapApi,
    dialog: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
  watch: {
    setUbicacion() {
      navigator.geolocation.getCurrentPosition((position) => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        };
      });
    },
  },
  mounted: function () {
    this.$gmapApiPromiseLazy().then(() => {
      if (this.google) {
        this.geocoder = new this.google.maps.Geocoder();
      }
    });
  },
};
</script>
<style scoped>
.c {
  background: none !important;
  box-shadow: none !important;
}
</style>

<style scoped>
#header {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
}
.autocomplete {
  position: absolute;
  top: 65px;
  left: 1px;
  right: 2px;
  z-index: 3;
}

#rango {
  width: 100% !important;
  position: absolute;
  bottom: -3px;
  left: 1px;
  right: 2px;
  z-index: 3;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0px -5px 10px 0px rgba(0, 0, 0, 0.5);
}
.field {
  box-shadow: none !important;
  border: none !important;
  background: none !important;
  font-size: 18pt;
}
</style>