<template>
  <div class="grey lighten-5">
    <v-app-bar color="deep-purple accent-4" dense style="background: #0eb308 !important" height="60">
      <v-btn  icon  color="black" @click="regresar(codigo)">
        <v-icon size="25" color="white" > mdi-arrow-left </v-icon>
      </v-btn>
    </v-app-bar>
    <v-row no-gutters style="border: none !important; border-radius: 8px" class="white">
      <v-col style="border: none !important" cols="12" md="6">
        <photos v-on:obtenerFoto="onResultados" :idProduct="idProduct" :plan="product.color"></photos>
      </v-col>
      <v-col cols="12" md="6">
        <v-row no-gutters class="pl-2 pr-2 pt-2">
          <v-col cols="6">
            <p class="font-weight-black title precio-style">
              Q.{{ product.precio }}
            </p>
          </v-col>
          <v-col cols="6" class="text-end">
            <p class="vistas font-weight-light">
              <v-icon class="mr-2">mdi-eye</v-icon><b>{{ product.vistas }}</b> vista(s)
            </p>
          </v-col>
        </v-row>
        <!--<v-btn @click="irbtn">ir</v-btn>-->
        <span class="pl-2 pr-2 mb-1 font-weight-medium">Comparte a tus amigos en:</span>
        <v-row no-gutters class="pl-2 pr-2 pt-1">
          <v-col cols="4" v-for="(item, i) in shared" :key="i" class="text-center">
            <ShareNetwork @change="obtenerMonedas" :network="item.network" :url="item.url" :title="item.title"
              :description="item.description">
              <v-icon :color="item.color" size="40">
                mdi-{{ item.icon }}</v-icon>
            </ShareNetwork>
          </v-col>
        </v-row>
        <hr>
        <p class="pl-1">Publicidad</p>
              <FranjaPublicidad></FranjaPublicidad>
              <hr class="mt-6">
        <v-list-item>
          <v-list-item-avatar @click="goSeller(product.id_user
          )">
            <v-img :src="product.photo"></v-img>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title class="font-weight-bold">{{ product.Nombre_Completo }}</v-list-item-title>
            <v-list-item-subtitle>Publicado</v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action v-if="product.id_user == idUser">
            <v-btn @click="editProduct(product)" icon>
              <v-icon color="blue lighten-1">mdi-pencil</v-icon>
            </v-btn>
            <v-btn @click="comprarPlan(product)" color="success" class="mt-3" rounded    >
        Destacar
            </v-btn>
          </v-list-item-action>
        </v-list-item>

        <div class="pl-2 pr-2">
          <p class="font-weight-black title mb-0" style="margin-bottom: -0px !important; padding-bottom: 0px">
            {{ product.titulo }}
          </p>
          <p style="margin-top: -1px;
                  padding-top: 0px;
                    line-height: 12px !important;
                  " class="font-weight-regular caption">
            <v-icon size="15" class="mr-0">mdi-map-marker</v-icon>
            {{ product.direccion }} {{ product.nombreMunicipio }}
            {{ product.nombreDepartamento }} - @{{ product.fecha_alta }}
          </p>
          <div class="mt-3">
            <p style="line-height: 18px !important" class="font-weight-medium">
              {{ product.descripcion }}
            </p>
            <v-btn elevation="2" @click="islogen()" class="mt-8 rounded-lg mb-8" color="green" rounded large block
              outlined>
              <v-icon size="30">mdi-whatsapp</v-icon> Quiero comprar
            </v-btn>
          </div>
        </div>
        <comentario :title="product.titulo" v-if="product.id_pro != ''" :id_proview="product.id_pro" />
        <v-divider></v-divider>
        <p class="mx-2 mt-2 font-weight-bold title">Quiz te interese</p>
        <prodSimilares v-if="product.id_categoria" :data="{ pro: product.id_pro, cat: product.id_categoria }"
          :idCat="product.id_categoria"></prodSimilares>
      </v-col>
    </v-row>

    <div v-if="id_proVen != ''">
      <detalleProducto :id="id_proVen" v-model="activarDialog" />
    </div>

    <login v-model="isLogin" />
    <!-- <btnVender></btnVender>-->
    <ViewFoto :foto="dataFoto" v-model="isPhoto"></ViewFoto>
    <editProduct v-if="id_proEd" v-on:input="updateVista" :categoria="id_cat" :id_pro="id_proEd" v-model="edit" />
    <buyplan v-on:input="onResultadosBuy" v-if="buyPro" :product="buyPro" v-model="showScheduleForm"></buyplan>

  </div>
</template>

<script>
import axios from "axios";
import api from "../models/controller";
import store from "@/store"
import detalleProducto from "@/components/Datos_Usuario/DialogUser.vue";
import photos from "@/components/DetalleProducto/Photos.vue";
import comentario from "@/components/Comentarios/Comentarios.vue";
import editProduct from "@/components/Dialogs/EditProduct.vue";
import prodSimilares from "@/components/ObtenerProductos/getProductosSimilares.vue";
import buyplan from "@/components/Buyplan/Home.vue";
import FranjaPublicidad from '@/components/PublicidadPagada/FranjasPublicitarias.vue'
//import appbar from "@/components/AppBar/AppBarProduct.vue";
import login from "@/components/Login/Login.vue";
import ViewFoto from "@/components/VerFotos/FotosCompletas.vue";
//import btnVender from "@/components/AgregarProducto/BtnVender";
import { mdiCartPlus } from "@mdi/js";
import { mdiCrownCircle } from "@mdi/js";
import { mdiCrown } from "@mdi/js";
import { mdiHome } from "@mdi/js";
import { mdiCheckDecagram } from "@mdi/js";
import { mdiArrowLeftCircle } from "@mdi/js";
import { mdiFacebook } from "@mdi/js";
import { mdiFacebookMessenger } from "@mdi/js";
import { mdiWhatsapp } from "@mdi/js";
import { mdiTwitter } from "@mdi/js";
import { mdiLinkedin } from "@mdi/js";
export default {
  props: ["id_pro", "codigo"],
  components: {
    buyplan,
    login,
    detalleProducto,
    comentario,
    ViewFoto,
    photos /*btnVender*/,
    prodSimilares,
    editProduct,
    FranjaPublicidad
  },
  data() {
    return {
      buyPro: null,
            showScheduleForm: false,
      idUser: null,
      edit: false,
      id_proEd: null,
      id_cat: null,
      products: {},
      isUser: false,
      isPhoto: false,
      dataFoto: '',
      vendedorData: {},
      idProduct: null,
      calificar: false,
      ranting: {},
      product: {},
      loading: false,
      selection: 1,
      galeria: [],
      id_proVen: "",
      vendedor: null,
      id_product: null,
      activarDialog: false,
      isLogin: false,
      shared: [],
      icons: {
        mdiCartPlus,
        mdiCrownCircle,
        mdiCrown,
        mdiArrowLeftCircle,
        mdiFacebook,
        mdiFacebookMessenger,
        mdiWhatsapp,
        mdiTwitter,
        mdiLinkedin,
      },
      items: [
        {
          text: "Inicio",
          disabled: false,
          icon: mdiHome,
          href: "/",
        },
        {
          text: "Producto",
          disabled: false,
          icon: mdiCheckDecagram,
          href: "breadcrumbs_link_1",
        },
      ],
    };
  },
  methods: {

    onResultadosBuy: function (item) {
            this.showScheduleForm = item;

            this.buyPro = null;
        },
    comprarPlan(item) {
            this.buyPro = item.id_pro;
            this.showScheduleForm = true;
        },
    updateVista:function(){
      let id = this.$route.params.id;
    this.getProductsPrincipal(id);

    this.$watch(
      () => this.$route.params.id,
      (toParams) => {
        this.getProductsPrincipal(toParams);
      }
    )
    },
    editProduct(item) {
      this.edit = true;
      this.id_proEd = item.id_pro
      this.id_cat = item.id_categoria
      this.products = item;
    },
    onResultados(datos) {
      this.isPhoto = true;
      this.dataFoto = datos;
    },

    goSeller(id_user) {
      this.$router.push(`/seller/${id_user}`).catch(() => { });
    },
    //obtenerMonedas
    regresar(codigo) {
      if (codigo == undefined) {
        this.$router.go(-1);
        //window.history.back();
      } else {
        console.log("IR INICIO");
        // this.$router.push('/')
        this.$router.go(-1);
        //window.history.back();
      }
    },
    obtenerMonedas: function (item) {
      console.log("OTENER NOMEDAS :", item)
    },
    actualizarRanting: async function (e, id) {
      await api.updateRanting(id, e).then(() => { });
    },
    retrocer() {
      this.$router.go(-1);
    },
    irVender: function () {
      if (window.localStorage.getItem("user")) {
        var credential = JSON.parse(localStorage.getItem("user"));
        // console.log("TOKEN , ",credential.token);

        axios.defaults.headers.common["authorization"] =
          "Bearer " + credential.token;
        axios
          .get("https://vendeloyaglobal.herokuapp.com/route")
          .then((resultado) => {
            if (resultado.status == 200) {
              this.$router.push("/addProduct").catch(() => { });
            }
          });
      } else {
        this.isLogin = true;
        // console.log("NO ESTA LOGEADO");
      }
    },

    reserve() {
      this.loading = true;

      setTimeout(() => (this.loading = false), 2000);
    },
    islogen: function () {
      if (window.localStorage.getItem("user")) {
        this.isLogin = false;
        window.open(
          `https://wa.me/502${this.vendedorData.telefono}?text=Hola ${this.vendedorData.Nombre_Completo
          }!%0A%0AVi%20este%20producto%20en%20*VENDELO%20YA*.%0A%0AMe%20interesa:%0A*${this.product.titulo
          }*%0A${this.product.descripcion}%0A%0A${encodeURIComponent(
            `https://vendeloyaglobal.com/detalleProducto/${this.idProduct}`
          )}`
        );
      } else {
        this.isLogin = true;
        // console.log("NO LOGEADO EL USUARIO");
      }
    },

    irbtn: function () {
      api.getPhotoGalery(this.idProduct).then((result) => {
        var datos = result.data;
        var photo = datos.data;
        console.log("DATOS DE LA GALERIA :", photo); // /detalleProducto/:id/img=:clave; id_categoria
        this.$router.push(
          `/detalleProducto/${photo.id_pro}/cat=${this.product.id_categoria}/img=${photo.clave}`
        );
      });
      /* this.$router
          .push(`/detalleProducT/foto/firebasestorage.googleapis.com/v0/b/storage-imge.appspot.com/o/Products%2FCategoria_1%2F1648494654680_21595344_00c86d.jpg`);
   */
    },
    setFichasShared: function () {
      var data = {
        id_user: this.product.id_user,
        fichas: 10,
        fichas_detalle: "Por compartir a usuarios.",
      };
      api.CargarFichasUserRefiere(data);
    },

    getProductsPrincipal: function (id) {

      this.idProduct = this.$route.params.id;
      axios
        .get(`https://vendeloyaglobal.herokuapp.com/api/productos/${id}`)
        .then((result) => {
          api.detalleVendedor(id).then((vende) => {
            let vendedor = vende.data[0];
            this.vendedorData = vendedor;

            let product = result.data[0];
            this.product = product;

            if (store.getters.isLoggedIn) {
              var data = store.getters.getUser;
              if (this.codigo != undefined) {
                this.setFichasShared()
              }
              var user = data;
              this.calificar = true;
              this.idUser = user.id_user;
              var vista = {
                id_user: user.id_user,
                id_pro: this.product.id_pro,
              };
              api.createView(vista).then(() => {
                api
                  .getRanting(this.product.id_pro, user.id_user)
                  .then((result) => {
                    this.ranting = result.data[0];
                  });
              });
            } else {
              this.calificar = false;
            }
            /*  if (window.localStorage.getItem("user")) {
                if (this.codigo != undefined) {
                  this.setFichasShared()
                }
                var credential = JSON.parse(localStorage.getItem("user"));
                let user = credential;
                this.calificar = true;
                this.idUser = user.id_user;
                var vista = {
                  id_user: user.id_user,
                  id_pro: this.product.id_pro,
                };
                api.createView(vista).then(() => {
                  api
                    .getRanting(this.product.id_pro, user.id_user)
                    .then((result) => {
                      this.ranting = result.data[0];
                    });
                });
              } else {
                this.calificar = false;
              }*/

            this.shared = [
              //share-all
              {
                network: "facebook",
                url: `https://vendeloyaglobal.com/detalleProducto/${this.idProduct}/${this.product.codigo}`,
                title:
                  "Vi esto en *VENDERLO YA* :" +
                  " *" +
                  this.product.titulo +
                  "* ",
                description: this.product.descripcion,
                icon: "facebook",
                color: "blue",
              },
              {
                network: "messenger",
                url: `vendeloyaglobal.com/detalleProducto/${this.idProduct}/${this.product.codigo}`,
                title:
                  " Vi esto en *VENDERLO YA* :" +
                  " *" +
                  this.product.titulo +
                  "* ",
                description: this.product.descripcion,
                icon: "facebook-messenger",
                color: "pink",
              },
              {
                network: "whatsapp",
                url: `https://vendeloyaglobal.com/detalleProducto/${this.idProduct}/${this.product.codigo}`,
                title:
                  "Vi esto en *VENDERLO YA* :" +
                  " *" +
                  this.product.titulo +
                  "* ",
                description: this.product.descripcion,
                icon: "whatsapp",
                color: "green",
              },
            ];
          });
        });
    }
  },
  mounted() { },
  created() {
    let id = this.$route.params.id;
    this.getProductsPrincipal(id);

    this.$watch(
      () => this.$route.params.id,
      (toParams) => {
        this.getProductsPrincipal(toParams);
      }
    )


  },
};
</script>

<style scoped>
.c {box-shadow: 4;}

.precio-style {color: rgb(10, 222, 10);}
</style>