<template>
    <div>
        <v-row>
            <v-col v-for="item in publicidad" :key="item.id" cols="12" md="4">
                <v-card class="pb-2" elevation="4" rounded style="border-radius: 10px !important;">

                    <v-img class="white--text align-end img-publi" aspect-ratio="1.5" style="background-position: center;
                    background-size: cover;
                    background-repeat:no-repeat ;
                    max-width: 100%;" :src="item.url">

                        <v-app-bar flat color="rgba(0, 0, 0, 0)">

                            <v-spacer></v-spacer>

                            <v-spacer></v-spacer>

                        </v-app-bar>
                    </v-img>
                    <v-card-actions>
                        <p class="title-empresa">{{ item.empresa }}</p>

                        <v-spacer></v-spacer>
                        <div>
                            <!--<p v-if="item.facebook!=null || item.instagram!=null || item.whatsapp!=null || item.pagina!=null" style="margin-bottom: -12px; padding-bottom: 0px !important;">Contacto:</p>-->
                            <v-btn @click=" redirigir(item.facebook)" v-if="item.facebook != null" icon color="blue">
                                <v-icon>mdi-facebook</v-icon>
                            </v-btn>

                            <v-btn @click=" redirigir(item.instagram)" v-if="item.instagram != null" icon color="pink">
                                <v-icon>mdi-instagram</v-icon>
                            </v-btn>

                            <v-btn @click=" redirigir(item.whatsapp)" v-if="item.whatsapp != null" icon color="green">
                                <v-icon>mdi-whatsapp</v-icon>
                            </v-btn>

                            <v-btn @click=" redirigir(item.pagina)" v-if="item.pagina != null" icon>
                                <v-icon>mdi-web</v-icon>
                            </v-btn>
                        </div>
                    </v-card-actions>
                    <v-btn text color="green accent-4" @click="active(item.id)">
                        Ver descripcin
                    </v-btn>
                    <v-expand-transition>
                        <v-card  v-if="item.id == id" class="transition-fast-in-fast-out v-card--reveal"
                            style="height: 100%; border-radius: 10px !important;">
                            <v-card-text class="pb-0">

                                <p>{{item.descripcion}}</p>
                            </v-card-text>

                            <v-card-actions class="pt-0">
                                <v-btn text color="red accent-4" @click="active(null)">
                                    Cerrar
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-expand-transition>
                </v-card>
            </v-col>
        </v-row>
    </div>
</template>
<script>
import api from "@/models/controller";
import timePublicidad from "../../events/timePublicidadMarca"
export default {
    data() {
        return {
            reveal: false,
            publicidad: [],
            id: null
        }
    },

    methods: {
        active: function (id) {
            this.id = id;
        },
        allPublicidad() {
            api.allPublicidadPagada().then((res) => {

                var lista = res.data;
               // this.publicidad = lista.result;
                
                timePublicidad(lista.result).then((resp)=>{
                    this.publicidad =resp;
                })
            })
        },

        redirigir: function (item) {
            window.location.href = item
        }
    },

    created() {
        this.allPublicidad();
    }
}
</script>

<style>
.v-card--reveal {bottom: 0;opacity: 1 !important;position: absolute;width: 100%;}

.img-publi {background-position: center;background-size: cover;background-repeat: no-repeat;max-width: 100%;}

.title-empresa {margin-bottom: -10px !important;font-size: 16pt;color: black;}

.description {color: gray !important;}
</style>