<template>
  <div>
    <v-list nav dense>
      <v-list-item-group>
        <div v-if="isLogin == true">
          <v-list-item @click="navegateItem(item.value, item.folder, item.title)" color="green"
            v-for="(item, i) in navegation" :key="i">
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title style="font-size: 12pt;">{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item @click="salir" color="red">
              <v-list-item-icon>
                <v-icon color="red">{{ appdrawer.mdiExitRun }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title style="color: red">SALIR</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          <!--<v-footer app>
            <v-list-item @click="salir" color="red">
              <v-list-item-icon>
                <v-icon color="red">{{ appdrawer.mdiExitRun }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title style="color: red">SALIR</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-footer>-->
        </div>
        <div v-else>
          <v-list-item to="/">
            <v-list-item-title><v-icon color="green" aria-label="price" role="img" aria-hidden="false">
                {{ appdrawer.mdiStoreCheck }}</v-icon>
              Comprar</v-list-item-title>
          </v-list-item>
          <v-list-item to="/servicios">
            <v-list-item-title><v-icon aria-label="price" role="img" aria-hidden="false">
                {{ appdrawer.mdiAccountCash }}</v-icon>
              Servicios</v-list-item-title>
          </v-list-item>
          <v-list-item to="/nosotros">
            <v-list-item-title><v-icon aria-label="your" role="img" aria-hidden="false">
                {{ appdrawer.mdiCardAccountDetails }}</v-icon>
              Nosotros</v-list-item-title>
          </v-list-item>
          <v-list-item @click="logearme = true">
            <v-list-item-title><v-icon aria-label="login" role="img" aria-hidden="false">
                {{ appdrawer.mdiLogin }}</v-icon>
              Entrar</v-list-item-title>
          </v-list-item>
        </div>
      </v-list-item-group>
    </v-list>
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-toolbar dark color="green">
        <v-toolbar-title>{{ title }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-card class="grey lighten-5">
        <editProfile v-if="cajon == 'perfil'" />
        <ventas v-if="cajon == 'ventas'" />
        <products v-if="cajon == 'products'" />
        <historial v-if="cajon == 'historial_pagos'" />
        <fichas v-if="cajon == 'fichas'" />
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import store from '../../store'
import api from "../../models/controller";
import ventas from "@/components/UserProductos/ListaVentas.vue";
import products from "@/components/UserProductos/ListaProductos.vue";
import historial from "@/components/ProcesoBoletas/HistorialPagos.vue";
import fichas from "../../components/Perfil/Items/Fichas/Home.vue";
import { mdiArrowUpBoldCircle } from "@mdi/js";
import { mdiExitRun } from '@mdi/js';
import { mdiStoreCheck } from "@mdi/js";
import { mdiDomain } from '@mdi/js';
import { mdiAccountCash } from "@mdi/js";
import { mdiLogin } from "@mdi/js";
import { mdiBallot } from "@mdi/js";
import { mdiFolder } from "@mdi/js";
import { mdiCardAccountDetails } from "@mdi/js";
import { mdiStore } from '@mdi/js';
import { mdiCashRegister } from '@mdi/js';
import { mdiHandshake } from '@mdi/js';
export default {
  components: {
    ventas,
    historial,
    fichas,
    products,
  },
  data() {
    return {
      dialog: false,
      title: "",
      cajon: "",
      navegation: [],
      user: {},
      isLogin: false,
      appdrawer: {
        mdiFolder,
        mdiBallot,
        mdiStore,
        mdiCashRegister,
        mdiArrowUpBoldCircle,
        mdiStoreCheck,
        mdiDomain,
        mdiAccountCash,
        mdiLogin,
        mdiExitRun,
        mdiCardAccountDetails,
        mdiHandshake
      },
    };
  },
  methods: {
    salir() {
      store.dispatch('logout');
      window.localStorage.removeItem("user");
      this.$router.go();
    },

    navegateItem(href, item, title) {
      if (item == "servicios" || item == "vender" || item == "comprar" || item =='products' || item=='historial_pagos' || item== 'nosotros') {
        const path = href;
        if (this.$route.path !== path) this.$router.push(path);
      } else {
        this.cajon = item;
        this.title = title;
        this.dialog = true;
      }
    },
  },
  created() {
    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
      this.isLogin = true;
      var id = data;
      // this.user= user.user;
    
      api.getUser(id.id_user).then((result) => {
        this.user = result.data[0];
        this.navegation = [
          {
            icon: mdiArrowUpBoldCircle,
            title: "Vender producto",
            href: "/addProduct",
            value: `/addProduct`,
            folder: "vender",
          },
          /*  {
              icon: mdiStoreCheck,
              title: "Comprar",
              href: "/",
              value: `/`,
              folder: "comprar",
            }, */
          {
            icon: mdiBallot,
            title: "Mis productos",
            value: `/mis-productos`, ///mis-productos
            href: `/mis-productos`,
            folder: "products",
          },

          {
            icon: mdiStore,
            title: "Mis ventas",
            href: `/perfil/${this.user.id_user}/ventas`,
            value: `/perfil/${this.id}/ventas`,
            folder: "ventas",
          },
          {
            icon: mdiCashRegister,
            title: "Mis pagos",
            value: `/perfil/historial_pagos`,
            href: `/perfil/historial_pagos`,
            folder: "historial_pagos",
          },
          {
            icon: mdiDomain,
            title: "Servicios",
            value: `/servicios`,
            href: `/servicios`,
            folder: "servicios",
          },

          {
            icon: mdiHandshake,
            title: "Nosotros",
            value: `/nosotros`,
            href: `/nosotros`,
            folder: "nosotros",
          },
        ];
      });
      // console.log("ESTA LOGEADO EL USUARIO :",this.user);
     
  }
  else {
      this.isLogin = false;
    }
  },
};
</script>