<template>
    <v-container>
        <div class="mx-auto" text-center style="text-align: center !important;">
            <v-icon class="mt-2" size="120" color="success">mdi-security</v-icon>
            <p class="mt-3 text-h5 font-weight-bold">AUTENTICA TU CUENTA</p>
            <div class="mb-5">
                Como manera de proteger tu cuenta te solicitamos poder autenticarla ya que as aseguramos que la informacin
                brindada sea verdica.
            </div>
            <div class="ma-auto position-relative" style="max-width: 300px">
                <v-otp-input length="4" v-model="otp" :disabled="loading" @finish="onFinish"></v-otp-input>
                <v-overlay absolute :value="loading">
                    <v-progress-circular indeterminate color="primary"></v-progress-circular>
                </v-overlay>
            </div>

            <div>
                Ingresa el cdigo que ha sido enviado a tu  correo electrnico:<span class="font-weight-bold"> {{ user.email }}</span>
            </div>
            <!--<div class="text--caption">Type or copy/paste.</div>-->
            <!--<v-btn v-if="snackbar" large rounded outlined class="mt-8">Continuar</v-btn>-->

            <v-snackbar v-model="snackbar" :color="snackbarColor" :timeout="2000">
                {{ text }}
            </v-snackbar>
        </div>
    </v-container>
</template>
  
<script>
import store from '../../store'
import api from '../../models/controller'
export default {
    data: () => ({
        loading: false,
        snackbar: false,
        snackbarColor: 'default',
        otp: '',
        text: '',
        expectedOtp: '',
        idUser: null,
        user: {}
    }),
    methods: {
        onFinish(rsp) {
            this.loading = true
            setTimeout(() => {
                this.loading = false
                this.snackbarColor = (rsp === this.expectedOtp) ? 'success' : 'red'
                if (this.snackbarColor == 'success') {
                    api.getVeryEmailByUser(this.user.id_user, this.user.code_verify).then(() => {
                        this.text = `Verificacin exitosa!`
                        this.snackbar = true
                        this.$router.push("/").catch(() => {});
                    })
                } else {
                    this.text = `Favor de ingresar un cdigo valido!`
                        this.snackbar = true
                   this.loading=false
                }

            }, 3500)
        },
    },

    created() {

        window.scrollTo(0, 0);
        if (store.getters.isLoggedIn) {
            var data = store.getters.getUser;
            api.getUser(data.id_user).then((result) => {
                this.user = result.data[0];
                this.expectedOtp = this.user.code_verify;
            })

        }
    }
}
</script>
<style scoped>
.position-relative {
    position: relative;
}
</style>
