<template>
    <div class="text-center mt-10">

        <v-alert v-if="isSuccess == true" dense text type="success" class="mt-4 mb-3">
            Datos enviados correctamente.
        </v-alert>

        <p v-if="tipo == 'Inversion'" style="font-size: 15pt;">Ser Inversionista</p>
        <p v-else style="font-size: 15pt;">Socio Estratgico</p>
        <v-form ref="form" v-model="valid" lazy-validation @submit.prevent="validate">
            <v-text-field name="to_name" rounded filled v-model="name" :rules="nameRules" label="Nombre Completo" required></v-text-field>
           
            <v-text-field v-if="tipo != 'Socio'" style="display: none" name="message" rounded filled value="Inversionista"></v-text-field>
           
            <div v-if="tipo == 'Socio'">
                <v-text-field style="display: none" name="message" rounded filled value="Socios Estratgico"></v-text-field>
           
                <v-text-field rounded filled v-model="empresa" :rules="Resempresa" label="Nombre Empresa"
                    required></v-text-field>
                <v-text-field rounded filled v-model="puestoEmpresa" :rules="nameRules" label="Puesto dentro de la Empresa"
                    required></v-text-field>
                <div style="text-align: left !important;" class="text-subtitle-1 text-medium-emphasis">Qu tipo de alianza
                    estrategica buscas?</div>

                <v-select class="col-pad" required rounded v-model="tipoAlianza" :items="itemAlianza" filled
                    label="Ingrese datos" persistent-hint single-line :rules="[v => !!v || 'Item is required']"></v-select>
            </div>

            <div>

                <v-row no-gutters>
                    <v-col cols="12" md="6">

                        <v-text-field name="from_email" class="col-pad" rounded filled v-model="email" :rules="emailRules" label="E-mail"
                            required></v-text-field>

                    </v-col>
                    <v-col cols="6" md="3">
                        <v-text-field style="margin-right: 4px !important;" class="col-pad" persistent-hint
                            hint="Ingresa sin +" rounded filled type="text" v-model="codigo" label="Cdigo Pais"
                            required></v-text-field>

                    </v-col>
                    <v-col cols="6" md="3">
                        <v-text-field rounded filled type="tel" v-model="tel" label="Telfono" required></v-text-field>

                    </v-col>

                    <v-col cols="12" md="6">
                        <v-select class="col-pad" required rounded v-model="selectPais" :items="paises" item-value="id"
                            filled item-text="nombre" label="Pais" persistent-hint return-object single-line
                            :rules="[v => !!v || 'Item is required']"></v-select>

                    </v-col>
                    <v-col cols="12" md="6" class="" v-if="tipo != 'Socio'">
                        <v-text-field rounded filled v-model="inversion" :rules="costo" prefix="$"
                            label="Cunto deseas invertir?" required></v-text-field>
                    </v-col>
                </v-row>

            </div>



            <v-textarea :rules="description" outlined name="input-7-4"
                label="Porque desesas invertir en nuestra plataforma de VENDELO YA?" v-model="descripcion"></v-textarea>

            <v-checkbox v-model="checkbox" :rules="[v => !!v || 'You must agree to continue!']"
                label="Permitido que mis datos sean usados para que se contacten conmigo" required></v-checkbox>


            <input :disabled="!valid"    class="submit mr-4" type="submit" name="send"  :value="procesoEnvio "/>
        </v-form>
        <!--<v-snackbar v-model="isSuccess" :timeout="timeout" color="success">
            {{ text }}

            <template v-slot:actions>
                <v-btn color="success" @click="snackbar = false">
                    Cerrar
                </v-btn>
            </template>
        </v-snackbar>-->

        <v-dialog
      v-model="isSuccess"
      persistent
      width="auto"
    >

      <v-card class="text-center">
        <v-icon class="mt-10" color="success" size="120">mdi-check-circle</v-icon>

        <v-card-text>Dentro de las siguientes 24 horas se estarn contactando contigo. </v-card-text>
        <v-card-text>Gracias por tu tiempo en espera!  </v-card-text>
        <v-card-text> Se te asign el asesor de VENDELO YA quin se estar contactando contigo en un lapso de 24 horas para el seguimiento de tu solicitud.</v-card-text>
        <v-card-actions>

          <v-btn block rounded outlined 
            color="green"
            variant="text"
            @click="irInicio"
          >
           De acuerdo
          </v-btn>
        </v-card-actions>
        <br>
      </v-card>
    </v-dialog>
    </div>
</template>

<script>
import api from "@/models/controller";
import apiInversion from "@/models/controllerInversion";
import veryemail from "@/models/email"

export default {
    props: { tipo: null },
    data: () => ({
        itemAlianza: ['Alianza Mercadotecnia',
            'Alianza Porductos',
            'Alianza Para el desarrollo de tecnologia y desarrollo de porductos',
            'Alianza no lucrativas'],
        text: 'Datos enviados correctamente.',
        timeout: 2000,
        procesoEnvio: 'Enviar Datos',
        isSuccess:false,
        paises: [],
        inversion: null,
        descripcion: '',
        puestoEmpresa: null,
        tipoAlianza: null,

        codigo: null,
        empresa: null,

        selectPais: { nombre: "Seleccionar", id: null },
        valid: true,
        name: '',
        nameRules: [
            v => !!v || 'Campo requerido.',

        ],

        description: [
            v => !!v || 'Campo requerido.',

        ],

        Resempresa: [
            v => !!v || 'Campo requerido.',

        ],
        costo: [
            v => !!v || 'Campo requerido.',

        ],
        email: '',
        tel: '',
        emailRules: [
            v => !!v || 'E-mail es requerido',
            v => /.+@.+\..+/.test(v) || 'E-mail no vlido',
        ],
        select: null,
        items: [
            'Item 1',
            'Item 2',
            'Item 3',
            'Item 4',
        ],
        checkbox: false,
    }),

    methods: {

        IrInicio:function(){
            this.isSuccess = false;
            this.$router.replace("/").catch(() => { });
        },
        validate: function (e) {

            if (this.$refs.form.validate()) {
                this.procesoEnvio = 'Enviando datos...';
                var fecha = new Date();
                var data = {}

                window.setTimeout(() => {
                    var create;
                    if (this.tipo == 'Inversion') {

                        data = {
                            idPais: this.selectPais.id,
                            nombre: this.name,
                            email: this.email,
                            codigotel: this.codigo,
                            telefono: this.tel,
                            moneda: '$',
                            cantInversion: this.inversion,
                            motivo: this.descripcion,
                            estado: 'Enviado',
                            permiso: this.checkbox,
                            fecha: fecha.toISOString(),
                        }
                        create = apiInversion.createInversion(data)
                    } else {
                        data = {
                            idPais: this.selectPais.id,
                            nombre: this.name,
                            empresa: this.empresa,
                            tipoAlianza: this.tipoAlianza,
                            puestoEmpresa: this.puestoEmpresa,
                            email: this.email,
                            codigotel: this.codigo,
                            telefono: this.tel,
                            moneda: '$',
                            cantInversion: this.inversion,
                            motivo: this.descripcion,
                            estado: 'Enviado',
                            permiso: this.checkbox,
                            fecha: fecha.toISOString(),
                        }
                        create = apiInversion.createSociosEstrategicos(data)
                    }

                    create.then(() => {

                        veryemail.sendEmailInverSocio(e.target)
                        this.isSuccess = true;
                        this.procesoEnvio = 'Datos Enviados';
                        window.setTimeout(() => {
                            this.procesoEnvio = 'Enviar Datos';
                        }, 2000)
                        this.reset();
                    })
                }, 2000)


            } else {
                console.log("Error")
            }

        },
        reset() {
            this.$refs.form.reset()
        },
        resetValidation() {
            this.$refs.form.resetValidation()
        },

        getAllPais: async function () {
            await api.getAllPais().then((result) => {
                var pa = result.data;
                var list = pa.data;
                list.forEach((element) => {
                    this.paises.push({
                        nombre: element.nombrePais,
                        id: element.idPais,
                    });
                });
            });
        },
    },

    created() {

        this.$watch(
            () => this.tipo,
            (toParams) => {
                this.tipo = toParams;
            })

        this.getAllPais();
    }
}
</script>

<style>
@media screen and (min-width: 550px) {

    .col-pad {
        margin-right: 4px !important;
    }

}

.submit{
    color:white;
    border-radius: 10px;
    padding:5px;
    width: 100%;
    background: green;
}
</style>