<template>
    <div>
        <h4 class="ml-2 font-weight-bold">Informacin</h4>
        <v-list>
            <v-list-item v-for="(item, i ) in info" :key="i" style=" margin-top:-20px">
                <v-list-item-icon>
                    <v-icon v-if="item.icon">
                        mdi-{{ item.icon }}
                    </v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ item.detail }}</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-list>
    </div>
</template>

<script>
import api from '../../models/controller'
export default {
    props: { id: null },
    data() {
        return {
            loading: null,
            seller: {},
            idSeller: null,
            info: []
        }
    },
    created() {
        this.idSeller = this.id

        this.$watch(
            () => this.id,
            (toParams) => {
                this.idSeller = toParams;
            }
        )
        api.getUser(this.idSeller).then((result) => {
            this.seller = result.data[0];
            this.info = [{ icon: 'phone', detail: this.seller.telefono }, { icon: 'email', detail: this.seller.email }, { icon: 'directions-fork', detail: this.seller.direccion } ]

        });
    }
}
</script>