<template>
  <div>
    <sinregistros v-if="this.products.length==0" :title="'Auch, an no has subido productos'" :total="this.products.length" :tipo="'products'"/>
    <productos v-else></productos>
  </div>

</template>
<script>
import api from "../../../../models/controller";
import sinregistros from "../../SinRegistros.vue";
import productos from '@/components/UserProductos/ListaProductos.vue';

import store from '../../../../store'
export default {
  components: { sinregistros, productos },
  data() {
    return {
      products: [],
    };
  },
  // Vuetify components provide
  // a theme variable that is
  // used to determine dark
  inject: {
    theme: {
      default: { isDark: false },
    },
  },

  created() {

    ///mis-productos
    if (store.getters.isLoggedIn) {
      var data = store.getters.getUser;
      try {
        api.getUserAllProduct(data.id_user).then((result) => {
          this.products = result.data;
        });
      } catch (error) {
        console.log(error);
      }
    } else {
      console.log("Hay que salir de ac");
    }
  },
};
</script>

<style>

.color{
color: #fbde04;  
}
</style>