<template>
    <div class="pl-1 pr-1 pt-1">
        <sinregistros :title="'Auch, an no has vendido.'" :total="ventas.length" :tipo="'productos'" />


        <v-list three-line>
            <template v-for="(item, index) in ventas">
                <v-subheader v-if="item.header" :key="item.header" v-text="item.header"></v-subheader>

                <v-divider v-else-if="item.divider" :key="index" :inset="item.inset"></v-divider>

                <v-list-item v-else :key="item.titulo">
                    <v-list-item-avatar tile size="100" style="border-radius: 10px !important;">
                        <v-img :src="item.thumbnail"></v-img>
                    </v-list-item-avatar>

                    <v-list-item-content>
                        <v-list-item-title style="margin-bottom: -40px;" class="text-h5">Q{{ item.precio
                        }}</v-list-item-title>
                        <v-list-item-subtitle style="margin-top: -10px;" class="text-h6">{{ item.titulo
                        }}</v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-icon class="mt-12">
                        <v-btn icon @click="comprarPlan(item)">
                            <v-icon color="success" size="40">
                                mdi-chevron-right
                            </v-icon>
                        </v-btn>

                    </v-list-item-icon>
                </v-list-item>
            </template>
        </v-list>
        <buyplan v-on:input="onResultados" v-if="buyPro" :product="buyPro" v-model="showScheduleForm"></buyplan>

    </div>
</template>
    
<script>
//import axios from "axios";
import store from '../../store'
import api from "../../models/controller";
import sinregistros from "@/components/Perfil/SinRegistros";
//import AppBar from "@/components/AppBar/AppbarGeneral.vue";
import buyplan from "@/components/Buyplan/Home.vue";

import { mdiCrownCircle } from "@mdi/js";
import { mdiAccount } from "@mdi/js";
import { mdiCrown } from "@mdi/js";
export default {
    components: { sinregistros, buyplan },
    data() {
        return {
            edit: false,
            cajon: null,
            id_pro: null,
            id_cat: null,
            dialogListPro: false,
            product: {},
            activeDelete: false,
            buyPro: null,
            showScheduleForm: false,
            ventas: [],
            planes: [],
            itemsProducts: [
                { title: 'Promocionar' },
                { title: 'Editar' },
                { title: 'Eliminar' },
            ],
            icons: {
                mdiCrown,
                mdiCrownCircle,
                mdiAccount,
            },
            open: [1, 2],
            search: null,
            caseSensitive: false,
            galery: []
        };
    },

    methods: {
        onResultados: function (item) {
            this.showScheduleForm = item;
            this.edit = false;
            this.buyPro = null;
            this.id_pro = null;
            this.id_cat = null;
        },
        comprarPlan(item) {
            this.buyPro = item.id_pro;
            this.showScheduleForm = true;
        },
        getAllProduct: function () {
            if (store.getters.isLoggedIn) {
                var data = store.getters.getUser;
                try {
                    api.getUserAllProduct(data.id_user).then((result) => {
                        var listaPro = [];
                        listaPro = result.data;
                        this.ventas.push({ header: 'Mis Productos' });
                        for (let i in listaPro) {
                            this.ventas.push(listaPro[i]);
                            this.ventas.push({ divider: true, inset: true },)
                        }
                    });
                } catch (error) {
                    console.log(error);
                }
            } else {
                console.log("Hay que salir de ac");
            }
        }
    },
    computed: {
        keywords() {
            if (!this.search) return [];
            const keywords = [];
            for (const search of this.searching) {
                keywords.push(search.keyword);
            }
            return keywords;
        },
        searching() {
            if (!this.search) return this.ventas;
            const search = this.search.toLowerCase();
            return this.ventas.filter((item) => {
                const text = item.titulo.toLowerCase();
                return text.indexOf(search) > -1;
            });
        },
    },
    created() {
        this.getAllProduct();
    },
};
</script>
  
<style>
.card-plan {
    display: grid;
    border-radius: 20px !important;
    background: rgb(117, 248, 117) !important;
    border: solid rgb(34, 189, 34) 2px !important;
}

.div-publicidad {
    align-self: center;
    justify-self: center;
}
</style>