<template>
    <div v-if="productos.length > 0">
        <v-list class="px-10" style="background: #0eb308; width: 100%;" two-line>
            <v-list-item @click="verDetalle(item.id_pro)" style="margin-left:-30px; margin-top:-20px;" v-for="item in productos" :key="item.id_pro">
                <v-list-item-avatar>
                    <v-img :src="item.thumbnail"></v-img>
                </v-list-item-avatar>

                <v-list-item-content>
                    <v-list-item-title>{{ item.titulo }}</v-list-item-title>
                    <v-list-item-subtitle>{{ item.descripcion }}</v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>
            
        </v-list>
    </div>
</template>
<script>
import api from "@/models/controller";
import store from "@/store"

export default {
    props: ["search"],
    data: () => ({
        productos: [],
        items: [
            { header: 'Today' },
            {
                avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
                title: 'Brunch this weekend?',
                subtitle: `<span class="text--primary">Ali Connors</span> &mdash; I'll be in your neighborhood doing errands this weekend. Do you want to hang out?`,
            },
            { divider: true, inset: true },
            {
                avatar: 'https://cdn.vuetifyjs.com/images/lists/2.jpg',
                title: 'Summer BBQ <span class="grey--text text--lighten-1">4</span>',
                subtitle: `<span class="text--primary">to Alex, Scott, Jennifer</span> &mdash; Wish I could come, but I'm out of town this weekend.`,
            },
            { divider: true, inset: true },
            {
                avatar: 'https://cdn.vuetifyjs.com/images/lists/3.jpg',
                title: 'Oui oui',
                subtitle: '<span class="text--primary">Sandra Adams</span> &mdash; Do you have Paris recommendations? Have you ever been?',
            },
            { divider: true, inset: true },
            {
                avatar: 'https://cdn.vuetifyjs.com/images/lists/4.jpg',
                title: 'Birthday gift',
                subtitle: '<span class="text--primary">Trevor Hansen</span> &mdash; Have any ideas about what we should get Heidi for her birthday?',
            },
            { divider: true, inset: true },
            {
                avatar: 'https://cdn.vuetifyjs.com/images/lists/5.jpg',
                title: 'Recipe to try',
                subtitle: '<span class="text--primary">Britta Holt</span> &mdash; We should eat this: Grate, Squash, Corn, and tomatillo Tacos.',
            },
        ],
    }),

    methods: {
        verDetalle:function(id) {
            var id_pro= id;

      if (store.getters.isLoggedIn) {
        var data = store.getters.getUser;
        let vista = {
          id_user: data.id_user,
          id_pro: id_pro,
          like: 0,
        };
        api.createView(vista).then(() => {
        });
      }
      // this.$router.push("/detalleProducto", {id_pro: id});
      this.$router.push({
        path: `/detalleProducto/${id_pro}`,
        id_pro: { id_pro },
      });

      //window.location.href= `/detalleProducto/${id}`
    },

        getProducts: function (data) {
            //console.log("DATOS DE SERACH :", data);
            api.getSearchGeneral(data).then((result) => {
                var datos = result.data;
                if (datos.length > 0) {
                    this.productos = datos.reverse();
                }
            });

            console.log("DATOS DE SERACH :", this.productos);
        }

    },

    created() {
        //  this.search=this.search
        this.$watch(
            () => this.search,
            (toParams) => {
                console.log("Datos del serach ", toParams)

                if (toParams != '') {

                    window.setTimeout(() => {
                    this.getProducts(toParams)
                }, 2000)
                } else if (toParams == '') {
                    this.productos=[];
                }


                // react to route changes...
            }
        )

    }
}
</script>