          <v-btn @click=" navegar(4)" dark style="width: 200px; border-radius: 15px; border:solid 1px white;"
            color="rgba(156, 2, 136, 0.63)" dense class="btn-ir">Ms detalles</v-btn>
        </div>
        <div class="carousel-item" data-bs-interval="2000" v-for="item in publicidad" :key="item.id">
          <img style="max-width: 100%" :src="item.url" class="d-block w-100 img-fluid" alt="...">
          <div class="descripcion">
            <div class="contenido">
              {{ item.descripcion }}
