<script>
import api from "@/models/controller";
import timePublicidad from "../events/timePublicidadMarca"

export default {
  data() {
    return {
      publicidad: [],
    }
  },
  methods: {
    navegar: function (item) {
      if (item != 4) {


        if (item.linkPrincipal == 'facebook') {
          window.location.href = `${item.facebook}`
        } else if (item.linkPrincipal == 'instagram') {
          window.location.href = `${item.instagram}`
        } else if (item.linkPrincipal == 'whatsapp') {
          window.location.href = `${item.whatsapp}`
        } else if (item.linkPrincipal == 'pagina') {
          window.location.href = `${item.pagina}`
        }
      } else {
        window.location.href = `https://www.facebook.com/globaltechnologygt`
      }

    },
    allPublicidad() {
      api.allPublicidadCorousel().then((res) => {
        var lista = res.data;
        timePublicidad(lista.result).then((resp) => {
          this.publicidad = resp;
        })

      })
    }
  },


  created() {
    this.allPublicidad();
  }
}
</script>

