<template>
  <div class="card mt-3 ml-2 mr-2" style="box-shadow: none; border: none">
    <div class="card-body" style="padding: 0px">
      <div class="card-title">
        <h3>Comentarios</h3>
        <hr />
      </div>
      <div class="card-body" style="padding: 0px; margin-top: -15px">
        <v-row style="padding: 0px" v-for="(msg, index) in messages" :key="index">
          <v-col cols="2">
            <v-avatar class="img-fluid">
              <img style="object-fit: cover; width: 50px; height: 50px" class="img-fluid" :src="msg.img"
                :alt="msg.user" />
            </v-avatar>
          </v-col>
          <v-col cols="10" class="position:relative">
            <v-menu bottom left v-if="idUser == msg.id_user">
              <template v-slot:activator="{ on, attrs }">
                <v-btn style="position: absolute; right: 2px" icon v-bind="attrs" v-on="on">
                  <v-icon>mdi-dots-vertical</v-icon>
                </v-btn>
              </template>

              <v-list>
                <v-list-item v-for="(item, i) in items" :key="i" @click="deletecomentary(msg)">
                  <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
            <span class="font-weight-bold">{{ msg.user }} </span>
            <p style="margin-bottom: 0px">
              <span>{{ msg.mensaje }}</span>
            </p>
            <span style="font-size: 6pt; padding-top: 0px; margin-top: 0px">{{
              msg.fecha
            }}</span>
          </v-col>
        </v-row>
      </div>
    </div>
    <br />
    <p v-if="!isLogin" class="text-center">
      Debes
      <a class="link" @click="isLoginActive = true">Iniciar Sesin</a> para
      poder comentar.
    </p>
    <v-form v-else style="margin: 0px; padding: 0px; width: 100%">
      <v-text-field rounded v-model="message" :append-outer-icon="message ? 'mdi-send' : undefined" filled
        clear-icon="mdi-close-circle" clearable label="Comentario" type="text" @click:append="toggleMarker"
        @click:append-outer="sendMessage" @click:prepend="changeIcon" @click:clear="clearMessage"></v-text-field>
    </v-form>
    <login v-model="isLoginActive" />
  </div>
</template>
<script>
//import axios from "axios";
import api from "../../models/controller";
import notificar from "../../models/controllerOnesignal"
import io from "socket.io-client";
import store from "@/store"
import login from "@/components/Login/Login.vue";
export default {
  components: { login },
  props: {
    id_proview: null, title: null
  },
  data() {
    return {
      progress: null,
      user: {},
      idUser: "",
      message: "",
      messages: [],
      socket: io("https://vendeloyaglobal.herokuapp.com"),
      isLogin: null,
      isLoginActive: null,
      password: "Password",
      show: false,
      marker: true,
      iconIndex: 0,
      icons: [
        "mdi-emoticon",
        "mdi-emoticon-cool",
        "mdi-emoticon-dead",
        "mdi-emoticon-excited",
        "mdi-emoticon-happy",
        "mdi-emoticon-neutral",
        "mdi-emoticon-sad",
        "mdi-emoticon-tongue",
      ],
      items: [{ title: "Eliminar" }],
    };
  },
  methods: {
    deletecomentary: function (item) {
      api.deleteComentario(item.id, item.id_pro, item.id_user).then(() => {
        this.getComentario()
      });
    },
    sendMessage: function () {
      if (this.message != "") {
        this.socket.emit("nuevo", {
          id_pro: this.id_pro,
          id_user: this.user.id_user,
          nombre_user: this.user.nombres + " " + this.user.apellidos,
          mensaje: this.message,
          img: this.user.photo,
        });

        notificar.notifiCliente('comentario', this.user.id_user, this.title, 'Alguien a comentado: *' + this.message);
        this.resetIcon();
        this.clearMessage();
      } else {
        console.log("SIN COMENTARIO");
      }
    },
    toggleMarker() {
      this.marker = !this.marker;
    },

    clearMessage() {
      this.message = "";
    },
    resetIcon() {
      this.iconIndex = 0;
    },
    changeIcon() {
      this.iconIndex === this.icons.length - 1
        ? (this.iconIndex = 0)
        : this.iconIndex++;
    },

    getComentario: function () {
      this.messages = [];
      try {
        if (store.getters.isLoggedIn) {
          this.isLogin = true;
          var data = store.getters.getUser;
          this.idUser = data.id_user;
          api.getUser(data.id_user).then((result) => {
            this.user = result.data[0];
            api.getUserComentario(this.id_pro).then((result) => {
              let comentario = [];
              comentario = result.data;
              comentario.forEach((element) => {
                this.messages = [...this.messages, element];
              });
            });
          });
        }else {
          this.isLogin = false;
        }
       /* if (localStorage.getItem("user")) {
          this.isLogin = true;
          var credential = JSON.parse(localStorage.getItem("user"));
          const idUser = credential;
          this.idUser = idUser.id_user;
          api.getUser(idUser.id_user).then((result) => {
            this.user = result.data[0];
            api.getUserComentario(this.id_pro).then((result) => {
              let comentario = [];
              comentario = result.data;
              comentario.forEach((element) => {
                this.messages = [...this.messages, element];
              });
            });
          });

        } else {
          this.isLogin = false;
        }*/
      } catch (error) {
        console.log(error);
      }
    },
  },
  mounted() {
    this.socket.on("refrescar", (data) => {
      let datos = {
        id_pro: data.id_pro,
        id_user: data.id_user,
        user: data.nombre_user,
        mensaje: data.mensaje,
        img: data.img,
        fecha: "Hace un momento",
      };
      this.messages = [...this.messages, datos];
    });

    this.getComentario();
  },
  computed: {
    icon() {
      return this.icons[this.iconIndex];
    },
  },
  created() {

    this.id_pro = this.id_proview;

    this.$watch(
      () => this.id_proview,
      (toParams) => {
        this.id_pro = toParams;
        this.getComentario();
        window.scrollTo(0, 0);
      }
    )

    this.getComentario();


  },
};
</script>

<style scoped>
.link {
  color: rgb(3, 206, 3) !important;
  font-weight: bolder;
  text-decoration: underline;
}
</style>