<template>
  <div>

    <v-app class="d-none d-sm-flex d-md-flex d-xl-flex d-lg-flex">
      <v-navigation-drawer app>
        <v-list>
          <v-list-item class="">
            <v-list-item-icon>
              <v-icon size="40">mdi-view-dashboard</v-icon>
            </v-list-item-icon>
            <v-list-item-content>

              <v-list-item-title>
                Mi perfil
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-divider></v-divider>
          <v-list-item-group v-model="model">
            <v-list-item color="green" v-for="(item, i) in items" :key="i" @click="cajon = item.folder">
              <v-list-item-icon>
                <v-icon size="40" v-text="item.icon"></v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title v-text="item.text"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-navigation-drawer>


      <editProfile :user="user" v-if="cajon == 'profile'" class="d-none d-sm-flex d-md-flex d-xl-flex d-lg-flex" />
      <ventas v-if="cajon == 'ventas'" />
      <products v-if="cajon == 'products'" />
      <historial v-if="cajon == 'historial_pagos'" />
      <fichas v-if="cajon == 'fichas'" />

    </v-app>

    <v-btn class="mt-1 ml-1 d-flex d-sm-none" style="position: absolute; top: 0px; right: 4px; z-index: 3" icon
      color="white" @click="back()">
      <v-icon size="35">mdi-close</v-icon>
    </v-btn>
    <movil style="width: 100%;" class="d-flex d-sm-none" />
  </div>
</template>

<script>
import store from '../../store'
//import axios from "axios";
//import AppBar from "@/components/AppBar/AppbarGeneral.vue";
import api from "../../models/controller";
import movil from "./PerfilMovil.vue";
import editProfile from "./PerfilEdit.vue";
import fichas from "./Items/Fichas/Home.vue";
import products from "./Items/Products/Home.vue";
import ventas from "@/components/UserProductos/ListaVentas.vue";
import historial from "@/components/ProcesoBoletas/HistorialPagos.vue";
export default {
  components: {/*AppBar,*/ ventas, historial, movil, editProfile, fichas, products },
  props: ["id", "folder"],
  data() {
    return {
      user: {},
      cajon: "profile",
      selectedItem: 0,
      model: 0,
      items: [
        {
          icon: "mdi-account-edit",
          text: "Mi perfil",
          value: `/perfil/${this.id}/perfil`,
          folder: "profile",
        },
        {
          icon: "mdi-format-list-text",
          text: "Mis productos",
          value: `/perfil/${this.id}/productos`,
          folder: "products",
        },
        {
          icon: "mdi-store",
          text: "Mis ventas",
          value: `/perfil/${this.id}/ventas`,
          folder: "ventas",
        },
        {
          icon: "mdi-cash-register",
          text: "Mis pagos",
          value: `/perfil/${this.id}/historial_pagos`,
          folder: "historial_pagos",
        },
        {
          icon: "mdi-currency-usd",
          text: "Mis fichas",
          value: `/perfil/${this.id}/historial_pagos`,
          folder: "fichas",
        },
        {
          icon: "mdi-cog",
          text: "Configuraciones",
          value: `/perfil/${this.id}/configuration`,
          folder: "configuration",
        },
      ],
    };
  },

  methods: {
    convertir() {
      this.cajon = this.folder;
    },

    back() {
      window.history.back();
    },
  },
  computed: {},
  created() {
    //window.scrollTo(0, 0);

    if (store.getters.isLoggedIn) {

      try {
        this.$watch(
          () => this.folder,
          (toParams) => {
            // react to route changes...
            this.cajon = toParams;
          }
        );
        this.cajon = 'profile';
        //getUser(id)

        api.getUser(this.id).then((result) => {
          this.user = result.data[0];
          //console.log("Datos del Usuario array: ",  this.user);
        });
      } catch (error) {
        console.log(error);
      }
    }
  },
};
</script>

<style scoped>
.item-avatar {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>