<template>
     <v-list subheader>
      <v-subheader>PREVIOS</v-subheader>
      <v-list-item v-for="chat in previous" :key="chat.nombre">
        <v-list-item-avatar>
          <v-img :alt="`${chat.nombre} avatar`" :src="chat.photo"></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title :v-text="chat.nombre"></v-list-item-title>
        </v-list-item-content>
        <v-list-item-icon >
          <v-icon> mdi-chat </v-icon>
        </v-list-item-icon>
      </v-list-item>
    </v-list>
</template>
<script>

import api from "../../models/controller";
//import chat from "@/components/Chat/ChatComplete.vue";
export default {
    components:{ /*chat*/},
  data: () => ({
    recent: [],
    previous: [],
    activarDialog: false,
    vendedor: null
  }),
  methods:{
      islogen(id) {
      if (localStorage.getItem("user")) {
        this.activarDialog = true;
        this.vendedor= id;
      }
    },
  },
  created() {
    if (localStorage.getItem("user")) {
      var credential = JSON.parse(localStorage.getItem("user"));
      const idUser = credential.user;
       api.getChatsLeidos(idUser.id_user).then((results) => {
        this.previous = results.data;
      });
    }
  },
};
</script>