<template>
<div>
  <AppBar title="Detalles del vendedor"/>
    <v-img :src="vendedor.photo" height="300px" dark>
     
    </v-img>
    <v-card-title class="pl-12 black--text">
      <div class="text-h5 font-weight-bolder text-center">
        {{ vendedor.Nombre_Completo }}
      </div>
    </v-card-title>
    <span class="pl-12">Vendedor</span>
    <v-list two-line>
      <v-list-item>
        <v-list-item-icon>
          <v-icon color="indigo"> mdi-phone </v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>(502) {{ vendedor.telefono }}</v-list-item-title>
          <v-list-item-subtitle>Celular</v-list-item-subtitle>
        </v-list-item-content>

        <v-list-item-icon @click="islogen(vendedor)">
          <v-icon large color="success">mdi-whatsapp</v-icon>
        </v-list-item-icon>
      </v-list-item>

      <v-divider inset></v-divider>

      <v-list-item>
        <v-list-item-icon>
          <v-icon color="indigo"> mdi-email </v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>{{ vendedor.email }}</v-list-item-title>
          <v-list-item-subtitle>Correo Personal</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
         <v-list-item>
        <v-list-item-icon>
          <v-icon color="indigo"> mdi-email </v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>{{ vendedor.email }}</v-list-item-title>
          <v-list-item-subtitle>Correo Personal</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>


    </v-list>

    <chat v-on:DialogChat="OnCerrar" v-if="vendId!= null" :photo="vendedor.photo" :nombre="vendedor.Nombre_Completo" :id_user="vendedor.id_user" v-model="activarDialog" />
  </div>
</template>

<script>
import axios from "axios";
import { mdiChat } from "@mdi/js";
import chat from "@/components/Chat/ChatComplete.vue";
import AppBar from "@/components/AppBar/AppBarProduct.vue";
export default {
  props: {
    value: Boolean,
    id: null,
  },
  components: { chat,  AppBar  },
  data() {
    return {
      vendedor: {},
      title: "",
      vendId: null,
      icons: {
        mdiChat,
      },
      activarDialog: false,
    };
  },

  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
  
  methods: {
    irChat: function (id) {
      this.$router.push(`/chat/${id}`).catch(() => {});
    },
   

    islogen(e) {
      console.log(e)
      if (window.localStorage.getItem("user")) {
       /* this.activarDialog = true;
        this.vendId=e.id_user; */
      }
    },

       OnCerrar(valor) {
        this.vendId= valor;
    },
  },

  created() {
    if (window.localStorage.getItem("user")) {
      axios
        .get(
          `https://vendeloyaglobal.herokuapp.com/api/contacto_vendedor/${this.$route.params.id}`
        )
        .then((result) => {
          let vendedor = result.data[0];
          this.vendedor = vendedor;
        });
    } else {
      console.log("NO AUTORIZADO");
    }
  },
};
</script>