<template>
  <v-container>

    <v-app-bar fixed  style="border-radius: none !important; height:89px; top:0px; box-shadow: none !important; margin-top:56px !important; background: white !important"  class="pt-10" color="rgba(0, 0, 0, 0)" >
          <v-btn class="mb-7"  icon  @click="retrocer">
      <v-icon large>{{ icons.mdiArrowLeftCircle }}</v-icon>
    </v-btn>
      <v-spacer></v-spacer>
      <v-text-field
      dense
        ref="search"
        v-model="search"
        class="border-3"
        rounded
        solo
        label="Buscar en vendelo ya"
        append-icon="search"
      ></v-text-field>
      <v-spacer></v-spacer>
    </v-app-bar>
   
    <main>
 <br>
  <br>
        <v-card style="box-shadow: none !important" class="mt-8">
        <v-list three-line>
      <v-list-item
        v-for="(item, i) in searching"
        :key="i"
        ripple
       @click="verDetalle(item.id_pro)"
      >
        <v-img 
          :src="item.thumbnail"
          class="mr-4 "
          max-width="80"
          max-height="80"
          style="border-radius:10px"
        ></v-img>

        <v-list-item-content>
          <span
            class="text-uppercase font-weight-regular text-caption"
            v-text="item.titulo"
          ></span>

          <div v-text="'Q.'+item.precio"></div>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-card>
    </main>
  </v-container>
</template>
<script>
import { mdiArrowLeftCircle } from "@mdi/js";
import api from '../../models/controller'

export default {
  data() {
    return {
      productos:[],
      search: null,
      icons: {
        mdiArrowLeftCircle,
      },
    };
  },
  methods: {
    retrocer() {
      this.$router.go(-1);
    },
     verDetalle(id) {
      if (localStorage.getItem("user")) {
        var credential = JSON.parse(localStorage.getItem("user"));
        let user = credential.user;
        console.log("DATOS ENVIANDOS : ", user);
        let vista = {
          id_user: user.id_user,
          id_pro: id,
          like: 0,
        };
        api.createView(vista)
          .then((result) => {
            console.log("DATOS id User : ", result.data, credential);
          });
      }
      // this.$router.push("/detalleProducto", {id_pro: id});
      this.$router.push({
        path: `/detalleProducto/${id}`,
        id_pro: { id },
      });
    },
  },
  computed: {
      keywords () {
        if (!this.search) return []

        const keywords = []

        for (const search of this.searching) {
          keywords.push(search.keyword)
        }

        return keywords
      },
      searching () {
        if (!this.search) return this.productos

        const search = this.search.toLowerCase()

        return this.productos.filter(item => {
          const text = item.titulo.toLowerCase()

          return text.indexOf(search) > -1
        })
      },
    },
    created() {
    this.productos = [];
    api.getAllProduct().then((result)=>{
         var datos = result.data;
         this.productos= datos;
         console.log("PRODUCTOS :",this.productos )
    })
  }
};
</script>
