<template>
    <div>
        <v-card tile class="mx-auto" color="green" dark  style="position: relative;">
            <v-img
          height="170px"
          src="../../assets/fondos/fondoportada.jpg"
        >
            <v-btn class="ml-5 mt-1" color="white" icon @click="$router.go(-1)">
                    <v-icon large>mdi-keyboard-backspace</v-icon>
                </v-btn>
            <div class="icon-like">
                <v-btn v-if="!active" color="white" icon @click="likeRegister(idSeller, 1)">
                    <v-icon color="white lighten-1" size="35">mdi-thumb-up-outline</v-icon>
                </v-btn>
                <v-btn v-else color="white"  @click="likeRegister(idSeller, 0)" icon>
                    <v-icon size="35" color="yellow">mdi-thumb-up</v-icon>
                </v-btn>
            </div>
            <v-card-actions style="border-bottom: solid 20px rgb(148, 253, 148);">
                <v-list-item class="grow" >
                    <v-list-item-avatar  size="80" style="border:solid 3px rgb(255, 255, 255)">
                        <v-img class="elevation-6" :alt="seller.nombres" :src="seller.photo"></v-img>
                    </v-list-item-avatar>

                    <v-list-item-content>
                        <v-list-item-title class="text-h6">   {{ seller.nombres }} {{ seller.apellidos }}</v-list-item-title>
                    </v-list-item-content>


                </v-list-item>
            </v-card-actions>
            </v-img>
        </v-card>
        <!---<v-img height="150px" src="https://cdn.pixabay.com/photo/2020/07/12/07/47/bee-5396362_1280.jpg">
            <v-app-bar flat color="rgba(0, 0, 0, 0)">
                <v-btn color="white" icon @click="$router.go(-1)">
                    <v-icon large>mdi-keyboard-backspace</v-icon>
                </v-btn>
                <v-spacer></v-spacer>
                <v-btn v-if="!active" color="white" icon @click="likeRegister(idSeller, 1)">
                    <v-icon color="white lighten-1" size="35">mdi-thumb-up-outline</v-icon>
                </v-btn>
                <v-btn v-else color="white" icon @click="likeRegister(idSeller, 0)">
                    <v-icon size="35" color="rgb(80, 249, 80)">mdi-thumb-up</v-icon>
                </v-btn>
            </v-app-bar>
            <v-card-title class="white--text mt-0">
                <v-avatar size="70">
                    <img style="object-fit: cover;" :alt="seller.nombres" :src="seller.photo">
                    <template v-slot:placeholder>
                        <v-row class="fill-height ma-0" align="center" justify="center">
                            <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                        </v-row>
                    </template>
                </v-avatar>
                <p class="ml-3">
                    {{ seller.nombres }} {{ seller.apellidos }}
                </p>
            </v-card-title>
        </v-img>-->

        <v-sheet class="px-1 pt-2">
            <v-chip-group mandatory active-class="primary--text" v-model="item">
                <v-chip v-for="tag in tags" :key="tag.id">
                    {{ tag.title }}
                </v-chip>
            </v-chip-group>
        </v-sheet>
        <div>
            <hr style="height: 5px !important;">
            <informacion :id="idSeller" v-if="item == 0"></informacion>
            <publicaciones :id="idSeller" v-else-if="item == 1"></publicaciones>
            <seguidores :id="idSeller" v-else-if="item == 2"></seguidores>
        </div>
    </div>
</template>
<script>
import api from '../../models/controller'
import store from '../../store'
import informacion from '@/components/Seller/Informacion.vue'
import publicaciones from '@/components/Seller/Publicaciones.vue'
import seguidores from '@/components/Seller/Seguidores.vue'
export default {
    components: { informacion, publicaciones, seguidores },
    props: ['id'],

    data() {
        return {
            item: null,
            active: null,
            seller: {},
            idSeller: null,
            tags: [{ id: 1, title: 'Informacin' }, { id: 2, title: 'Publicaciones' }, { id: 3, title: 'Seguidores' }
            ],
        }
    },
    methods: {
        likeRegister: function (id, status) {

            if (store.getters.isLoggedIn) {
                var credential = store.getters.getUser;
                let user = credential;
                var data = {
                    idUser: Number(id),
                    idFollower: user.id_user,
                    status: status
                }
                api.likeRegister(data).then(() => {
                    this.getLike(Number(id));
                })
            } else {
                console.log("No register")
            }
        },
        getLike: function (idSeller) {


            if (store.getters.isLoggedIn) {
                var credential = store.getters.getUser;
                let user = credential;
                api.getFollowerLike(idSeller, user.id_user).then((result) => {
                    var like = result.data;
                    this.active = like.status
                })
            } else {
                console.log("No register")
            }
        }
    },
    watch: {
        // whenever question changes, this function will run
    },
    created() {
        this.idSeller = this.$route.params.id

        this.$watch(
            () => this.$route.params.id,
            (toParams) => {
                this.idSeller = toParams;
                this.$router.go(0)
            }
        )
        api.getUser(this.idSeller).then((result) => {
            this.seller = result.data[0];
        });

        this.getLike(this.idSeller);

    }
}
</script>

<style>
.icon-like{
    position: absolute;
    right: 10px;
    top:15px;
    z-index: 5;
}
</style>