<template>
  <v-container class="grey lighten-5 text-center" style="height: 100%">
      
      <h3 class="mt-5">Listado de Planes disponibles</h3>
    <v-row class="mb-6 text-center" justify="center">
      <v-col cols="12" md="4" v-for="item in planes" :key="item.id_plan">
        <v-card class="mx-auto my-12" elevation="5" v-if="item.id_plan != 5">
          <v-toolbar class="text-center" :color="item.color" dark>
            <h2 class=" text-center">
              Plan {{ item.nombre_plan }}
            </h2></v-toolbar
          >
          <v-card-text>
            <br />
            <h1 style="font-size: 3rem">Q.{{ item.precio }}</h1>
            <h4>Por anuncio</h4>
            <br />
            <span>{{ item.descripcion }}</span>
            <br />
            <v-btn class="mt-5" rounded :color="item.color" dark block> Vender Producto </v-btn>
          </v-card-text>
          <br />
        </v-card>
      </v-col>
      <span class="">*Recuerda que puedes actualizar el plan de tu anuncio cuando quieras.</span>
    </v-row>
    <a href="javascript:void window.open('')" >
    Abrir en una pestaa nueva
</a>

<v-card>
   <iframe style="width:100%" src="https://mallvirtual.com.gt/formulario-de-pago/1556/pliis-technology-businesses/14e676072a5b15381b6f7f33c3528215"></iframe>
  </v-card>
    <br>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      planes: [],
    };
  },

  methods : {
   openLinkInNewTab (url) {
  window.open(url, '_blank');
}
  },
  created() {
    window.scrollTo(0, 0);

    axios
      .get("https://vendeloyaglobal.herokuapp.com/api/planes", {
        onUploadProgress: (uploadEvent) => {
          console.log(
            "Proceso de Carga :" +
              Math.round((uploadEvent.loaded / uploadEvent.total) * 100) +
              "%"
          );
        },
      })
      .then((datos) => {
        var planes = datos.data;
        planes.forEach(element => {
            if(element.id_plan != 5 && element.id_plan != 4  ){
                this.planes.push(element);
            }
            
        });
      
        this.planes = this.planes.reverse();
        console.log("DATOS DE PLANES : ", planes);
      });
  },
};
</script>

